import {
  AfterViewInit,
  Component,
  ElementRef,
  Input,
  OnDestroy,
  OnInit,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  DeliveryFormatsDataSource,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetadataIntellisenseDatasource,
  MetaDataType,
} from 'shared-code-lib';
import { FileDropEvent } from '../../../../blocks/directives/drop-zone.directive';
import { DeliverToFtpWorkflowStep } from '../../../../blocks/models/workflow-step';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-step-deliver-to-ftp-general',
  templateUrl: './step-deliver-to-ftp-general.component.html',
  styleUrls: ['./step-deliver-to-ftp-general.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliverToFtpGeneralComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() deliverToFtpStep: DeliverToFtpWorkflowStep;
  @ViewChild('ppkFile', { static: true }) ppkFileInputRef: ElementRef;
  ppkFileInput: HTMLInputElement;

  fieldType: FieldDefinition;
  fieldAddress: FieldDefinition;
  fieldPortt: FieldDefinition;
  fieldUsername: FieldDefinition;
  fieldPassword: FieldDefinition;
  fieldPort: FieldDefinition;
  fieldSecurityType: FieldDefinition;
  fieldPPKPassphrase: FieldDefinition;
  fieldDeliveryFormat: FieldDefinition;
  fieldDocumentName: FieldDefinition;

  get deliverToFtpStepPort() {
    return `${this.deliverToFtpStep.port}`;
  }
  set deliverToFtpStepPort(val: string) {
    this.deliverToFtpStep.port = Number(val);
  }
  get deliverToFtpStepSecurityType() {
    return `${this.deliverToFtpStep.securityType}`;
  }
  set deliverToFtpStepSecurityType(val: string) {
    this.deliverToFtpStep.securityType = Number(val);
  }

  constructor(private translateService: TranslateService, private userStateService: UserStateService) {}

  ngOnInit(): void {
    this.ppkFileInput = this.ppkFileInputRef.nativeElement;
    this.createFields();

    if (!this.deliverToFtpStep.usePPKFile) {
      this.deliverToFtpStep.ppkFileBase64 = '';
      this.deliverToFtpStep.ppkFilename = '';
      this.deliverToFtpStep.ppkPassphrase = '';
    }
  }

  ngAfterViewInit(): void {}

  ngOnDestroy(): void {}

  onDeliveryTypeChanged = (type: number): void => {};

  createFields = (): void => {
    const ftpTypes = [
      { value: 'sftp', name: this.translateService.instant('step-deliver-to-ftp.type-sftp') },
      { value: 'ftp', name: this.translateService.instant('step-deliver-to-ftp.type-ftp') },
      { value: 'ftps', name: this.translateService.instant('step-deliver-to-ftp.type-ftps') },
    ];

    this.fieldType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('step-deliver-to-ftp.type-label'),
      placeholder: '',
      datasource: new FixedDataSource(ftpTypes),
      required: true,
    });

    this.fieldAddress = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-deliver-to-ftp.address'),
      placeholder: this.translateService.instant('step-deliver-to-ftp.address-placeholder'),
      required: true,
      substitutable: false,
    });

    this.fieldPort = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('step-deliver-to-ftp.port'),
      placeholder: this.translateService.instant('step-deliver-to-ftp.port-placeholder'),
      required: true,
      substitutable: false,
    });

    this.fieldUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-deliver-to-ftp.username'),
      placeholder: this.translateService.instant('step-deliver-to-ftp.username-placeholder'),
      required: true,
      substitutable: false,
    });

    this.fieldPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translateService.instant('step-deliver-to-ftp.password'),
      placeholder: this.translateService.instant('step-deliver-to-ftp.password-placeholder'),
      required: true,
      substitutable: false,
    });

    this.fieldPPKPassphrase = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translateService.instant('step-deliver-to-ftp.passphrase'),
      placeholder: this.translateService.instant('step-deliver-to-ftp.passphrase-placeholder'),
      required: false,
      substitutable: false,
    });

    this.fieldDocumentName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-deliver-to-email.document-name'),
      placeholder: this.translateService.instant('step-deliver-to-email.document-name-placeholder'),
      required: false,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userStateService),
    });

    this.fieldDeliveryFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('workflow-step.delivery-format'),
      placeholder: '',
      datasource: new DeliveryFormatsDataSource(this.translateService, this.userStateService),
      required: true,
    });

    const ftpSecurityTypes: IFieldOptions[] = [
      { value: '1', name: this.translateService.instant('step-deliver-to-ftp.security-type-tls') },
      { value: '2', name: this.translateService.instant('step-deliver-to-ftp.security-type-ssl') },
      { value: '3', name: this.translateService.instant('step-deliver-to-ftp.security-type-implicit') },
    ];

    this.fieldSecurityType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('step-deliver-to-ftp.connection-type'),
      placeholder: '',
      datasource: new FixedDataSource(ftpSecurityTypes),
      required: true,
    });
  };

  ftpTypeChanged = (value: string): void => {
    this.deliverToFtpStep.ftpServerType = value;
  };

  onPPKFileSelected = (evt: any): void => {
    if (this.ppkFileInput.files.length == 0) return;
    this.readFile(this.ppkFileInput.files[0]);
    this.deliverToFtpStep.ppkFilename = this.ppkFileInput.files[0].name;
  };

  public onFilesDropped = (event: FileDropEvent): void => {
    if (event.files.length == 0) return;
    this.readFile(event.files[0]);
    this.deliverToFtpStep.ppkFilename = event.files[0].name;
  };

  getPPKFilename = (): string => {
    if (this.deliverToFtpStep.ppkFilename) return this.deliverToFtpStep.ppkFilename;

    return this.translateService.instant('step-deliver-to-ftp.no-ppk-file-selected');
  };

  readFile = (file: File): void => {
    this.deliverToFtpStep.ppkFileBase64 = null;
    const reader = new FileReader();
    reader.onload = (event: any) => {
      this.deliverToFtpStep.ppkFileBase64 = event.target.result.split(',')[1];
    };

    reader.readAsDataURL(file);
  };
}
