import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { DecisionPoint } from '../../../blocks/models/workflow-step';
import { Step } from '../step';

@Component({
  selector: 'app-step-decision-point',
  templateUrl: './step-decision-point.component.html',
  styleUrls: ['./step-decision-point.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDecisionPointComponent implements OnInit {
  @Input() step: Step;

  decisionPointWorkflowStep: DecisionPoint;

  fieldDisplayName: FieldDefinition;

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.decisionPointWorkflowStep = <DecisionPoint>this.step.workflowStep;
    this.createFields();
  }

  private createFields = () => {
    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translate.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });
  };
}
