import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from '../../../../../../../Bamba.Shared';
import { TranslateDocumentWorkflowStep } from '../../../blocks/models/workflow-step';
import { Step } from '../step';

@Component({
  selector: 'app-step-translate-document',
  templateUrl: './step-translate-document.component.html',
  styleUrls: ['./step-translate-document.component.scss'],
})
export class StepTranslateDocumentComponent implements OnInit {
  @Input() step: Step;

  translateDocument: TranslateDocumentWorkflowStep;

  sourceLanguageCodeField: FieldDefinition;
  targetLanguageCodeField: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.translateDocument = this.step.workflowStep as TranslateDocumentWorkflowStep;

    this.createFields();
  }

  createFields = (): void => {
    this.sourceLanguageCodeField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant(`step-translate-document.source-language-code`),
      placeholder: '',
      required: true,
      datasource: this.createLanaguageCodeDataSource(true),
    });

    this.targetLanguageCodeField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant(`step-translate-document.target-language-code`),
      placeholder: '',
      required: true,
      datasource: this.createLanaguageCodeDataSource(false),
    });
  };

  createLanaguageCodeDataSource = (includeAutoDetect: boolean): FixedDataSource => {
    const options = new Array<IFieldOptions>();

    this.translateDocument.supportedLanguageCodes.forEach((lc) => {
      options.push({ name: this.translateService.instant(`step-translate-document.languages.${lc}`), value: lc });
    });

    options.sort((x, y) => x.name.localeCompare(y.name));

    if (includeAutoDetect)
      options.unshift({
        name: this.translateService.instant(`step-translate-document.languages.automatic`),
        value: '*',
      });

    return new FixedDataSource(options);
  };
}
