import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition } from '../../../../../../../../../../Bamba.Shared/src/models/field-definition';
import { EnumDataSource } from '../../../../../../../../../../Bamba.Shared/src/models/fixed-datasource';
import { MetaDataType } from '../../../../../../../../../../Bamba.Shared/src/models/metadata-definition';
import { PageSelectionCriteria, TextContentCriteria } from '../../../../../../blocks/models/workflow-step';

@Component({
  selector: 'app-text-content-criteria-options',
  templateUrl: './text-content-criteria-options.component.html',
  styleUrls: ['./text-content-criteria-options.component.scss'],
})
export class TextContentCriteriaOptionsComponent implements OnInit {
  @Input() textContentCriteria: TextContentCriteria;
  @Input() id: number;
  textField: FieldDefinition;
  pageSelectionField: FieldDefinition;
  pageRangeField: FieldDefinition;
  minimumRepeatsField: FieldDefinition;
  maximumRepeatsField: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields() {
    this.textField = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: '',
      required: false,
    });

    this.pageSelectionField = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: true,
      datasource: new EnumDataSource(PageSelectionCriteria, 'PageSelectionCriteriaEnum', this.translateService),
    });

    this.pageRangeField = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: '',
      required: false,
    });

    this.minimumRepeatsField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('simple-classifier-step-properties.minimum-repeats-label'),
      placeholder: '',
      required: true,
    });

    this.maximumRepeatsField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('simple-classifier-step-properties.maximum-repeats-label'),
      placeholder: '',
      required: true,
    });
  }

  updatePageSelection(value: string) {
    this.textContentCriteria.pageSelection = +value;
  }

  updateMinimumRepeats(value: string) {
    this.textContentCriteria.minimumRepeats = +value;
  }

  updateMaximumRepeats(value: string) {
    this.textContentCriteria.maximumRepeats = +value;
  }
}
