import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  DelayService,
  EnumDataSource,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  LineItemConfiguration,
  MetaDataDefinition,
  MetaDataType,
  ObjectIdGenerator,
  Tab,
} from 'shared-code-lib';
import { AwsFieldMapping, DuplicateSelectionType, FieldMappingType } from '../../../blocks/models/aws-field-mapping';
import { InvoiceExtractionFields } from '../../../blocks/models/invoice-extraction-fields';
import { InvoiceExtractionWorkflowStep } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-invoice-extraction',
  templateUrl: './step-invoice-extraction.component.html',
  styleUrls: ['./step-invoice-extraction.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepInvoiceExtractionComponent implements OnInit {
  @Input() step: Step;
  @Input() useInvoiceFields: boolean = false;
  @Input() useExpressions: boolean = false;

  tabs = new Array<Tab>();
  invoiceExtractionStep: InvoiceExtractionWorkflowStep;
  dataFieldIdFields: Array<FieldDefinition> = [];
  dataFieldMappingTypeFields: Array<FieldDefinition> = [];
  dataFieldValueFields: Array<FieldDefinition> = [];
  dataFieldAdvancedFields: Array<FieldDefinition> = [];
  dataFieldDuplicateSelectionFields: Array<FieldDefinition> = [];
  lineItemFieldLabelNames: Array<string> = [];
  lineItemValueFields: Array<FieldDefinition> = [];
  lineItemMappingTypeFields: Array<FieldDefinition> = [];
  lineItemAdvancedFields: Array<FieldDefinition> = [];
  fieldDisplayName: FieldDefinition;
  fieldLineItemsId: FieldDefinition;
  fieldTableExpression: FieldDefinition;
  fieldTableRowExpression: FieldDefinition;
  dataFieldsDataSource = new FixedDataSource(new Array<IFieldOptions>());
  awsFieldsDataSource = new FixedDataSource(new Array<IFieldOptions>());
  awsFieldAdvancedDataSource = new FixedDataSource(new Array<IFieldOptions>());
  fieldMappingTypeDataSource = new FixedDataSource(new Array<IFieldOptions>());
  lineItemMappingCache: Array<AwsFieldMapping>;
  lineItemCacheFieldId = '';
  activeTab = 'datafields';
  extractionFields: InvoiceExtractionFields;

  constructor(
    private translate: TranslateService,
    private userService: UserStateService,
    private delay: DelayService
  ) {}

  ngOnInit() {
    this.invoiceExtractionStep = <InvoiceExtractionWorkflowStep>this.step.workflowStep;
    this.extractionFields = new InvoiceExtractionFields(
      this.useInvoiceFields ? this.translate.instant('invoice-extraction-mapping', { returnObject: true }) : []
    );
    this.setLineItemsCache();
    this.createTabs();
    this.createNonMappingFields();
    this.loadDataSources();
    this.createMappingFields();
  }

  createTabs = () => {
    this.tabs.push(new Tab('datafields', this.translate.instant('step-interactive.index-fields')));
    this.tabs.push(new Tab('lineitems', this.translate.instant('step-invoice-extraction.line-items-tab')));
  };

  createMappingFields = () => {
    this.invoiceExtractionStep.dataFieldMappings.map((m) => {
      this.createNewDataFieldMappingField();
    });

    const lineItemMetaDataOptions = new Array<IFieldOptions>();
    lineItemMetaDataOptions.push({
      name: this.translate.instant('Metadata-Form.none'),
      value: ObjectIdGenerator.Empty,
    });

    for (const mdd of this.userService.user.metaData) {
      if (mdd.type != MetaDataType.LineItems) continue;

      lineItemMetaDataOptions.push({ name: mdd.name, value: mdd.id });
    }

    lineItemMetaDataOptions.sort((x, y) => x.name.localeCompare(y.name));

    this.fieldLineItemsId = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-invoice-extraction.line-items-field-id'),
      required: false,
      datasource: new FixedDataSource(lineItemMetaDataOptions),
    });

    this.fieldTableExpression = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-invoice-extraction.table-expression-field'),
      placeholder: this.translate.instant('step-invoice-extraction.table-expression-placeholder'),
      required: false,
    });

    this.fieldTableRowExpression = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-invoice-extraction.table-row-expression-field'),
      placeholder: this.translate.instant('step-invoice-extraction.table-row-expression-placeholder'),
      required: false,
    });
  };

  createNonMappingFields = () => {
    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translate.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });
  };

  activateTab = (view) => {
    this.activeTab = view;
    this.setLineItemsCache();
  };

  createNewDataFieldMappingField = () => {
    this.dataFieldIdFields.push(
      new FieldDefinition({
        type: MetaDataType.List,
        datasource: this.dataFieldsDataSource,
        required: true,
      })
    );

    this.dataFieldMappingTypeFields.push(
      new FieldDefinition({
        type: MetaDataType.List,
        required: true,
        datasource: this.fieldMappingTypeDataSource,
      })
    );

    this.dataFieldValueFields.push(
      new FieldDefinition({
        type: MetaDataType.List,
        required: true,
        datasource: this.awsFieldsDataSource,
      })
    );

    this.dataFieldAdvancedFields.push(
      new FieldDefinition({
        type: MetaDataType.Text,
        required: true,
        substitutable: true,
        datasource: this.awsFieldAdvancedDataSource,
      })
    );

    this.dataFieldDuplicateSelectionFields.push(
      new FieldDefinition({
        type: MetaDataType.List,
        required: true,
        datasource: new EnumDataSource(
          DuplicateSelectionType,
          'step-invoice-extraction.duplicateSelectionTypeEnum',
          this.translate
        ),
      })
    );
  };

  createNewLineItemMappingRow = (fieldId: string) => {
    const mdd = this.userService.findMetaDataById(fieldId);
    if (!mdd) return;

    this.lineItemFieldLabelNames.push(mdd.name);

    const lineItemMappingTypeDataSource = new FixedDataSource([...this.fieldMappingTypeDataSource.options]);

    lineItemMappingTypeDataSource.options.push({
      name: this.translate.instant('step-invoice-extraction.mapping-type-not-mapped'),
      value: FieldMappingType.NotMapped.toString(),
    });

    this.lineItemMappingTypeFields.push(
      new FieldDefinition({
        type: MetaDataType.List,
        required: true,
        datasource: lineItemMappingTypeDataSource,
      })
    );

    this.lineItemValueFields.push(
      new FieldDefinition({
        type: MetaDataType.List,
        required: true,
        datasource: this.awsFieldsDataSource,
      })
    );

    this.lineItemAdvancedFields.push(
      new FieldDefinition({
        type: MetaDataType.Text,
        required: true,
        substitutable: true,
        datasource: this.awsFieldAdvancedDataSource,
      })
    );
  };

  onAddDataFieldMapping = (): void => {
    const fieldMapping = AwsFieldMapping.create();
    fieldMapping.type = this.useInvoiceFields ? FieldMappingType.Standard : FieldMappingType.Advanced;

    this.invoiceExtractionStep.dataFieldMappings.push(fieldMapping);

    this.createNewDataFieldMappingField();
    this.delay.execute(this.setFocusToAddedMapping, 1);
  };

  onDeleteMapping = (index: number): void => {
    this.invoiceExtractionStep.dataFieldMappings.splice(index, 1);
    this.dataFieldIdFields.splice(index, 1);
    this.dataFieldValueFields.splice(index, 1);
  };

  loadDataSources = (): void => {
    this.extractionFields.fields.forEach((f) => {
      const field = { name: f.name, value: `%${f.value}%` };
      this.awsFieldsDataSource.options.push(field);

      const advancedField = { name: f.value, value: f.value };
      this.awsFieldAdvancedDataSource.options.push(advancedField);
    });

    this.awsFieldsDataSource.options.sort((x, y) => x.name.localeCompare(y.name));
    this.awsFieldsDataSource.options.unshift({
      name: this.translate.instant('admin-workflow.not-mapped'),
      value: '',
    });

    this.awsFieldAdvancedDataSource.options.sort((x, y) => x.name.localeCompare(y.name));

    this.userService.user.metaData.forEach((f) => {
      if (
        f.type == MetaDataType.LineItems ||
        f.type == MetaDataType.Expression ||
        f.type == MetaDataType.CascadingOptions
      )
        return;

      const mdd = { name: f.name, value: f.id };
      this.dataFieldsDataSource.options.push(mdd);
    });

    this.dataFieldsDataSource.options.sort((x, y) => x.name.localeCompare(y.name));

    this.dataFieldsDataSource.options.unshift({
      name: this.translate.instant('admin-workflow.not-mapped'),
      value: ObjectIdGenerator.Empty,
    });

    this.fieldMappingTypeDataSource.options.push({
      name: this.translate.instant('step-invoice-extraction.mapping-type-standard'),
      value: FieldMappingType.Standard.toString(),
    });

    this.fieldMappingTypeDataSource.options.push({
      name: this.translate.instant('step-invoice-extraction.mapping-type-advanced'),
      value: FieldMappingType.Advanced.toString(),
    });
  };

  onLineItemsFieldChanged = (value: string): void => {
    this.invoiceExtractionStep.lineItemMappings.splice(0, this.invoiceExtractionStep.lineItemMappings.length);
    this.lineItemFieldLabelNames.splice(0, this.lineItemFieldLabelNames.length);
    this.lineItemValueFields.splice(0, this.lineItemValueFields.length);
    this.lineItemMappingTypeFields.splice(0, this.lineItemMappingTypeFields.length);

    const mdd = this.userService.user.metaData.find((f) => f.id == value);
    if (!mdd) return;

    const lineItemField = new MetaDataDefinition(mdd);
    lineItemField.lineItemConfigurations.forEach((li) => {
      const cacheMapping = this.getCachedValue(value, li);
      if (!cacheMapping) return;

      this.invoiceExtractionStep.lineItemMappings.push(
        new AwsFieldMapping({
          fieldId: li.metaDataId,
          value: cacheMapping.value,
          type: this.useInvoiceFields ? cacheMapping.type : FieldMappingType.Advanced,
          duplicateSelectionType: cacheMapping.duplicateSelectionType,
        })
      );

      this.applyAutoMapping(
        li.metaDataId,
        this.invoiceExtractionStep.lineItemMappings.length - 1,
        this.invoiceExtractionStep.lineItemMappings
      );

      this.createNewLineItemMappingRow(li.metaDataId);
    });
  };

  onDataFieldChanged = (selectedFieldValue: string, index: number): void => {
    if (index >= this.invoiceExtractionStep.dataFieldMappings.length) return;
    if (selectedFieldValue == '') return;

    this.applyAutoMapping(selectedFieldValue, index, this.invoiceExtractionStep.dataFieldMappings);
  };

  onLineItemDataFieldChanged = (selectedFieldValue: string, index: number): void => {
    if (index >= this.invoiceExtractionStep.lineItemMappings.length) return;
    if (selectedFieldValue == '') return;

    this.applyAutoMapping(selectedFieldValue, index, this.invoiceExtractionStep.lineItemMappings);
  };

  applyAutoMapping = (selectedFieldValue: string, index: number, fieldMappings: Array<AwsFieldMapping>): void => {
    if (fieldMappings[index].value) return;

    if (fieldMappings[index].type === FieldMappingType.NotMapped) {
      fieldMappings[index].value = '';
      return;
    }

    const mdd = this.userService.findMetaDataById(selectedFieldValue);
    if (!mdd) return;

    const matchingField = this.extractionFields.fields.find((ef) => ef.mappingMatches(mdd.name));
    if (matchingField) {
      fieldMappings[index].value = `%${matchingField.value}%`;
    }
  };

  setLineItemsCache = (): void => {
    this.lineItemMappingCache = [];

    this.invoiceExtractionStep.lineItemMappings.map((m) => {
      this.lineItemMappingCache.push(m);
    });

    this.lineItemCacheFieldId = this.invoiceExtractionStep.lineItemsFieldId;
  };

  getCachedValue = (lineItemFieldId: string, lineItem: LineItemConfiguration): AwsFieldMapping => {
    const mapping = this.lineItemMappingCache.find((m) => {
      if (m.fieldId === lineItem.metaDataId) return m;
    });

    return mapping && lineItemFieldId === this.lineItemCacheFieldId ? mapping : AwsFieldMapping.create();
  };

  setFocusToAddedMapping = (): void => {
    const valueField = <HTMLSelectElement>(
      document.querySelector(
        `#datafield-mapping-${this.invoiceExtractionStep.dataFieldMappings.length - 1} .custom-drop-down`
      )
    );
    if (valueField) {
      valueField.focus();
    }
  };
}
