import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, IFieldOptions } from '../../../../../../../../Bamba.Shared/src/models/field-definition';
import { FixedDataSource } from '../../../../../../../../Bamba.Shared/src/models/fixed-datasource';
import { MetaDataType } from '../../../../../../../../Bamba.Shared/src/models/metadata-definition';
import { DelayService } from '../../../../../../../../Bamba.Shared/src/utils/delay.service';
import {
  Classification,
  ContentCriteria,
  ContentCriteriaType,
  IContentCriteria,
  PageSelectionCriteria,
} from '../../../../blocks/models/workflow-step';

@Component({
  selector: 'app-classification',
  templateUrl: './classification.component.html',
  styleUrls: ['./classification.component.scss'],
})
export class ClassificationComponent implements OnInit {
  @Input() classification: Classification;
  @Input() index: number;
  @Input() disableDeletion: boolean;
  @Output() onDeleteClassification = new EventEmitter<number>();

  nameField: FieldDefinition;
  contentCriteriaRequiredField: FieldDefinition;

  get disableContentCriteriaDeletion() {
    return this.classification.contentCriteria.length === 1;
  }

  constructor(private translateService: TranslateService, private delayService: DelayService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields() {
    this.nameField = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('simple-classifier-step-properties.name-label'),
      placeholder: this.translateService.instant('simple-classifier-step-properties.name-placeholder'),
      required: true,
    });

    this.setContentCriteriaRequiredField();
  }

  setContentCriteriaRequiredField() {
    this.contentCriteriaRequiredField = null;
    this.delayService.execute(() => {
      const options: IFieldOptions[] = [
        { name: this.translateService.instant('simple-classifier-step-properties.all'), value: '0' },
      ];

      const contentCriteriaNumbers = Array.from(
        { length: this.classification.contentCriteria.length - 1 },
        (_, i) => i + 1
      );

      contentCriteriaNumbers.forEach((num) => {
        options.push({
          name: this.translateService.instant('simple-classifier-step-properties.at-least') + ' ' + num,
          value: num + '',
        });
      });

      this.contentCriteriaRequiredField = new FieldDefinition({
        type: MetaDataType.List,
        label: '',
        placeholder: '',
        datasource: new FixedDataSource(options),
        required: false,
      });

      if (this.classification.contentCriteriaRequired > contentCriteriaNumbers.length) {
        this.classification.contentCriteriaRequired = contentCriteriaNumbers.length;
      }
    }, 0);
  }

  deleteClassification() {
    if (this.disableDeletion) return;
    this.onDeleteClassification.emit(this.index);
  }

  addContentCriteria() {
    this.classification.contentCriteria.push(this.getDefaultContentCriteria());

    this.delayService.execute(() => {
      const criteriaTextField = <HTMLInputElement>(
        document.querySelector(
          `#classification-${this.index} #content-criteria-${
            this.classification.contentCriteria.length - 1
          } .field-text`
        )
      );
      if (criteriaTextField) {
        criteriaTextField.focus();
      }
    }, 50);

    this.setContentCriteriaRequiredField();
  }

  getDefaultContentCriteria() {
    return ContentCriteria.create({
      contentCriteriaType: 'RegexContentCriteria',
      pageSelection: PageSelectionCriteria.First,
      pageRange: '',
      onlyIfOnSpecificPages: false,
      text: '',
      caseInsensitive: false,
      repeats: false,
      minimumRepeats: 1,
      maximumRepeats: 1,
    } as IContentCriteria);
  }

  deleteContentCriteria(indexToDelete: number) {
    if (this.disableContentCriteriaDeletion) return;
    this.classification.contentCriteria.splice(indexToDelete, 1);
    this.setContentCriteriaRequiredField();
  }

  onSelectContentCriteriaRequired(value: string) {
    this.classification.contentCriteriaRequired = +value;
  }

  updateContentCriteriaType(newType: ContentCriteriaType, index: number) {
    const oldCriteria = this.classification.contentCriteria[index];
    if (!oldCriteria) return;

    const newCriteriaInitializer = {
      ...oldCriteria.serializable(),
      contentCriteriaType: newType,
    };

    this.classification.contentCriteria.splice(index, 1, ContentCriteria.create(newCriteriaInitializer));
  }
}
