import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Tab } from 'shared-code-lib';
import { DeliverToNetDocsWorkflowStep } from '../../../../app/blocks/models/workflow-step';

@Component({
  selector: 'app-step-delivery-to-netdocs',
  templateUrl: './step-delivery-to-netdocs.component.html',
  styleUrls: ['./step-delivery-to-netdocs.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliveryToNetdocsComponent implements OnInit {
  @Input() deliverToNetDocs: DeliverToNetDocsWorkflowStep;
  @Input() workflowId: string;

  activeTab = 'general';
  tabs = new Array<Tab>();

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.tabs.push(new Tab('general', this.translate.instant('step-deliver.general')));
  }

  activateTab = (view: string): void => {
    this.activeTab = view;
  };
}
