import { Component, ElementRef, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FileDropEvent } from '../../../blocks/directives/drop-zone.directive';
import { FormsWorkflowStep } from '../../../blocks/models/workflow-step';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-forms',
  templateUrl: './step-forms.component.html',
  styleUrls: ['./step-forms.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepFormsComponent implements OnInit {
  @ViewChild('file', { static: true })
  fileInputRef: ElementRef;
  @Input() step: Step;

  fileInput: HTMLInputElement;

  formsWorkflowStep: FormsWorkflowStep;

  constructor(private translate: TranslateService, private hydraApi: HydraApiService) {}

  ngOnInit() {
    this.formsWorkflowStep = <FormsWorkflowStep>this.step.workflowStep;
    this.formsWorkflowStep.enabled = true;
    this.fileInput = this.fileInputRef.nativeElement;
  }

  validateForm = (): boolean => {
    return true; //todo:
  };

  onFileSelected = (evt: any): void => {
    if (this.fileInput.files.length == 0) return;
    this.readFile(this.fileInput.files[0]);
    this.formsWorkflowStep.pathname = this.fileInput.files[0].name;
  };

  public onFilesDropped = (event: FileDropEvent) => {
    if (event.files.length == 0) return;
    this.readFile(event.files[0]);
    this.formsWorkflowStep.pathname = event.files[0].name;
  };

  getProjectFile = (): string => {
    if (this.formsWorkflowStep.pathname) return this.formsWorkflowStep.pathname;

    return this.translate.instant('admin-workflow.no-forms-project-selected');
  };

  readFile = (file: File): void => {
    this.formsWorkflowStep.file = null;
    const reader = new FileReader();
    reader.onload = (event: any) => {
      this.formsWorkflowStep.file = event.target.result.split(',')[1];
    };

    reader.readAsDataURL(file);
  };
}
