import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import {
  DelayService,
  FieldDefinition,
  FixedDataSource,
  IFieldDataSource,
  IFieldOptions,
  MetadataIntellisenseDatasource,
  MetaDataType,
  Tab,
} from 'shared-code-lib';
import { ExportToWebServiceWorkflowStep } from '../../../blocks/models/workflow-step';
import { Dictionary } from '../../../blocks/services/models';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-export-to-webservice',
  templateUrl: './step-export-to-webservice.component.html',
  styleUrls: ['./step-export-to-webservice.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepExportToWebServiceComponent implements OnInit {
  directiveRef: PerfectScrollbarDirective;
  @ViewChild(PerfectScrollbarDirective, { static: false }) set content(content: PerfectScrollbarDirective) {
    if (content) {
      this.directiveRef = content;
    }
  }
  @Input() step: Step;

  workflowStep: ExportToWebServiceWorkflowStep;

  tabs: Array<Tab>;
  activeTab: string = 'endpoint';

  actionDataSource: FixedDataSource;
  typeDataSource: FixedDataSource;

  actionField: FieldDefinition;
  urlField: FieldDefinition;
  timeoutField: FieldDefinition;
  typeField: FieldDefinition;
  bodyField: FieldDefinition;
  focusedHeaderRow: number;

  get workflowStepTimeout() {
    return `${this.workflowStep.timeout}`;
  }
  set workflowStepTimeout(val: string) {
    this.workflowStep.timeout = Number(val);
  }
  get workflowStepType() {
    return `${this.workflowStep.type}`;
  }
  set workflowStepType(val: string) {
    this.workflowStep.type = Number(val);
  }

  constructor(private translate: TranslateService, private delay: DelayService, private userState: UserStateService) {}

  ngOnInit() {
    this.workflowStep = <ExportToWebServiceWorkflowStep>this.step.workflowStep;
    this.initDataSources();
    this.createFields();
    this.createTabs();
  }

  createFields = (): void => {
    this.actionField = this.createField('action', 'action', MetaDataType.List, true, false, this.actionDataSource);
    this.urlField = this.createField('url', 'url', MetaDataType.Text, true);
    this.timeoutField = this.createField('timeout', 'timeout', MetaDataType.Integer, false);
    this.typeField = this.createField('type', 'type', MetaDataType.List, true, false, this.typeDataSource);
    this.bodyField = this.createField('body', 'body', MetaDataType.LargeText, false);
  };

  createField = (
    label: string = null,
    placeholder: string = '',
    type: MetaDataType = MetaDataType.Text,
    required: boolean = false,
    substitutable: boolean = true,
    datasource: IFieldDataSource = null
  ): FieldDefinition => {
    return new FieldDefinition({
      type: type,
      label: label ? this.translate.instant(`step-export-to-webservice.${label}`) : '',
      placeholder: placeholder ? this.translate.instant(`step-export-to-webservice.${placeholder}`) : '',
      required: required,
      datasource: substitutable ? new MetadataIntellisenseDatasource(this.userState) : datasource,
      substitutable: substitutable,
    });
  };

  initDataSources = (): void => {
    const actionsNames = ['POST', 'PUT', 'GET'];
    this.actionDataSource = new FixedDataSource(new Array<IFieldOptions>());

    actionsNames.forEach((x) => {
      this.actionDataSource.options.push({
        name: this.translate.instant(`step-export-to-webservice.actions.${x.toLocaleLowerCase()}`),
        value: x,
      });
    });

    this.typeDataSource = new FixedDataSource(new Array<IFieldOptions>());
    this.typeDataSource.options.push({
      name: this.translate.instant('step-export-to-webservice.document'),
      value: '0',
    });
    this.typeDataSource.options.push({
      name: this.translate.instant('step-export-to-webservice.substituted-text'),
      value: '1',
    });
  };

  createTabs = (): void => {
    const tabsNames = ['endpoint', 'headers', 'body'];
    this.tabs = [];

    tabsNames.forEach((x) => {
      this.tabs.push(new Tab(x, this.translate.instant(`step-export-to-webservice.${x}`)));
    });
  };

  activateTab = (tab: string): void => {
    this.activeTab = tab;
  };

  onAddValue = () => {
    this.workflowStep.headers.push(new Dictionary('', ''));

    this.delay.execute(() => {
      this.directiveRef.scrollToBottom(0, 300);
      this.focusedHeaderRow = this.workflowStep.headers.length - 1;
    }, 100);
  };

  deleteAllItems = (): void => {
    this.workflowStep.headers = [];

    this.delay.execute(() => {
      this.directiveRef.scrollToTop(0, 300);
    }, 100);
  };

  deleteItem = (item) => {
    const rowIndexToDelete = this.workflowStep.headers.indexOf(item);

    if (rowIndexToDelete > -1) {
      this.workflowStep.headers.splice(rowIndexToDelete, 1);
    }
  };

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.workflowStep.headers, event.previousIndex, event.currentIndex);
  }
}
