export class SharePointFolder {
  public id: string;
  public url: string;
  public name: string;
  public documentLibraryId: string;
  public folders: Array<SharePointFolder>;
  public hasChildFolders: boolean;
  public expanded: boolean;

  constructor(id: string, url: string, documentLibraryId: string, name: string, hasChildFolders: boolean) {
    this.id = id;
    this.name = name;
    this.url = url;
    this.documentLibraryId = documentLibraryId;
    this.hasChildFolders = hasChildFolders;
    this.expanded = false;
  }

  findFolder(id: string): SharePointFolder {
    if (this.id === id) return this;

    for (let f of this.folders) {
      const foundFolder = f.findFolder(id);
      if (foundFolder) return foundFolder;
    }

    return null;
  }

  findParentIds(id: string): Array<string> {
    if (id === this.id) {
      const parentIds = new Array<string>();
      parentIds.push(this.id);
      return parentIds;
    }

    if (this.folders) {
      for (let f of this.folders) {
        const parentIds = f.findParentIds(id);
        if (parentIds) {
          parentIds.unshift(this.id);
          return parentIds;
        }
      }
    }

    return null;
  }
}
