import { Component, OnInit, ViewEncapsulation, Input, Output, EventEmitter } from '@angular/core';
import { Role } from 'shared-code-lib';
import { HydraApiService } from '../../../../blocks/services/hydra-api.service';

@Component({
  selector: 'app-step-interactive-roles',
  templateUrl: './step-interactive-roles.component.html',
  styleUrls: ['./step-interactive-roles.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepInteractiveRolesComponent implements OnInit {
  @Input() roleIds: Array<string> = [];
  @Output() roleIdsChange = new EventEmitter<Array<string>>();

  available: Array<Role> = [];
  current: Array<Role> = [];

  constructor(private hydraApi: HydraApiService) {}

  ngOnInit() {
    this.loadPicklistItems();
  }

  currentChanged = (items: Array<Role>) => {
    this.roleIds = [];
    for (let item of items) {
      this.roleIds.push(item.id);
    }

    this.roleIdsChange.emit(this.roleIds);
  };

  loadPicklistItems = () => {
    this.hydraApi.getRoles(this.loaded, this.loadFailed);
  };

  loaded = (roles: Array<Role>) => {
    this.available = roles;
    this.current = roles.filter((r) => this.roleIds.find((ri) => ri == r.id) != null);
  };

  loadFailed = () => {};
}
