export interface IInvoiceExtractionField {
  name: string;
  value: string;
  autoMapTags: Array<string>;
}

export class InvoiceExtractionField implements IInvoiceExtractionField {
  public name: string;
  public value: string;
  public autoMapTags: Array<string>;

  constructor(initializer: IInvoiceExtractionField) {
    this.name = initializer ? initializer.name : '';
    this.value = initializer ? initializer.value : '';
    this.autoMapTags = initializer ? initializer.autoMapTags : [];
  }

  mappingMatches = (matchText: string): boolean => {
    let matchFound = false;
    this.autoMapTags.find((t) => {
      if (!matchText.replace(' ', '').toLowerCase().includes(t.toLowerCase())) return;
      matchFound = true;
    });

    return matchFound;
  };
}

export class InvoiceExtractionFields {
  public fields: Array<InvoiceExtractionField> = [];

  constructor(translations: any) {
    const keys = Object.keys(translations);

    keys.forEach((key) => {
      const mapping = translations[key];
      this.fields.push(
        new InvoiceExtractionField({
          name: mapping.name,
          value: key,
          autoMapTags: this.getAutoMapTags(mapping.autoMapTags),
        })
      );
    });
  }

  private getAutoMapTags(tags: any): Array<string> {
    const autoMapTags: Array<string> = [];
    const keys = Object.keys(tags);

    keys.forEach((k) => {
      autoMapTags.push(tags[k]);
    });

    return autoMapTags;
  }
}
