import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType, Tab } from 'shared-code-lib';
import { Workflow } from '../../../blocks/models/workflow';
import { InteractiveWorkflowStep, IWorkflowStep } from '../../../blocks/models/workflow-step';
import { Step } from '../step';

@Component({
  selector: 'app-step-interactive',
  templateUrl: './step-interactive.component.html',
  styleUrls: ['./step-interactive.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepInteractiveComponent implements OnInit {
  @Input() step: Step;
  @Input() workflow: Workflow;

  workflowStep: InteractiveWorkflowStep;

  fieldDisplayName: FieldDefinition;

  activeTab = 'roles';
  tabs = new Array<Tab>();

  previousClassifySteps = new Array<IWorkflowStep>();

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.workflowStep = <InteractiveWorkflowStep>this.step.workflowStep;
    this.createFields();
    this.createTabs();
  }

  private createTabs = () => {
    this.tabs.push(new Tab('roles', this.translateService.instant('step-interactive.roles')));
    this.tabs.push(new Tab('index', this.translateService.instant('step-interactive.index-fields')));
    this.tabs.push(new Tab('groupings', this.translateService.instant('step-interactive.groupings')));
    this.tabs.push(new Tab('outcomes', this.translateService.instant('step-interactive.outcomes')));
    this.tabs.push(new Tab('capabilities', this.translateService.instant('step-interactive.capabilities')));
    this.tabs.push(new Tab('thresholds', this.translateService.instant('step-interactive.thresholds')));
    this.tabs.push(new Tab('advanced', this.translateService.instant('step-interactive.advanced')));

    this.loadPreviousClassifySteps(this.workflowStep.id);

    if (this.previousClassifySteps.length) {
      this.tabs.push(new Tab('training', this.translateService.instant('step-interactive.training')));
    }
  };

  private createFields = () => {
    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translateService.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });
  };

  activateTab = (view) => {
    this.activeTab = view;
  };

  loadPreviousClassifySteps = (stepId: string, visitedSteps: Array<any> = []): boolean => {
    if (visitedSteps.some((a) => a == stepId)) return;

    visitedSteps.push(stepId);

    const previousSteps = this.workflow.workflowSteps.filter((s) => this.isStepLinkedByOutcome(s, stepId));
    if (!previousSteps) return false;

    previousSteps.forEach((ps) => {
      if (ps.stepType === 'formsai' && this.previousClassifySteps.findIndex((s) => s.id === ps.id) < 0) {
        this.previousClassifySteps.push(ps);
      }

      this.loadPreviousClassifySteps(ps.id, visitedSteps);
    });

    return false;
  };

  isStepLinkedByOutcome = (step: IWorkflowStep, stepId: string): boolean => {
    const outcomes = step.outcomes.filter((o) => o.nextId === stepId);
    return outcomes.length > 0;
  };
}
