import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Tab } from '../../../../../../../Bamba.Shared/src/form/tab-bar/tab-bar.component';
import { DeliverToSharePoint365WorkflowStep } from '../../../../app/blocks/models/workflow-step';
import { ExportMetaData } from '../models/export-metadata';
import { SharePointFolder } from './models/sharepoint-folder';

@Component({
  selector: 'app-step-deliver-to-sharepoint365',
  templateUrl: './step-deliver-to-sharepoint365.component.html',
  styleUrls: ['./step-deliver-to-sharepoint365.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliverToSharepoint365Component implements OnInit {
  @Input() deliverToSharePoint365: DeliverToSharePoint365WorkflowStep;
  @Input() workflowId: string;

  @Output() folderSelected: EventEmitter<SharePointFolder> = new EventEmitter<SharePointFolder>();
  @Output() startLoading: EventEmitter<string> = new EventEmitter<string>();
  @Output() finishedLoading: EventEmitter<any> = new EventEmitter();

  activeTab = 'general';
  tabs = new Array<Tab>();

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.tabs.push(new Tab('general', this.translate.instant('step-deliver.general')));
    this.tabs.push(new Tab('export-metadata', this.translate.instant('step-deliver.export-metadata')));
  }

  activateTab = (view: string): void => {
    this.activeTab = view;
  };

  onFolderSelected(event: SharePointFolder): void {
    this.folderSelected.emit(event);
  }

  onStartLoading(event: string): void {
    this.startLoading.emit(event);
  }

  onFinishedLoading(): void {
    this.finishedLoading.emit();
  }

  onMetaDataTypeChanged(exportMetaData: ExportMetaData): void {
    this.deliverToSharePoint365.exportMetaData = exportMetaData;
  }
}
