import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  DelayService,
  EnumDataSource,
  FieldDefinition,
  FixedDataSource,
  MappingHandleMultiple,
  MetaDataType,
} from 'shared-code-lib';
import { BarcodeMapping } from '../../../../blocks/models/workflow-step';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-barcode-mappings',
  templateUrl: './barcode-mappings.component.html',
  styleUrls: ['./barcode-mappings.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class BarcodeMappingsComponent implements OnInit {
  @Input() mappings: Array<BarcodeMapping> = [];
  @Output() mappingsChange = new EventEmitter<Array<BarcodeMapping>>();

  dataFieldsDropLists = new Array<FieldDefinition>();
  handleMultipleDropLists = new Array<FieldDefinition>();

  dataFieldsDataSource: FixedDataSource;
  handleMultipleDataSource: FixedDataSource;

  constructor(private delay: DelayService, private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit() {
    this.handleMultipleDataSource = new EnumDataSource(
      MappingHandleMultiple,
      'MappingHandleMultipleEnum',
      this.translate
    );

    const dataFieldsOptions = this.userState.user.metaData
      .map((mdd) => {
        return { name: mdd.name, value: mdd.id };
      })
      .sort((a, b) => a.name.localeCompare(b.name));

    this.dataFieldsDataSource = new FixedDataSource(dataFieldsOptions);
  }

  getDataFieldsDropList(index: number): FieldDefinition {
    return this.getDropListField(index, this.dataFieldsDropLists, this.dataFieldsDataSource);
  }

  getHandleMultipleDropList(index: number): FieldDefinition {
    return this.getDropListField(index, this.handleMultipleDropLists, this.handleMultipleDataSource);
  }

  getDropListField = (index: number, dropLists: Array<FieldDefinition>, ds: FixedDataSource): FieldDefinition => {
    while (dropLists.length <= index) {
      dropLists.push(
        new FieldDefinition({
          type: MetaDataType.List,
          label: '',
          placeholder: '',
          datasource: ds,
          required: false,
        })
      );
    }

    return dropLists[index];
  };

  onDeleteMapping = (mapping: BarcodeMapping, index: number): void => {
    this.mappings.splice(index, 1);
    this.mappingsChange.emit(this.mappings);
  };

  onAddMapping = (): void => {
    this.mappings.push(BarcodeMapping.create());
    this.mappingsChange.emit(this.mappings);

    this.delay.execute(this.setFocusToAddedMapping, 1);
  };

  setFocusToAddedMapping = (): void => {
    const id = `barcode-mapping-${this.mappings.length - 1}`;
    const lastMapping = document.getElementById(id);
    if (lastMapping) {
      lastMapping.focus();
    }
  };

  getMappingHandle = (index: number): string => {
    return `${this.mappings[index].mappingHandleMultiple}`;
  };
  setMappingHandle = (index: number, val: string) => {
    this.mappings[index].mappingHandleMultiple = Number(val);
  };
}
