import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  DeliveryFormatsDataSource,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetadataIntellisenseDatasource,
  MetaDataType,
} from 'shared-code-lib';
import { DeliverToAWSS3WorkflowStep } from '../../../../blocks/models/workflow-step';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-step-deliver-to-s3-general',
  templateUrl: './step-deliver-to-s3-general.component.html',
  styleUrls: ['./step-deliver-to-s3-general.component.scss'],
})
export class StepDeliverToS3GeneralComponent implements OnInit {
  @Input() deliverToS3: DeliverToAWSS3WorkflowStep;

  fieldAccessKey: FieldDefinition;
  fieldSecret: FieldDefinition;
  fieldBucketName: FieldDefinition;
  fieldRegion: FieldDefinition;
  fieldBaseFolder: FieldDefinition;
  fieldDeliveryFormat: FieldDefinition;
  fieldDocumentName: FieldDefinition;

  constructor(private translateService: TranslateService, private userStateService: UserStateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields = () => {
    const regions = new Array<IFieldOptions>();

    const regionIds = [
      'us-east-1',
      'us-east-2',
      'us-west-1',
      'us-west-2',
      'af-south-1',
      'ap-east-1',
      'ap-south-1',
      'ap-northeast-2',
      'ap-northeast-1',
      'ap-northeast-3',
      'ap-southeast-1',
      'ap-southeast-2',
      'ca-central-1',
      'cn-north-1',
      'cn-north-2',
      'eu-central-1',
      'eu-west-1',
      'eu-west-2',
      'eu-south-1',
      'eu-west-3',
      'eu-north-1',
      'me-south-1',
      'sa-east-1',
      'us-gov-east-1',
      'us-gov-west-1',
    ];

    for (const id of regionIds) {
      regions.push({ name: id, value: id });
    }

    this.fieldAccessKey = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-deliver-to-s3.access-key'),
      placeholder: this.translateService.instant('step-deliver-to-s3.access-key-placeholder'),
      required: true,
      substitutable: false,
    });

    this.fieldSecret = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-deliver-to-s3.secret'),
      placeholder: this.translateService.instant('step-deliver-to-s3.secret-placeholder'),
      required: true,
      substitutable: false,
    });

    this.fieldBucketName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-deliver-to-s3.bucket-name'),
      placeholder: this.translateService.instant('step-deliver-to-s3.bucket-name-placeholder'),
      required: true,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userStateService),
    });

    this.fieldBaseFolder = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-deliver-to-s3.base-folder'),
      placeholder: this.translateService.instant('step-deliver-to-s3.base-folder-placeholder'),
      required: false,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userStateService),
    });

    this.fieldDocumentName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-deliver-to-email.document-name'),
      placeholder: this.translateService.instant('step-deliver-to-email.document-name-placeholder'),
      required: false,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userStateService),
    });

    this.fieldDeliveryFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('workflow-step.delivery-format'),
      placeholder: '',
      datasource: new DeliveryFormatsDataSource(this.translateService, this.userStateService),
      required: true,
    });

    this.fieldRegion = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('step-deliver-to-s3.region'),
      placeholder: '',
      datasource: new FixedDataSource(regions),
      required: true,
    });
  };
}
