import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Tab } from '../../../../../../../Bamba.Shared/src/form/tab-bar/tab-bar.component';
import { DeliverToAWSS3WorkflowStep } from '../../../../app/blocks/models/workflow-step';
import { ExportMetaData } from '../models/export-metadata';

@Component({
  selector: 'app-step-deliver-to-s3',
  templateUrl: './step-deliver-to-s3.component.html',
  styleUrls: ['./step-deliver-to-s3.component.scss'],
})
export class StepDeliverToS3Component implements OnInit {
  @Input() deliverToS3: DeliverToAWSS3WorkflowStep;
  @Input() workflowId: string;

  activeTab = 'general';
  tabs = new Array<Tab>();

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.tabs.push(new Tab('general', this.translate.instant('step-deliver.general')));
    this.tabs.push(new Tab('export-metadata', this.translate.instant('step-deliver.export-metadata')));
  }

  activateTab = (view: string): void => {
    this.activeTab = view;
  };

  onMetaDataTypeChanged(exportMetaData: ExportMetaData): void {
    this.deliverToS3.exportMetaData = exportMetaData;
  }
}
