import {
  Component,
  OnInit,
  ViewEncapsulation,
  Input,
  ElementRef,
  ViewChild,
  AfterViewInit,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { Role } from 'shared-code-lib';

@Component({
  selector: 'app-role-selector',
  templateUrl: './role-selector.component.html',
  styleUrls: ['./role-selector.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class RoleSelectorComponent implements OnInit, AfterViewInit, OnChanges {
  @ViewChild('roleSelectorModal', { static: true }) stepPropertiesModalElement: ElementRef;
  @Input() roles: Array<string>;
  @Output() rolesChange = new EventEmitter<Array<string>>();

  roleNames = '';

  allRoles: Array<Role>;

  constructor(
    private translateService: TranslateService,
    private userStateService: UserStateService,
    private ngxSmartModalService: NgxSmartModalService,
    private hydraApi: HydraApiService
  ) {}

  roleSelectorModal: NgxSmartModalComponent;

  ngOnInit() {
    this.loadPicklistItems();
    this.updateRoleNames();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!changes.roles) return;

    this.updateRoleNames();
  }

  ngAfterViewInit() {
    this.roleSelectorModal = this.ngxSmartModalService.getModal('roleSelectorModal');
  }

  loadPicklistItems = () => {
    this.hydraApi.getRoles(this.loaded, this.loadFailed);
  };

  loaded = (roles: Array<Role>) => {
    this.allRoles = roles;

    this.updateRoleNames();
  };

  loadFailed = () => {};

  updateRoleNames = (): void => {
    this.roleNames = '';
    if (!this.roles) return;
    if (!this.allRoles) return;

    if (this.roles.length == 0) {
      this.roleNames = this.translateService.instant('role-selector.any');
    } else {
      for (const rid of this.roles) {
        const role = this.allRoles.find((r) => r.id == rid);
        if (!role) continue;

        if (this.roleNames.length) this.roleNames += ', ';
        this.roleNames += role.name;
      }
    }
  };

  onUpdateSubmitRoles = (): void => {
    this.roleSelectorModal.open();

    this.ngxSmartModalService.setModalData(
      {
        selectedRoles: this.allRoles.filter((r) => this.roles.find((ri) => ri == r.id) != null),
        allowedRoles: this.allRoles,
      },
      'roleSelectorModal',
      true
    );
  };

  closeRoleSelectorModal = (): void => {
    this.roleSelectorModal.close();
  };

  currentChanged = (items: Array<Role>): void => {
    const data = this.ngxSmartModalService.getModalData('roleSelectorModal') as any;

    data.selectedRoles = items;
  };

  saveProperties = (): void => {
    this.roleSelectorModal.close();

    const data = this.ngxSmartModalService.getModalData('roleSelectorModal') as any;
    if (!data) return;

    this.roles = data.selectedRoles.map((r) => r.id);

    this.rolesChange.emit(this.roles);

    this.updateRoleNames();
  };
}
