import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { DetectPIIWorkflowStep, PIIEntityTypeWrapper } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-detect-pii',
  templateUrl: './step-detect-pii.component.html',
  styleUrls: ['./step-detect-pii.component.scss'],
})
export class StepDetectPiiComponent implements OnInit {
  @Input() step: Step;

  available: Array<PIIEntityTypeWrapper> = [];
  current: Array<PIIEntityTypeWrapper> = [];

  detectPIIStep: DetectPIIWorkflowStep;
  confidenceThresholdField: FieldDefinition;

  get confidenceThreshold() {
    return `${this.detectPIIStep.confidenceThreshold}`;
  }
  set confidenceThreshold(val: string) {
    this.detectPIIStep.confidenceThreshold = Number(val);
  }

  get redactPII() {
    return this.detectPIIStep.redactPII;
  }
  set redactPII(val: boolean) {
    this.detectPIIStep.redactPII = val;
  }

  constructor(private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    this.detectPIIStep = this.step.workflowStep as DetectPIIWorkflowStep;
    this.createFields();
    this.loadItems();
  }

  createFields = (): void => {
    this.confidenceThresholdField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translate.instant('step-detect-pii.confidenceThreshold'),
      placeholder: this.translate.instant('step-detect-pii.confidenceThreshold'),
      allowNegativeNumbers: false,
      required: true,
    });
  };

  loadItems = () => {
    this.available = this.getAllPIIEntityTypes();

    if (this.detectPIIStep.isNewStep()) {
      this.current = this.available.map((p) => new PIIEntityTypeWrapper(p.tag, p.displayName));
      this.currentChanged(this.current);
    } else {
      this.current = this.available.filter((f) => this.detectPIIStep.categories.includes(f.tag));
    }
  };

  currentChanged = (categories: Array<PIIEntityTypeWrapper>): void => {
    this.detectPIIStep.categories = [];
    categories.forEach((c) => {
      this.detectPIIStep.categories.push(c.tag);
    });
  };

  getAllPIIEntityTypes = () => {
    const arrayPIIEntityTypeWrapper: Array<PIIEntityTypeWrapper> = [];
    const piiTypeTranslations = this.translate.instant('step-detect-pii.pii-entity-type', { returnObject: true });
    const keys = Object.keys(piiTypeTranslations);
    keys.forEach((key) => {
      arrayPIIEntityTypeWrapper.push(new PIIEntityTypeWrapper(key, piiTypeTranslations[key]));
    });
    return arrayPIIEntityTypeWrapper;
  };
}
