import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  FieldDefinition,
  FixedDataSource,
  IFieldDataSource,
  IFieldOptions,
  MetaDataDefinition,
  MetadataIntellisenseDatasource,
  MetaDataType,
  ObjectIdGenerator,
} from 'shared-code-lib';
import { KeywordExtractionWorkflowStep } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-keyword-extraction',
  templateUrl: './step-keyword-extraction.component.html',
  styleUrls: ['./step-keyword-extraction.component.scss'],
})
export class StepKeywordExtractionComponent implements OnInit {
  @Input() step: Step;

  keywordExtractionStep: KeywordExtractionWorkflowStep;
  maxKeywordsField: FieldDefinition;
  fieldLineItemsId: FieldDefinition;
  fieldLineItemsText: FieldDefinition;
  fieldLineItemsIndex: FieldDefinition;
  keyWordDataSource: FixedDataSource;
  currentStepLineItem: string;

  get maxKeywords() {
    return `${this.keywordExtractionStep.maxKeywords}`;
  }
  set maxKeywords(val: string) {
    this.keywordExtractionStep.maxKeywords = Number(val);
  }

  constructor(
    private translate: TranslateService,
    private userService: UserStateService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.keywordExtractionStep = this.step.workflowStep as KeywordExtractionWorkflowStep;
    this.currentStepLineItem = this.keywordExtractionStep.lineItemsFieldId;
    this.createLineItemFields();
    this.createFields();
  }

  createLineItemFields = () => {
    const lineItemMetaDataOptions = new Array<IFieldOptions>();

    for (const mdd of this.userService.user.metaData) {
      if (mdd.type != MetaDataType.LineItems) continue;

      lineItemMetaDataOptions.push({ name: mdd.name, value: mdd.id });
    }

    lineItemMetaDataOptions.sort((x, y) => x.name.localeCompare(y.name));

    this.fieldLineItemsId = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-keyword-extraction.line-items-field-id'),
      required: false,
      datasource: new FixedDataSource(lineItemMetaDataOptions),
    });

    this.keyWordDataSource = new FixedDataSource(new Array<IFieldOptions>());
    this.fieldLineItemsText = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-keyword-extraction.line-item-field-text'),
      required: true,
      datasource: this.keyWordDataSource,
    });

    this.fieldLineItemsIndex = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-keyword-extraction.line-item-field-index'),
      required: false,
      datasource: this.keyWordDataSource,
    });
  };

  createFields() {
    this.maxKeywordsField = this.createField('maxKeywords', 'maxKeywords', MetaDataType.Integer, true);
  }

  createField = (
    label: string = null,
    placeholder: string = '',
    type: MetaDataType = MetaDataType.Text,
    required: boolean = false,
    substitutable: boolean = true,
    datasource: IFieldDataSource = null
  ): FieldDefinition => {
    return new FieldDefinition({
      type: type,
      label: label ? this.translate.instant(`step-keyword-extraction.${label}`) : '',
      placeholder: placeholder ? this.translate.instant(`step-keyword-extraction.${placeholder}`) : '',
      required: required,
      datasource: substitutable ? new MetadataIntellisenseDatasource(this.userService) : datasource,
      substitutable: substitutable,
    });
  };

  onLineItemsFieldChanged = (value: string): void => {
    const mddLineItem = this.userService.user.metaData.find((f) => f.id == value);
    if (!mddLineItem) return;

    const lineItemField = new MetaDataDefinition(mddLineItem);
    const keywordOptions: Array<IFieldOptions> = [];

    for (const config of lineItemField.lineItemConfigurations) {
      const mdd = this.userService.findMetaDataById(config.metaDataId);
      if (!mdd) continue;

      keywordOptions.push({ name: mdd.name, value: mdd.id });
    }

    keywordOptions.sort((x, y) => x.name.localeCompare(y.name));

    keywordOptions.unshift({
      name: this.translate.instant('Metadata-Form.none'),
      value: ObjectIdGenerator.Empty,
    });

    this.keyWordDataSource.updateOptions(keywordOptions);

    if (value !== this.currentStepLineItem) {
      this.keywordExtractionStep.lineItemFieldText = ObjectIdGenerator.Empty;
      this.keywordExtractionStep.lineItemFieldIndex = ObjectIdGenerator.Empty;
      this.cdr.detectChanges();
    }

    this.currentStepLineItem = this.keywordExtractionStep.lineItemsFieldId;
  };
}
