import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ObjectIdGenerator } from 'shared-code-lib';
import { FieldDefinition, IFieldOptions } from '../../../../../../../../Bamba.Shared/src/models/field-definition';
import { FixedDataSource } from '../../../../../../../../Bamba.Shared/src/models/fixed-datasource';
import { MetaDataType } from '../../../../../../../../Bamba.Shared/src/models/metadata-definition';
import { Workflow } from '../../../../blocks/models/workflow';
import { InteractiveWorkflowStep } from '../../../../blocks/models/workflow-step';

@Component({
  selector: 'app-step-interactive-advanced',
  templateUrl: './step-interactive-advanced.component.html',
  styleUrls: ['./step-interactive-advanced.component.scss'],
})
export class StepInteractiveAdvancedComponent implements OnInit {
  @Input() workflowStep: InteractiveWorkflowStep;
  @Input() workflow: Workflow;

  fieldSortBy: FieldDefinition;
  fieldSortDirection: FieldDefinition;
  fieldTimeoutInHours: FieldDefinition;
  fieldTimeoutOutcomeId: FieldDefinition;

  get workflowStepSortBy() {
    return `${this.workflowStep.sortBy}`;
  }
  set workflowStepSortBy(val: string) {
    this.workflowStep.sortBy = Number(val);
  }
  get workflowStepSortDirection() {
    return `${this.workflowStep.sortDirection}`;
  }
  set workflowStepSortDirection(val: string) {
    this.workflowStep.sortDirection = Number(val);
  }
  get workflowStepTimeoutInHours() {
    return `${this.workflowStep.timeoutInHours}`;
  }
  set workflowStepTimeoutInHours(val: string) {
    this.workflowStep.timeoutInHours = Number(val);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    const SortByOptions = [
      { name: this.translate.instant('admin-workflow.interactive-step-sort-by-date'), value: '0' },
      { name: this.translate.instant('admin-workflow.interactive-step-sort-by-name'), value: '1' },
    ];

    const sortDirectionOptions = [
      { name: this.translate.instant('admin-workflow.interactive-step-sort-direction-ascending'), value: '0' },
      { name: this.translate.instant('admin-workflow.interactive-step-sort-direction-descending'), value: '1' },
    ];

    this.fieldSortBy = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-workflow.interactive-step-sort-by'),
      placeholder: this.translate.instant('admin-workflow.interactive-step-sort-by-placeholder'),
      datasource: new FixedDataSource(SortByOptions),
      required: true,
    });

    this.fieldSortDirection = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-workflow.interactive-step-sort-direction'),
      placeholder: this.translate.instant('admin-workflow.interactive-step-sort-direction-placeholder'),
      datasource: new FixedDataSource(sortDirectionOptions),
      required: true,
    });

    this.fieldTimeoutInHours = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translate.instant('step-interactive.timeout-in-hours'),
      placeholder: this.translate.instant('step-interactive.timeout-in-hours'),
      required: false,
    });

    const options = new Array<IFieldOptions>();
    const sortedSteps = this.workflow.workflowSteps.sort((x, y) => x.name.localeCompare(y.name));
    options.push({
      name: this.translate.instant('Metadata-Form.none'),
      value: ObjectIdGenerator.Empty,
    });
    for (let step of sortedSteps) {
      const item = { name: step.name, value: step.id };
      options.push(item);
    }
    this.fieldTimeoutOutcomeId = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-interactive.timeout-outcome'),
      placeholder: this.translate.instant('step-interactive.timeout-outcome'),
      datasource: new FixedDataSource(options),
      required: false,
    });
  }
}
