import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  AnnotationConfiguration,
  FieldDefinition,
  Files,
  FixedDataSource,
  IFieldOptions,
  MetaDataType,
  Tab,
} from 'shared-code-lib';
import {
  FontColors,
  supportedBarcodesArray,
} from '../../../blocks/components/html-editor/html-editor-toolbar/editor-toolbar-constants';
import { Action } from '../../../blocks/core/actions';
import { Filename } from '../../../blocks/core/fileName';
import { DocumentStampWorkflowStep } from '../../../blocks/models/workflow-step';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';
import {
  ApplyToPages,
  AvailableTabs,
  ContentTextModes,
  ContentTypes,
  HorizontalLocations,
  LengthUnits,
  LocationTypes,
  VerticalLocations,
} from './step-document-stamp.enums';

@Component({
  selector: 'app-step-document-stamp',
  templateUrl: './step-document-stamp.component.html',
  styleUrl: './step-document-stamp.component.scss',
  encapsulation: ViewEncapsulation.None,
})
export class StepDocumentStampComponent implements OnInit {
  @Input() step: Step;

  readonly tabs: Tab[] = [
    new Tab(`${AvailableTabs.ContentTypes}`, this.translate.instant('step-document-stamp.content')),
    new Tab(`${AvailableTabs.LocationTypes}`, this.translate.instant('step-document-stamp.location')),
    new Tab(`${AvailableTabs.Options}`, this.translate.instant('step-document-stamp.options')),
  ];
  readonly supportedFileTypes = Filename.supportedImageFileTypes;

  documentStampWorkflowStep: DocumentStampWorkflowStep;
  currentlySelectedTab: AvailableTabs = AvailableTabs.ContentTypes;
  fields: IFields;

  AvailableTabs = AvailableTabs;
  ContentTypes = ContentTypes;
  LocationTypes = LocationTypes;
  Action = Action;
  Number = Number;

  constructor(
    private hydra: HydraApiService,
    private userState: UserStateService,
    private translate: TranslateService
  ) {}

  ngOnInit(): void {
    this.documentStampWorkflowStep = <DocumentStampWorkflowStep>{ ...this.step.workflowStep };

    this.hydra.getAnnotationConfigurations(this.onAnnotationConfigLoad, this.onAnnotationConfigLoadFailed);
  }

  onAnnotationConfigLoad = (config: AnnotationConfiguration) => {
    this.createFields(config);
  };

  onAnnotationConfigLoadFailed = (err: any) => {};

  createFields = (config: AnnotationConfiguration): void => {
    this.fields = {} as IFields;

    const fontFamilies: IFieldOptions[] = config.fontNames.map((f) => ({
      name: f,
      value: f,
      style: `font-family: ${f};`,
    }));
    const fontSizes: IFieldOptions[] = config.fontSizes.map((px) => ({
      name: `${px}`,
      value: `${px}`,
      style: `font-size: ${px}px;`,
    }));
    const fontColors: IFieldOptions[] = FontColors.defaultColors.map((c) => ({
        name: c.key,
        value: c.value,
    }));

    const barcodeTypes: IFieldOptions[] = supportedBarcodesArray
      .filter((bc) => this.userState.hasFeature(`BARCODE_${bc.value.toUpperCase()}`))
      .map((bc) => ({ name: bc.name, value: bc.value }));
    const stamps: IFieldOptions[] = config.stamps.map((s) => ({ name: s, value: s }));

    this.fields.content = {
      type: new FieldDefinition({
        type: MetaDataType.List,
        datasource: new FixedDataSource([
          { name: this.translate.instant('step-document-stamp.text'), value: 'TextContent' },
          { name: this.translate.instant('step-document-stamp.image'), value: 'ImageContent' },
          { name: this.translate.instant('step-document-stamp.stamp'), value: 'StampContent' },
          { name: this.translate.instant('step-document-stamp.barcode'), value: 'BarcodeContent' },
        ]),
        label: this.translate.instant('step-document-stamp.type'),
      }),
      text: {
        font: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource(fontFamilies),
          defaultValue: fontFamilies[0].value,
          label: this.translate.instant('step-document-stamp.font-family'),
        }),
        size: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource(fontSizes),
          defaultValue: fontSizes[0].value,
          label: this.translate.instant('step-document-stamp.font-size'),
        }),
        mode: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource([
            { name: this.translate.instant('step-document-stamp.solid'), value: `${ContentTextModes.Solid}` },
            { name: this.translate.instant('step-document-stamp.outline'), value: `${ContentTextModes.Outline}` },
          ]),
          defaultValue: `${ContentTextModes.Solid}`,
          label: this.translate.instant('step-document-stamp.font-mode'),
        }),
        color: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource(fontColors),
          defaultValue: fontColors.find((f) => f.name === 'Black').value,
          label: this.translate.instant('step-document-stamp.font-color'),
        }),
        textField: new FieldDefinition({
          type: MetaDataType.LargeText,
          label: this.translate.instant('step-document-stamp.text'),
          substitutable: true,
        }),
      },
      image: {
        imageField: null,
      },
      stamp: {
        stampField: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource(stamps),
          defaultValue: stamps[0].value,
          label: this.translate.instant('step-document-stamp.stamp'),
        }),
      },
      barcode: {
        barcodeType: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource(barcodeTypes),
          defaultValue: barcodeTypes[0].value,
          label: this.translate.instant('step-document-stamp.barcode-type'),
        }),
        barcodeValue: new FieldDefinition({
          type: MetaDataType.LargeText,
          label: this.translate.instant('step-document-stamp.barcode-value'),
        }),
      },
    };

    this.fields.location = {
      type: new FieldDefinition({
        type: MetaDataType.List,
        datasource: new FixedDataSource([
          { name: this.translate.instant('step-document-stamp.relative'), value: 'RelativeLocation' },
          { name: this.translate.instant('step-document-stamp.specific'), value: 'SpecificLocation' },
          { name: this.translate.instant('step-document-stamp.stretch'), value: 'StretchLocation' },
        ]),
        defaultValue: 'RelativeLocation',
        label: this.translate.instant('step-document-stamp.type'),
      }),
      relative: {
        horizontal: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource([
            { name: this.translate.instant('step-document-stamp.left'), value: `${HorizontalLocations.Left}` },
            { name: this.translate.instant('step-document-stamp.center'), value: `${HorizontalLocations.Center}` },
            { name: this.translate.instant('step-document-stamp.right'), value: `${HorizontalLocations.Right}` },
          ]),
          defaultValue: `${HorizontalLocations.Left}`,
          label: this.translate.instant('step-document-stamp.horizontal-alignment'),
        }),
        vertical: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource([
            { name: this.translate.instant('step-document-stamp.top'), value: `${VerticalLocations.Top}` },
            { name: this.translate.instant('step-document-stamp.center'), value: `${VerticalLocations.Center}` },
            { name: this.translate.instant('step-document-stamp.bottom'), value: `${VerticalLocations.Bottom}` },
          ]),
          defaultValue: `${VerticalLocations.Top}`,
          label: this.translate.instant('step-document-stamp.vertical-alignment'),
        }),
      },
      specific: {
        top: new FieldDefinition({
          type: MetaDataType.Decimal,
          label: this.translate.instant('step-document-stamp.top'),
        }),
        left: new FieldDefinition({
          type: MetaDataType.Decimal,
          label: this.translate.instant('step-document-stamp.left'),
        }),
        topUnits: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource([
            { name: this.translate.instant('step-document-stamp.in'), value: `${LengthUnits.Inches}` },
            { name: this.translate.instant('step-document-stamp.mm'), value: `${LengthUnits.Millimeters}` },
            { name: this.translate.instant('step-document-stamp.pt'), value: `${LengthUnits.Points}` },
          ]),
          defaultValue: `${LengthUnits.Inches}`,
          label: this.translate.instant('step-document-stamp.top-units'),
        }),
        leftUnits: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource([
            { name: this.translate.instant('step-document-stamp.in'), value: `${LengthUnits.Inches}` },
            { name: this.translate.instant('step-document-stamp.mm'), value: `${LengthUnits.Millimeters}` },
            { name: this.translate.instant('step-document-stamp.pt'), value: `${LengthUnits.Points}` },
          ]),
          defaultValue: `${LengthUnits.Inches}`,
          label: this.translate.instant('step-document-stamp.left-units'),
        }),
      },
      stretch: {
        insertAt: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource([
            { name: this.translate.instant('step-document-stamp.top'), value: `${VerticalLocations.Top}` },
          ]),
          defaultValue: `${VerticalLocations.Top}`,
          label: this.translate.instant('step-document-stamp.insert-at'),
        }),
        horizontalAlignment: new FieldDefinition({
          type: MetaDataType.List,
          datasource: new FixedDataSource([
            { name: this.translate.instant('step-document-stamp.left'), value: `${HorizontalLocations.Left}` },
            { name: this.translate.instant('step-document-stamp.center'), value: `${HorizontalLocations.Center}` },
            { name: this.translate.instant('step-document-stamp.right'), value: `${HorizontalLocations.Right}` },
          ]),
          defaultValue: `${HorizontalLocations.Left}`,
          label: this.translate.instant('step-document-stamp.horizontal-alignment'),
        }),
      },
    };

    this.fields.options = {
      rotate: new FieldDefinition({
        type: MetaDataType.Integer,
        label: this.translate.instant('step-document-stamp.rotation-degrees'),
      }),
      applyTo: new FieldDefinition({
        type: MetaDataType.List,
        datasource: new FixedDataSource([
          { name: this.translate.instant('step-document-stamp.all-pages'), value: `${ApplyToPages.All}` },
          { name: this.translate.instant('step-document-stamp.first-page'), value: `${ApplyToPages.First}` },
          { name: this.translate.instant('step-document-stamp.last-page'), value: `${ApplyToPages.Last}` },
        ]),
        defaultValue: `${ApplyToPages.All}`,
        label: this.translate.instant('step-document-stamp.apply-to'),
      }),
    };
  };

  onTabSelected = (tab: string): void => {
    this.currentlySelectedTab = Number(tab);
  };

  onFileInputChange = async (event: Event): Promise<void> => {
    const files = Array.from((event.target as HTMLInputElement).files);

    const base64 = files.length !== 0 ? await Files.toBase64String(files[0], true) : '';

    this.documentStampWorkflowStep.content.base64 = base64;
  };
}

interface IFields {
  content: {
    type: FieldDefinition;
    text: {
      font: FieldDefinition;
      size: FieldDefinition;
      mode: FieldDefinition;
      color: FieldDefinition;
      textField: FieldDefinition;
    };
    image: {
      imageField: FieldDefinition;
    };
    stamp: {
      stampField: FieldDefinition;
    };
    barcode: {
      barcodeType: FieldDefinition;
      barcodeValue: FieldDefinition;
    };
  };
  location: {
    type: FieldDefinition;
    relative: {
      horizontal: FieldDefinition;
      vertical: FieldDefinition;
    };
    specific: {
      top: FieldDefinition;
      left: FieldDefinition;
      topUnits: FieldDefinition;
      leftUnits: FieldDefinition;
    };
    stretch: {
      insertAt: FieldDefinition;
      horizontalAlignment: FieldDefinition;
    };
  };
  options: {
    rotate: FieldDefinition;
    applyTo: FieldDefinition;
  };
}
