import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';

import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { TranslateService } from '@ngx-translate/core';
import { DelayService, FieldDefinition, FixedDataSource, IFieldOptions, Key, MetaDataType } from 'shared-code-lib';
import {
  Threshold,
  ThresholdDataField,
  ThresholdStyle,
  ThresholdUnits,
} from '../../../../blocks/models/workflow-threshold';

@Component({
  selector: 'app-step-interactive-thresholds',
  templateUrl: './step-interactive-thresholds.component.html',
  styleUrls: ['./step-interactive-thresholds.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepInteractiveThresholdsComponent implements OnInit {
  @Input() thresholds: Array<Threshold> = [];
  @Output() thresholdsChange = new EventEmitter<Array<Threshold>>();

  dataFieldDataSource: FixedDataSource;
  unitsDataSource: FixedDataSource;
  stylesDataSource: FixedDataSource;

  dataFieldDropLists = new Array<FieldDefinition>();
  unitsdDropLists = new Array<FieldDefinition>();
  stylesDropLists = new Array<FieldDefinition>();

  fieldTooltip: FieldDefinition;

  sortable = false;
  constructor(private delay: DelayService, private translate: TranslateService) {}

  ngOnInit() {
    const dataFields = new Array<IFieldOptions>();
    dataFields.push({
      name: this.translate.instant('step-interactive.date-created'),
      value: `${ThresholdDataField.DateCreated}`,
    });
    dataFields.push({
      name: this.translate.instant('step-interactive.date-modified'),
      value: `${ThresholdDataField.DateModified}`,
    });
    this.dataFieldDataSource = new FixedDataSource(dataFields);

    const units = new Array<IFieldOptions>();
    units.push({ name: this.translate.instant('step-interactive.unit-minutes'), value: `${ThresholdUnits.Minutes}` });
    units.push({ name: this.translate.instant('step-interactive.unit-hours'), value: `${ThresholdUnits.Hours}` });
    units.push({ name: this.translate.instant('step-interactive.unit-days'), value: `${ThresholdUnits.Days}` });
    this.unitsDataSource = new FixedDataSource(units);

    const styles = new Array<IFieldOptions>();
    styles.push({ name: this.translate.instant('step-interactive.style-caution'), value: `${ThresholdStyle.Caution}` });
    styles.push({ name: this.translate.instant('step-interactive.style-warning'), value: `${ThresholdStyle.Warning}` });
    styles.push({
      name: this.translate.instant('step-interactive.style-critical'),
      value: `${ThresholdStyle.Critical}`,
    });
    this.stylesDataSource = new FixedDataSource(styles);

    this.fieldTooltip = new FieldDefinition({
      type: MetaDataType.Text,
      placeholder: this.translate.instant('step-interactive.threshold-tooltip-placeholder'),
    });
  }

  onDeleteThreshold = (threshold: Threshold): void => {
    this.thresholds = this.thresholds.filter((o) => o.id !== threshold.id);
    this.thresholdsChange.emit(this.thresholds);
  };

  onThresholdOrderChanged = (): void => {
    this.thresholdsChange.emit(this.thresholds);
  };

  onAddThreshold = (): void => {
    this.thresholds.push(Threshold.create());
    this.thresholdsChange.emit(this.thresholds);

    this.delay.execute(this.setFocusToAddedThreshold, 1);
  };

  setFocusToAddedThreshold = (): void => {
    const id = `workflow-threshold-${this.thresholds.length - 1}`;
    const lastThreshold = document.getElementById(id);
    if (lastThreshold) {
      lastThreshold.focus();
    }
  };

  getUnitsDropList = (index: number): FieldDefinition =>
    this.getDropListField(index, this.unitsdDropLists, this.unitsDataSource);
  getDataFieldDropList = (index: number): FieldDefinition =>
    this.getDropListField(index, this.dataFieldDropLists, this.dataFieldDataSource);
  getStylesDropList = (index: number): FieldDefinition =>
    this.getDropListField(index, this.stylesDropLists, this.stylesDataSource);

  getDropListField = (index: number, dropLists: Array<FieldDefinition>, ds: FixedDataSource): FieldDefinition => {
    while (dropLists.length <= index) {
      dropLists.push(
        new FieldDefinition({
          type: MetaDataType.List,
          label: '',
          placeholder: '',
          datasource: ds,
          required: false,
        })
      );
    }

    return dropLists[index];
  };

  validateNumber = (event): boolean => {
    var key;
    if (window.event) key = event.key;

    if (event.key === Key.Backspace || event.key === Key.Delete) {
      return true;
    } else if (isNaN(Number(key)) || event.key === Key.Space) {
      return false;
    } else {
      return true;
    }
  };

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.thresholds, event.previousIndex, event.currentIndex);
  }

  getThresholdDataField = (index: number) => {
    return `${this.thresholds[index].dataField}`;
  };
  setThresholdDataField = (index: number, val: string) => {
    this.thresholds[index].dataField = Number(val);
  };
  getThresholdUnit = (index: number) => {
    return `${this.thresholds[index].unit}`;
  };
  setThresholdUnit = (index: number, val: string) => {
    this.thresholds[index].unit = Number(val);
  };
  getThresholdStyle = (index: number) => {
    return `${this.thresholds[index].style}`;
  };
  setThresholdStyle = (index: number, val: string) => {
    this.thresholds[index].style = Number(val);
  };
}
