import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { BarcodeCoverPageWorkflowStep, BarcodeMapping } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-barcode-cover-page',
  templateUrl: './step-barcode-cover-page.component.html',
  styleUrls: ['./step-barcode-cover-page.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepBarcodeCoverPageComponent implements OnInit {
  @ViewChild(PerfectScrollbarDirective, { static: true }) directiveRef?: PerfectScrollbarDirective;
  @Input() step: Step;

  fieldDisplayName: FieldDefinition;
  fieldExpectedBarcodeCount: FieldDefinition;
  workflowStep: BarcodeCoverPageWorkflowStep;
  dataFieldsDataSource: FixedDataSource;
  dataFieldsLoaded: boolean = false;
  expectedNumberOfBarcodes: string;

  constructor(
    private translate: TranslateService,
    private userState: UserStateService
  ) {}

  ngOnInit() {
    this.workflowStep = <BarcodeCoverPageWorkflowStep>this.step.workflowStep;
    this.dataFieldsDataSource = new FixedDataSource(new Array<IFieldOptions>());
    if (!this.workflowStep.barcodeMappings.length) this.workflowStep.barcodeMappings.push(BarcodeMapping.create());
    this.expectedNumberOfBarcodes = this.workflowStep.barcodeMappings.length.toString();
    this.loadMetaData();
    this.createFields();
  }

  loadMetaData = (): void => {
    const metadataFields: Array<IFieldOptions> = [];

    metadataFields.push({
      name: this.translate.instant('admin-workflow.none'),
      value: '',
    });

    const mdds = this.userState.user.metaData.filter(
      (mdd) =>
        mdd.type !== MetaDataType.Expression &&
        mdd.type !== MetaDataType.CascadingOptions &&
        mdd.type !== MetaDataType.LineItems
    );

    mdds.forEach((mdd) => {
      metadataFields.push({
        name: mdd.name,
        value: mdd.id,
      });
    });

    this.dataFieldsDataSource.updateOptions(metadataFields);
    this.dataFieldsLoaded = true;
  };

  createFields() {
    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translate.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });

    const expectedCounts: Array<IFieldOptions> = [];
    for (let i = 1; i <= 50; i++) {
      expectedCounts.push({ name: i.toString(), value: i.toString() });
    }

    this.fieldExpectedBarcodeCount = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-workflow.expected-barcode-count'),
      placeholder: this.translate.instant('admin-workflow.expected-barcode-count'),
      required: true,
      datasource: new FixedDataSource(expectedCounts),
    });
  }

  createListField = (label: string, placeholder: string, datasource: FixedDataSource) => {
    return new FieldDefinition({
      type: MetaDataType.List,
      label: label ? this.translate.instant(label) : '',
      placeholder: this.translate.instant(placeholder),
      required: true,
      datasource: datasource,
    });
  };

  createTextField = (label: string, placeholder: string) => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: label ? this.translate.instant(label) : '',
      placeholder: this.translate.instant(placeholder),
    });
  };

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.workflowStep.barcodeMappings, event.previousIndex, event.currentIndex);
  }

  onExpectedBarcodesChanged = (): void => {
    const count = parseInt(this.expectedNumberOfBarcodes);
    if (this.workflowStep.barcodeMappings.length > count) {
      this.workflowStep.barcodeMappings.splice(count);
      return;
    }

    do {
      this.workflowStep.barcodeMappings.push(BarcodeMapping.create());
    } while (this.workflowStep.barcodeMappings.length < count);
  };
}
