import { TranslateService } from '@ngx-translate/core';
import { IWorkflowStep } from '../../blocks/models/workflow-step';

export class Step {
  public workflowStep: IWorkflowStep;

  constructor(public stepType: string) {}

  clone = (): Step => {
    const step = new Step(this.stepType);
    if (this.workflowStep) step.workflowStep = this.workflowStep.clone();

    return step;
  };

  getCellName = (translate: TranslateService): string => {
    return this.workflowStep.name;
  };

  private static steps: Array<Step>;

  static getSteps = (): Array<Step> => {
    if (Step.steps == null) {
      Step.steps = [];

      Step.steps.push(new Step('start'));
      Step.steps.push(new Step('completed'));
      Step.steps.push(new Step('completedfailed'));
      Step.steps.push(new Step('bursting'));
      Step.steps.push(new Step('ocr'));
      Step.steps.push(new Step('interactivestep'));
      Step.steps.push(new Step('forms'));
      Step.steps.push(new Step('formsai'));
      Step.steps.push(new Step('delivertofilebound'));
      Step.steps.push(new Step('imageprocessing'));
      Step.steps.push(new Step('decisionpoint'));
      Step.steps.push(new Step('movetoworkflow'));
      Step.steps.push(new Step('delivertoemail'));
      Step.steps.push(new Step('delivertointerfax'));
      Step.steps.push(new Step('delivertonetdocs'));
      Step.steps.push(new Step('delivertoawss3'));
      Step.steps.push(new Step('delivertosharepoint365'));
      Step.steps.push(new Step('routingsheet'));
      Step.steps.push(new Step('exporttoapi'));
      Step.steps.push(new Step('barcodedatamapping'));
      Step.steps.push(new Step('delivertoftp'));
      Step.steps.push(new Step('setdatafields'));
      Step.steps.push(new Step('exporttowebservice'));
      Step.steps.push(new Step('barcodecoverpage'));
      Step.steps.push(new Step('invoiceextraction'));
      Step.steps.push(new Step('detectsignature'));
      Step.steps.push(new Step('detectpii'));
      Step.steps.push(new Step('translatedocument'));
      Step.steps.push(new Step('simpleclassifier'));
      Step.steps.push(new Step('addtemplate'));
      Step.steps.push(new Step('summarization'));
      Step.steps.push(new Step('keywordextraction'));
      Step.steps.push(new Step('dataextraction'));
      Step.steps.push(new Step('extractionpro'));
      Step.steps.push(new Step('classifier'));
      Step.steps.push(new Step('duplicate'));
      Step.steps.push(new Step('renamedocument'));
      Step.steps.push(new Step('ocrplus'));
      Step.steps.push(new Step('documentstamp'));
    }

    return Step.steps;
  };

  static getStepFor = (stepType: string): Step => {
    console.log(stepType);

    const step = Step.getSteps().find((s) => s.stepType == stepType);
    if (step == null) return null;

    return step.clone();
  };
}

export enum StepCategory {
  Process = 0,
  Export = 1,
}
