import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';

import { FieldDefinition, IFieldDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { Workflow } from '../../../blocks/models/workflow';

@Component({
  selector: 'app-scan-settings',
  templateUrl: './scan-settings.component.html',
  styleUrls: ['./scan-settings.component.scss'],
})
export class ScanSettingsComponent implements OnInit {
  @Input() workflow: Workflow;

  fieldColorMode: FieldDefinition;
  fieldDPI: FieldDefinition;
  fieldPageSize: FieldDefinition;
  fieldSides: FieldDefinition;

  listColorMode: ListDataSource;
  listDPI: ListDataSource;
  listPageSize: ListDataSource;
  listSides: ListDataSource;

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.createDataSources();
    this.createFields();
  }

  getValue = (name: string): string => {
    const scansetting = this.workflow.scanSettings.find((s) => s.name == name);
    if (!scansetting) return '';

    return scansetting.value;
  };

  setValue = (name: string, value: string): void => {
    const scansetting = this.workflow.scanSettings.find((s) => s.name == name);
    if (!scansetting) return;

    scansetting.value = value;
  };

  createDataSources = (): void => {
    this.listColorMode = new ListDataSource(this.translate);
    this.listColorMode.addItem('scan-settings.color-mode-blackwhite', 'BlackWhite');
    this.listColorMode.addItem('scan-settings.color-mode-gray', 'Gray');
    this.listColorMode.addItem('scan-settings.color-mode-rgb', 'RGB');

    this.listDPI = new ListDataSource(this.translate);
    this.listDPI.addItem('100', '100');
    this.listDPI.addItem('150', '150');
    this.listDPI.addItem('200', '200');
    this.listDPI.addItem('300', '300');
    this.listDPI.addItem('400', '400');
    this.listDPI.addItem('500', '500');
    this.listDPI.addItem('600', '600');

    this.listPageSize = new ListDataSource(this.translate);
    this.listPageSize.addItem('scan-settings.page-size-usletter', 'USLetter');
    this.listPageSize.addItem('scan-settings.page-size-uslegal', 'USLegal');
    this.listPageSize.addItem('scan-settings.page-size-a4', 'A4');

    this.listSides = new ListDataSource(this.translate);
    this.listSides.addItem('scan-settings.sides-duplex', '1');
    this.listSides.addItem('scan-settings.sides-simplex', '0');
  };

  createFields = (): void => {
    this.fieldColorMode = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('scan-settings.color-mode'),
      placeholder: this.translate.instant('scan-settings.color-mode-placeholder'),
      required: true,
      datasource: this.listColorMode,
    });

    this.fieldDPI = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('scan-settings.dpi'),
      placeholder: this.translate.instant('scan-settings.dpi-placeholder'),
      required: true,
      datasource: this.listDPI,
    });

    this.fieldPageSize = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('scan-settings.page-size'),
      placeholder: this.translate.instant('scan-settings.page-size-placeholder'),
      required: true,
      datasource: this.listPageSize,
    });

    this.fieldSides = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('scan-settings.sides'),
      placeholder: this.translate.instant('scan-settings.sides-placeholder'),
      required: true,
      datasource: this.listSides,
    });
  };
}

class ListDataSource implements IFieldDataSource {
  changed = new Subject<void>();
  items = new Array<IFieldOptions>();

  constructor(private translate: TranslateService) {}

  addItem = (name: string, value: string): void => {
    this.items.push({ name: this.translate.instant(name), value: value });
  };

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    successCallBack(this.items);
  }
}
