import { HttpClient } from '@angular/common/http';
import { Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Subscription } from 'rxjs';
import { DelayService } from 'shared-code-lib';
import { DeliverToSharePoint365WorkflowStep } from '../../../../blocks/models/workflow-step';
import { SharePointService } from '../../../../blocks/services/sharepoint.service';
import { SharePointFolder } from '../models/sharepoint-folder';
import { SharePointFolderResult } from '../models/sharepoint-folders-result';

@Component({
  selector: 'app-sharepoint-folder',
  templateUrl: './sharepoint-folder.component.html',
  styleUrls: ['./sharepoint-folder.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SharePointFolderComponent implements OnInit, OnDestroy {
  @Input() folder: SharePointFolder;
  @Input() deliverToSharePoint365: DeliverToSharePoint365WorkflowStep;

  isExpanded: boolean = false;
  selectedFolder: SharePointFolder;
  subscriptions: Subscription;

  constructor(private http: HttpClient, private sharePointService: SharePointService, private delay: DelayService) {
    this.subscriptions = new Subscription();
  }

  ngOnInit() {
    this.subscriptions.add(
      this.sharePointService.onSelectFolder.subscribe((folder: SharePointFolder) => {
        this.selectedFolder = folder;
      })
    );

    this.subscriptions.add(
      this.sharePointService.onExpandAndSelect.subscribe((selectionInfo: any) => {
        if (!selectionInfo || !selectionInfo.folderIds || selectionInfo.folderIds.length === 0) return;
        if (selectionInfo.folderIds[selectionInfo.levelIndex] !== this.folder.id) return;

        this.expandAndSelect(selectionInfo);
      })
    );
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
  }

  toggleExpand(): Promise<void> {
    return new Promise((resolve, reject) => {
      this.isExpanded = !this.isExpanded;
      this.folder.expanded = this.isExpanded;

      if (this.isExpanded && !this.folder.folders) {
        this.loadChildFolders().then(() => {
          resolve();
        });
      } else {
        resolve();
      }
    });
  }

  isSelected() {
    return this.selectedFolder && this.selectedFolder.id === this.folder.id;
  }

  loadChildFolders(): Promise<void> {
    return new Promise((resolve, reject) => {
      this.folder.folders = [];
      const component = this;
      this.sharePointService.showSpinner(true);
      this.sharePointService.getFolderChildren(
        this.deliverToSharePoint365,
        this.folder.id,
        (result: SharePointFolderResult) => {
          this.sharePointService.showSpinner(false);
          if (result.succeeded) {
            for (const folder of result.folders) {
              let sharePointFolder = new SharePointFolder(
                folder.id,
                folder.url,
                this.folder.documentLibraryId,
                folder.name,
                folder.hasChildFolders
              );

              component.folder.folders.push(sharePointFolder);
              component.folder.expanded = true;
            }
            this.sharePointService.showSpinner(false);
            resolve();
          } else {
            this.sharePointService.showSpinner(false);
            this.sharePointService.showWarningMessage(result.error);
            reject();
          }
        },
        (err) => {
          this.sharePointService.showSpinner(false);
          this.sharePointService.showWarningMessage(err.error);
          reject();
        }
      );
    });
  }

  onSelectFolder(event: Event) {
    this.sharePointService.selectedFolder(this.folder);
  }

  expandAndSelect(selectionInfo: any) {
    if (selectionInfo.levelIndex === selectionInfo.folderIds.length - 1) {
      this.sharePointService.selectedFolder(this.folder);
      return;
    }

    if (selectionInfo.levelIndex > selectionInfo.folderIds.length - 1) return;

    if (!this.folder.expanded) {
      this.toggleExpand().then(() => {
        if (!this.folder.folders) return;

        selectionInfo.levelIndex++;
        for (const folder of this.folder.folders) {
          if (
            selectionInfo.levelIndex === selectionInfo.folderIds.length - 1 &&
            selectionInfo.folderIds[selectionInfo.folderIds.length - 1] == folder.id
          ) {
            this.delay.execute(() => {
              this.sharePointService.selectedFolder(folder);
            }, 100);
            return;
          }

          this.delay.execute(() => {
            this.sharePointService.expandAndSelect(selectionInfo);
          }, 100);
          return;
        }
      });
    }
  }
}
