import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { ExportMetaDataCSV } from '../../models/export-metadata';
import { FieldDefinition } from '../../../../../../../../Bamba.Shared/src/models/field-definition';
import { TranslateService } from '@ngx-translate/core';
import { MetaDataType } from 'shared-code-lib';

@Component({
  selector: 'app-export-metadata-csv',
  templateUrl: './export-metadata-csv.component.html',
  styleUrls: ['./export-metadata-csv.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ExportMetaDataCsvComponent implements OnInit {
  @Input() exportMetaDataCSV: ExportMetaDataCSV;

  fieldDelimiter: FieldDefinition;

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields(): void {
    this.fieldDelimiter = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('export-metadata.delimiter'),
      placeholder: this.translate.instant('export-metadata.delimiter-placeholder'),
    });
  }
}
