import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType, Tab } from 'shared-code-lib';
import { ExportToAPIWorkflowStep } from '../../../blocks/models/workflow-step';
import { Step } from '../step';

@Component({
  selector: 'app-step-export-to-api',
  templateUrl: './step-export-to-api.component.html',
  styleUrls: ['./step-export-to-api.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepExportToAPIComponent implements OnInit {
  @Input() step: Step;

  fieldDisplayName: FieldDefinition;
  exportToAPIStep: ExportToAPIWorkflowStep;

  activeTab = 'roles';
  tabs = new Array<Tab>();

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.exportToAPIStep = <ExportToAPIWorkflowStep>this.step.workflowStep;
    this.createFields();
    this.createTabs();
  }

  createFields() {
    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translate.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });
  }

  createTabs() {
    this.tabs.push(new Tab('roles', this.translate.instant('step-interactive.roles')));
  }

  activateTab(view: string) {
    this.activeTab = view;
  }
}
