import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Input, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { cloneDeep } from 'lodash';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { Observable, Subscription } from 'rxjs';
import { ObjectIdGenerator } from '../../../../../../../Bamba.Shared';
import { FieldDefinition, IFieldOptions } from '../../../../../../../Bamba.Shared/src/models/field-definition';
import { FixedDataSource } from '../../../../../../../Bamba.Shared/src/models/fixed-datasource';
import { MetaDataType } from '../../../../../../../Bamba.Shared/src/models/metadata-definition';
import { DelayService } from '../../../../../../../Bamba.Shared/src/utils/delay.service';
import { Classified, NotClassified, Outcome } from '../../../blocks/models/outcome';
import {
  Classification,
  ContentCriteria,
  IContentCriteria,
  PageSelectionCriteria,
  SimpleClassifierWorkflowStep,
} from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-simple-classifier',
  templateUrl: './step-simple-classifier.component.html',
  styleUrls: ['./step-simple-classifier.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepSimpleClassifierComponent implements OnInit, OnDestroy {
  @Input() step: Step;
  @Input() onSave: Observable<any>;
  @ViewChild(PerfectScrollbarDirective) perfectScrollbar: PerfectScrollbarDirective;

  simpleClassifierStep: SimpleClassifierWorkflowStep;

  metaDataOptions: IFieldOptions[] = [];
  fieldDataField: FieldDefinition;

  subscription = new Subscription();

  get stepHasOneClassification() {
    return this.simpleClassifierStep.classifications.length === 1;
  }

  constructor(
    private translateService: TranslateService,
    private delayService: DelayService,
    private userState: UserStateService
  ) {}

  ngOnInit(): void {
    this.initStep();
    this.subscription.add(this.onSave.subscribe(this.saveChangesToWorkflow));

    this.setupMetaData();
    this.createFields();
  }

  initStep(): void {
    this.simpleClassifierStep = cloneDeep(this.step.workflowStep as SimpleClassifierWorkflowStep);
    if (!this.simpleClassifierStep.classifications.length)
      this.simpleClassifierStep.classifications.push(this.getDefaultClassificationDefinition());
  }

  saveChangesToWorkflow = (): void => {
    const nextId = this.simpleClassifierStep.outcomes.find((o) => o.outcomeType == 'notclassified')?.nextId ?? '';

    this.simpleClassifierStep.outcomes = this.simpleClassifierStep.classifications.map(this.outcomeFromClassification);

    this.simpleClassifierStep.outcomes.push(
      new NotClassified({
        id: this.simpleClassifierStep.id,
        outcomeType: 'notclassified',
        text: '',
        label: '',
        enabled: true,
        requiresValidMetaData: false,
        requiresConfirmation: false,
        nextId: nextId,
      })
    );

    this.step.workflowStep = cloneDeep(this.simpleClassifierStep);
  };

  outcomeFromClassification = (c: Classification): Outcome => {
    const nextId = this.simpleClassifierStep.outcomes.find((o) => o.id == c.id)?.nextId ?? '';

    return new Classified({
      id: c.id,
      outcomeType: 'classified',
      text: c.name,
      label: c.name,
      enabled: true,
      requiresValidMetaData: false,
      requiresConfirmation: false,
      nextId: nextId,
    });
  };

  getDefaultClassificationDefinition(): Classification {
    return new Classification({
      id: ObjectIdGenerator.generateNewObjectId(),
      name: '',
      contentCriteriaRequired: 0,
      contentCriteria: [this.getDefaultContentCriteria()],
    });
  }

  getDefaultContentCriteria(): IContentCriteria {
    return ContentCriteria.create({
      contentCriteriaType: 'RegexContentCriteria',
      pageSelection: PageSelectionCriteria.First,
      pageRange: '',
      onlyIfOnSpecificPages: false,
      text: '',
      caseInsensitive: false,
      repeats: false,
      minimumRepeats: 1,
      maximumRepeats: 1,
    } as IContentCriteria);
  }

  setupMetaData(): void {
    this.metaDataOptions = this.userState.user.metaData
      .filter((metaData) => metaData.type !== MetaDataType.LineItems)
      .map((metaData) => ({ name: metaData.label, value: metaData.id }));

    this.metaDataOptions.unshift({
      name: this.translateService.instant('simple-classifier-step-properties.form-type'),
      value: '',
    });
  }

  createFields(): void {
    this.fieldDataField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('simple-classifier-step-properties.data-field-label'),
      placeholder: '',
      datasource: new FixedDataSource(this.metaDataOptions),
      required: true,
    });
  }

  moveItem(event: CdkDragDrop<string[]>): void {
    moveItemInArray(this.simpleClassifierStep.classifications, event.previousIndex, event.currentIndex);
  }

  deleteClassification(indexToDelete: number): void {
    if (this.stepHasOneClassification) return;
    this.simpleClassifierStep.classifications.splice(indexToDelete, 1);
    this.perfectScrollbar.update();
  }

  addClassification(): void {
    this.simpleClassifierStep.classifications.push(this.getDefaultClassificationDefinition());
    this.delayService.execute(() => {
      const classificationNameField = <HTMLInputElement>(
        document.querySelector(`#classification-${this.simpleClassifierStep.classifications.length - 1} input`)
      );
      if (classificationNameField) {
        classificationNameField.focus();
      }
    }, 50);
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
