import { Component, Input, ViewEncapsulation } from '@angular/core';
import { ExportMetaDataXML } from '../../models/export-metadata';
import { TranslateService } from '@ngx-translate/core';
import { Filename } from '../../../../blocks/core/fileName';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-export-metadata-xml',
  templateUrl: './export-metadata-xml.component.html',
  styleUrls: ['./export-metadata-xml.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ExportMetaDataXmlComponent {
  @Input() exportMetaDataXML: ExportMetaDataXML;

  constructor(private translate: TranslateService, private toastr: ToastrService) {}

  get xltFilename(): string {
    return !!this.exportMetaDataXML.xltFilename
      ? this.exportMetaDataXML.xltFilename
      : this.translate.instant('export-metadata.no-file-selected');
  }

  onXltFileSelected(event: any): void {
    if (!event.target.files?.length) return;

    const file = event.target.files[0];

    if (!Filename.extensionIsOneOf(file.name, ['xslt'])) {
      this.toastr.error(this.translate.instant('export-metadata.only-xslt-supported'));
      return;
    }

    this.exportMetaDataXML.xltFilename = file.name;

    const reader = new FileReader();

    reader.onload = (e: ProgressEvent<FileReader>) => {
      this.exportMetaDataXML.xltContent = e.target?.result?.toString() || '';
    };

    reader.readAsText(file);
  }
}
