import { AfterViewInit, Component, ElementRef, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ApiResult, Control } from '../../blocks/services/models';
import { UserStateService } from '../../blocks/services/user-state.service';

import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import { Feature, FieldDefinition, MetaDataType } from 'shared-code-lib';
import { WorfklowDesignerProperties, Workflow } from '../../blocks/models/workflow';
import { Deliver, IWorkflowStep } from '../../blocks/models/workflow-step';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { Step, StepCategory } from './step';
import { WorkflowEditorComponent } from './workflow-editor/workflow-editor.component';

@Component({
  selector: 'app-admin-workflow',
  templateUrl: './admin-workflow.component.html',
  styleUrls: ['./admin-workflow.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminWorkflowComponent implements OnInit, AfterViewInit, OnDestroy {
  saveButton: Control;
  cancelButton: Control;
  formControls: Array<Control> = [];
  fieldName: FieldDefinition;
  fieldDescription: FieldDefinition;
  workflow: Workflow;
  workflowSteps: Array<IWorkflowStep> = [];
  toolbars: { [key: number]: mxToolbar } = {};
  graph: mxGraph;

  workflowRoutingSheetsEnabled: boolean;

  routeParams: Params;
  routeData: Data;

  @ViewChild(WorkflowEditorComponent, { static: true })
  workflowEditor: WorkflowEditorComponent;
  @ViewChild('processActionstoolbarContainer', { static: true })
  processToolbarContainer: ElementRef;
  @ViewChild('exportActionstoolbarContainer', { static: true })
  exportToolbarContainer: ElementRef;
  @ViewChild(PerfectScrollbarDirective, { static: true })
  directiveRef?: PerfectScrollbarDirective;

  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private router: Router,
    private hydraApi: HydraApiService,
    private route: ActivatedRoute,
    private toastr: ToastrService,
    public errorsService: ErrorsService,
    private spinner: SpinnerStateService,
    private userState: UserStateService
  ) {
    mxToolbar.prototype.addMode = function (title: string, element: HTMLElement, selectFunc: (evt, cell) => {}) {
      if (this.enabled) {
        mxEvent.addListener(
          element,
          'click',
          mxUtils.bind(this, function (evt) {
            this.selectMode(element, selectFunc);
            this.noReset = false;
          })
        );

        mxEvent.addListener(
          element,
          'dblclick',
          mxUtils.bind(this, function (evt) {
            this.selectMode(element, selectFunc);
            this.noReset = true;
          })
        );

        if (this.defaultMode == null) {
          this.defaultMode = element;
          this.defaultFunction = selectFunc;
          this.selectMode(element, selectFunc);
        }
      }

      (this.container as HTMLElement).appendChild(element);
      return element;
    };
  }

  ngOnInit() {
    this.workflowRoutingSheetsEnabled = this.userState.hasFeature(Feature.WorkflowStepRoutingSheet);

    this.workflow = new Workflow(
      '',
      '',
      '',
      [],
      [],
      '',
      new Date(),
      new Date(),
      new WorfklowDesignerProperties({
        translateX: 0,
        translateY: 0,
      }),
      false,
      [],
      []
    );

    this.createButtons();
    this.createFields();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        this.loadWorkflow();
      })
    );
  }

  ngAfterViewInit() {
    this.toolbars[StepCategory.Process] = new mxToolbar(this.processToolbarContainer.nativeElement);
    this.toolbars[StepCategory.Export] = new mxToolbar(this.exportToolbarContainer.nativeElement);
    Object.values(this.toolbars).forEach((toolbar: mxToolbar) => (toolbar.enabled = false));
    this.graph = this.workflowEditor.graph;
    this.hydraApi.getWorkflowSteps(this.workflowStepsLoaded, this.workflowStepsLoadFailed);
  }

  private workflowStepsLoadFailed = () => {
    //todo: toastr
  };

  private workflowStepsLoaded = (steps: Array<IWorkflowStep>) => {
    this.workflowSteps = steps;
    this.buildToolbar();
  };

  buildToolbar = (): void => {
    for (let ws of this.workflowSteps) {
      const step = Step.getStepFor(ws.stepType);
      step.workflowStep = ws;

      this.addToToolbar(step, `assets/img/workflow-editor/${step.stepType}.svg`);
    }
  };

  addToToolbar = (step: Step, icon): void => {
    let toolbar;
    if (step.workflowStep instanceof Deliver) {
      toolbar = this.toolbars[StepCategory.Export];
    } else {
      toolbar = this.toolbars[StepCategory.Process];
    }
    var img = this.addToolbarItem(step, this.graph, toolbar, icon);
    img.enabled = true;
  };

  addToolbarItem = (step: Step, graph: mxGraph, toolbar: mxToolbar, image) => {
    // Function that is executed when the image is dropped on
    // the graph. The cell argument points to the cell under
    // the mousepointer if there is one.

    var that = this;
    var onItemDrop = function (graph: mxGraph, evt, cell: mxCell, x, y) {
      graph.stopEditing(false);

      if (!(step instanceof Step)) return;

      const ws = step.workflowStep.clone();
      ws.generateId();

      const vertex = that.workflowEditor.createVertexForStep(ws);
      vertex.geometry.x = x;
      vertex.geometry.y = y;
      that.workflowEditor.addWorkflowStepVertex(vertex, ws);
      that.workflowEditor.connectVertexStep(ws);

      graph.setSelectionCell(vertex);

      that.workflow.workflowSteps.push(ws);
    };

    // Creates the image which is used as the drag icon (preview)
    const iconContainer = document.createElement('div');
    iconContainer.className = 'mxToolbarMode';
    iconContainer.title = this.translate
      .instant('admin-workflow.toolbar-step-tooltip')
      .replace('$step$', step.getCellName(this.translate));
    (iconContainer as any).initialClassName = iconContainer.className;

    const iconImage = document.createElement('img');
    iconImage.className = 'mx-icon-image';
    iconImage.src = image;
    iconImage.width = 30;
    iconImage.height = 30;
    iconContainer.appendChild(iconImage);

    const iconTitle = document.createElement('div');
    iconTitle.className = 'mx-icon-title';
    iconTitle.innerText = step.getCellName(this.translate);
    iconContainer.appendChild(iconTitle);

    var img = toolbar.addMode(step.getCellName(this.translate), iconContainer, function (evt, cell) {
      var pt = this.graph.getPointForEvent(evt);
      onItemDrop(graph, evt, cell, pt.x, pt.y);
    });

    // Disables dragging if element is disabled. This is a workaround
    // for wrong event order in IE. Following is a dummy listener that
    // is invoked as the last listener in IE.
    mxEvent.addListener(img, 'mousedown', function (evt) {
      // do nothing
    });

    // This listener is always called first before any other listener
    // in all browsers.
    mxEvent.addListener(img, 'mousedown', function (evt) {
      if (img.enabled == false) {
        mxEvent.consume(evt);
      }
    });

    mxUtils.makeDraggable(img, graph, onItemDrop);

    return img;
  };

  onSave = () => {
    if (this.workflow.id) {
      this.hydraApi.updateWorkflow(this.workflow, this.workflowSaved, this.workflowSaveFailed);
    } else {
      this.hydraApi.createWorkflow(this.workflow, this.workflowSaved, this.workflowSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  normalizeGraphY = () => {
    const minY = this.workflow.workflowSteps
      .map((step) => step.designerProperties.y)
      .reduce((min, y) => (y < min ? y : min), Number.POSITIVE_INFINITY);
    this.workflow.workflowSteps.forEach((step) => {
      if (minY < 0) {
        step.designerProperties.y += minY * -1;
      }
    });
  };

  workflowSaved = (result: ApiResult) => {
    if (result.success) {
      const workflow = new Workflow(
        result.object.id,
        result.object.tenantId,
        result.object.name,
        result.object.workflowSteps,
        result.object.scanSettings,
        result.object.description,
        result.object.dateCreatedUTC,
        result.object.dateModifiedUTC,
        result.object.designerProperties,
        result.object.allowRoutingSheetGeneration,
        result.object.rolesAllowedToSubmitFrom,
        result.object.prompts
      );

      this.workflowFullySaved(workflow);
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-workflow.validation-errors');
    }

    this.spinner.hide();
  };

  workflowFullySaved = (workflow: Workflow): void => {
    this.toastr.success(this.translate.instant('admin-workflow.workflow-saved'));
    this.returnToGrid();
  };

  workflowSaveFailed = (err: any) => {
    this.toastr.error(this.translate.instant('admin-workflow.workflow-save-failed'));
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'settings/BambaSettingsApplication/BambaSettingsApplication-Upland.Bamba.Application.Settings.Workflows'
    ); // todo - handle this "better"
  };

  private workflowLoaded = (w: Workflow) => {
    if (this.isDuplicate()) {
      w.name = `${this.translate.instant('Admin.CopyOf')} ${w.name}`;
      w.id = '';
    } else if (this.routeData.type == 'create') {
      w.id = '';
    }

    this.workflow = w;
  };

  private workflowLoadFailed = () => {
    //todo: toastr
  };

  private loadWorkflow = () => {
    if (this.routeData.type == 'properties' || this.isDuplicate()) {
      this.hydraApi.getWorkflow(this.routeParams.workflowid, this.workflowLoaded, this.workflowLoadFailed);
    } else if (this.routeData.type == 'create') {
      this.hydraApi.getWorkflowTemplate(
        this.routeParams.workflowtemplate,
        this.workflowLoaded,
        this.workflowLoadFailed
      );
    }
  };

  private createFields = () => {
    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-workflow.name'),
      placeholder: this.translate.instant('admin-workflow.name-placeholder'),
      required: true,
    });

    this.fieldDescription = new FieldDefinition({
      type: MetaDataType.LargeText,
      label: this.translate.instant('admin-workflow.description'),
      placeholder: this.translate.instant('admin-workflow.description-placeholder'),
    });
  };

  private createButtons() {
    this.saveButton = new Control(
      this.onSave,
      this.translate.instant('admin-workflow.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );
    this.cancelButton = new Control(
      this.onCancel,
      this.translate.instant('admin-workflow.cancel'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );
    this.formControls.push(this.saveButton);
    this.formControls.push(this.cancelButton);
  }

  private isDuplicate(): boolean {
    return this.routeData.type == 'duplicate';
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
