import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { MetaDataDefinition, MetaDataType } from 'shared-code-lib';
import { Workflow } from '../../../../blocks/models/workflow';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-step-interactive-groupings',
  templateUrl: './step-interactive-groupings.component.html',
  styleUrls: ['./step-interactive-groupings.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepInteractiveGroupingsComponent implements OnInit {
  @Input() itemGroupingIds: Array<string> = [];
  @Input() workflow: Workflow;
  @Output() itemGroupingIdsChange = new EventEmitter<Array<string>>();

  available: Array<MetaDataDefinition> = [];
  current: Array<MetaDataDefinition> = [];

  constructor(private userState: UserStateService) {}

  ngOnInit(): void {
    this.setupMetaData();
  }

  setupMetaData(): void {
    const mdds = this.userState.user.metaData;
    this.current = [];

    this.available = mdds.filter(
      (m) =>
        m.type != MetaDataType.LineItems &&
        m.type != MetaDataType.CascadingOptions &&
        m.allowedForWorkflow(this.workflow.id)
    );

    for (let id of this.itemGroupingIds) {
      const item = mdds.find((m) => m.id === id);
      if (!item.allowedForWorkflow(this.workflow.id)) continue;

      if (item) this.current.push(item);
    }
  }

  currentChanged(items: Array<MetaDataDefinition>): void {
    this.itemGroupingIds = items.map((item) => item.id);

    this.itemGroupingIdsChange.emit(this.itemGroupingIds);
  }
}
