import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EnumDataSource, MetaDataDefinition, MetaDataType, MetadataWithDocumentNameDatasource } from 'shared-code-lib';
import { FieldDefinition } from '../../../../../../../../Bamba.Shared/src/models/field-definition';
import { UserStateService } from '../../../../../app/blocks/services/user-state.service';
import {
  ExportMetaData,
  ExportMetaDataCSV,
  ExportMetaDataEncoding,
  ExportMetaDataType,
  ExportMetaDataXML,
} from '../../models/export-metadata';

@Component({
  selector: 'app-export-metadata',
  templateUrl: './export-metadata.component.html',
  styleUrls: ['./export-metadata.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class ExportMetaDataComponent implements OnInit {
  @Input() exportMetaData: ExportMetaData;
  @Input() workflowId: string;
  @Output() metaDataTypeChanged = new EventEmitter<any>();

  ExportType = ExportMetaDataType;
  EncodingType = ExportMetaDataEncoding;

  dataFields: Array<MetaDataDefinition> = [];
  availableDataFields: Array<MetaDataDefinition> = [];
  currentDataFields: Array<MetaDataDefinition> = [];

  fieldFilename: FieldDefinition;
  fieldEncoding: FieldDefinition;
  fieldType: FieldDefinition;

  get exportType(): string {
    return `${this.exportMetaData.type}`;
  }
  set exportType(val: string) {
    this.exportMetaData.type = Number(val);
  }

  get encodingType(): string {
    return `${this.exportMetaData.encodingType}`;
  }
  set encodingType(val: string) {
    this.exportMetaData.encodingType = Number(val);
  }

  get exportMetaDataCSV(): ExportMetaDataCSV {
    return this.exportMetaData as ExportMetaDataCSV;
  }

  get exportMetaDataXML(): ExportMetaDataXML {
    return this.exportMetaData as ExportMetaDataXML;
  }

  constructor(private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    this.createFields();
    this.setupMetaData();
  }

  createFields(): void {
    this.fieldFilename = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('export-metadata.filename'),
      placeholder: this.translate.instant('export-metadata.filename-placeholder'),
      substitutable: true,
      datasource: new MetadataWithDocumentNameDatasource(this.userState, this.translate),
    });

    this.fieldEncoding = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('export-metadata.encoding'),
      datasource: new EnumDataSource(ExportMetaDataEncoding, 'exportMetadataEncoding', this.translate),
    });

    this.fieldType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('export-metadata.type'),
      datasource: new EnumDataSource(ExportMetaDataType, 'exportMetadataType', this.translate),
    });
  }

  onExportTypeChanged(event: any): void {
    this.exportMetaData = ExportMetaData.create({ ...this.exportMetaData });
    this.metaDataTypeChanged.emit(this.exportMetaData);
    this.dataFieldsChanged();
  }

  onEncodingTypeChanged(event: any): void {
    if (this.exportMetaData.encodingType !== ExportMetaDataEncoding.ASCII) return;

    this.exportMetaData.includeBOM = false;
  }

  setupMetaData(): void {
    this.dataFields = this.userState.user.metaData;
    this.dataFieldsChanged();
  }

  dataFieldsChanged(): void {
    if (!this.dataFields || !this.exportMetaData) return;

    this.availableDataFields = this.dataFields.filter(this.filterAllowedMetaData);

    this.exportMetaData.metaDataFields = this.exportMetaData.metaDataFields.filter((id) =>
      this.availableDataFields.some((mdd) => mdd.id === id)
    );

    this.currentDataFields = this.availableDataFields.filter((mdd) =>
      this.exportMetaData.metaDataFields.some((id) => mdd.id === id)
    );
  }

  filterAllowedMetaData = (mdd: MetaDataDefinition): boolean => {
    if (mdd == null || !mdd.allowedForWorkflow(this.workflowId)) return false;

    if (mdd.type === MetaDataType.CascadingOptions) return false;

    if (
      this.exportMetaData.type === ExportMetaDataType.CSV &&
      [MetaDataType.LineItems, MetaDataType.LargeText].includes(mdd.type)
    ) {
      return false;
    }

    return true;
  };

  currentDataFieldsChanged = (items: Array<MetaDataDefinition>): void => {
    this.exportMetaData.metaDataFields = items.map((item) => item.id);
  };
}
