import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import {
  DeliveryFormatsDataSource,
  FieldDefinition,
  MetadataIntellisenseDatasource,
  MetaDataType,
} from 'shared-code-lib';
import { EmailDestination, EmailDestinationType } from '../../../../blocks/models/email-destination';
import { DeliverToEmailWorkflowStep } from '../../../../blocks/models/workflow-step';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-step-deliver-to-email-general',
  templateUrl: './step-deliver-to-email-general.component.html',
  styleUrls: ['./step-deliver-to-email-general.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliverToEmailGeneralComponent implements OnInit {
  @Input() deliverToEmail: DeliverToEmailWorkflowStep;

  fieldEmailAddress: FieldDefinition;
  fieldSubject: FieldDefinition;
  fieldBody: FieldDefinition;
  fieldDeliveryFormat: FieldDefinition;
  fieldDocumentName: FieldDefinition;

  constructor(private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    if (!this.deliverToEmail.destinations.length) {
      this.deliverToEmail.destinations.push(new EmailDestination('', '', EmailDestinationType.To));
    }

    this.createFields();
    this.onIncludeDocumentChanged();
  }

  createFields = (): void => {
    this.fieldEmailAddress = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-deliver-to-email.email-address'),
      placeholder: this.translate.instant('step-deliver-to-email.email-address-placeholder'),
      required: true,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userState),
    });

    this.fieldSubject = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-deliver-to-email.subject'),
      placeholder: this.translate.instant('step-deliver-to-email.subject-placeholder'),
      required: false,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userState),
    });

    this.fieldBody = new FieldDefinition({
      type: MetaDataType.LargeText,
      label: this.translate.instant('step-deliver-to-email.body'),
      placeholder: this.translate.instant('step-deliver-to-email.body-placeholder'),
      required: false,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userState),
    });

    this.fieldDeliveryFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('workflow-step.delivery-format'),
      placeholder: '',
      datasource: new DeliveryFormatsDataSource(this.translate, this.userState),
      required: true,
    });

    this.fieldDocumentName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-deliver-to-email.document-name'),
      placeholder: this.translate.instant('step-deliver-to-email.document-name-placeholder'),
      required: false,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userState),
    });
  };

  onIncludeDocumentChanged = (): void => {
    this.fieldBody.required = !this.deliverToEmail.includeDocument;
  };
}
