import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { Tab } from 'shared-code-lib';
import { DeliverToFileBoundWorkflowStep } from '../../../../app/blocks/models/workflow-step';
import { ExportMetaData } from '../models/export-metadata';

@Component({
  selector: 'app-step-delivery-filebound',
  templateUrl: './step-delivery-filebound.component.html',
  styleUrls: ['./step-delivery-filebound.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliveryFileboundComponent implements OnInit {
  @Input() deliverToFileBound: DeliverToFileBoundWorkflowStep;
  @Input() workflowId: string;

  activeTab = 'general';
  tabs = new Array<Tab>();

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.tabs.push(new Tab('general', this.translate.instant('step-deliver.general')));
    this.tabs.push(new Tab('export-metadata', this.translate.instant('step-deliver.export-metadata')));
  }

  activateTab = (view: string): void => {
    this.activeTab = view;
  };

  onMetaDataTypeChanged(exportMetaData: ExportMetaData): void {
    this.deliverToFileBound.exportMetaData = exportMetaData;
  }
}
