import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { Tab } from '../../../../../../../Bamba.Shared/src/form/tab-bar/tab-bar.component';
import { DeliverToEmailWorkflowStep } from '../../../../app/blocks/models/workflow-step';
import { ExportMetaData } from '../models/export-metadata';

@Component({
  selector: 'app-step-deliver-to-email',
  templateUrl: './step-deliver-to-email.component.html',
  styleUrls: ['./step-deliver-to-email.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliverToEmailComponent implements OnInit {
  @Input() deliverToEmail: DeliverToEmailWorkflowStep;
  @Input() workflowId: string;

  activeTab = 'general';
  tabs = new Array<Tab>();

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.tabs.push(new Tab('general', this.translate.instant('step-deliver.general')));
    this.tabs.push(new Tab('export-metadata', this.translate.instant('step-deliver.export-metadata')));
  }

  activateTab = (view: string): void => {
    this.activeTab = view;
  };

  onMetaDataTypeChanged(exportMetaData: ExportMetaData): void {
    this.deliverToEmail.exportMetaData = exportMetaData;
  }
}
