import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, IFieldDataSource, MetadataIntellisenseDatasource, MetaDataType } from 'shared-code-lib';
import { DetectSignatureWorkflowStep } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-detect-signature',
  templateUrl: './step-detect-signature.component.html',
  styleUrls: ['./step-detect-signature.component.scss'],
})
export class StepDetectSignatureComponent implements OnInit {
  @Input() step: Step;

  detectSignatureStep: DetectSignatureWorkflowStep;
  confidenceThresholdField: FieldDefinition;

  get confidenceThreshold() {
    return `${this.detectSignatureStep.confidenceThreshold}`;
  }
  set confidenceThreshold(val: string) {
    this.detectSignatureStep.confidenceThreshold = Number(val);
  }

  constructor(private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    this.detectSignatureStep = this.step.workflowStep as DetectSignatureWorkflowStep;
    this.createFields();
  }

  createFields = (): void => {
    this.confidenceThresholdField = this.createField(
      'confidenceThreshold',
      'confidenceThreshold',
      MetaDataType.Integer,
      true
    );
  };

  createField = (
    label: string = null,
    placeholder: string = '',
    type: MetaDataType = MetaDataType.Text,
    required: boolean = false,
    substitutable: boolean = true,
    datasource: IFieldDataSource = null
  ): FieldDefinition => {
    return new FieldDefinition({
      type: type,
      label: label ? this.translate.instant(`step-detect-signature.${label}`) : '',
      placeholder: placeholder ? this.translate.instant(`step-detect-signature.${placeholder}`) : '',
      required: required,
      datasource: substitutable ? new MetadataIntellisenseDatasource(this.userState) : datasource,
      substitutable: substitutable,
    });
  };
}
