import { Component, Input, ViewEncapsulation } from '@angular/core';
import { Step } from '../step';
import { OCRPlusWorkflowStep, WorkflowStep } from '../../../blocks/models/workflow-step';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-step-ocrplus',
  templateUrl: './step-ocrplus.component.html',
  styleUrl: './step-ocrplus.component.scss',
  encapsulation: ViewEncapsulation.None,
})
export class StepOcrPlusComponent {
  @Input() step: Step;

  ocrWorkflowStep: OCRPlusWorkflowStep;

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.ocrWorkflowStep = <OCRPlusWorkflowStep>this.step.workflowStep;
  }
}
