import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  ElementRef,
  HostListener,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription } from 'rxjs';
import { BrowserDetectorService, MouseClick } from 'shared-code-lib';
import { Action, ActionEvent } from '../../../blocks/core/actions';
import { KeyboardActionMapping } from '../../../blocks/core/keyboardActionMapping';
import { Log } from '../../../blocks/core/log';
import { Failed, Interactive, Outcome, Succeeded } from '../../../blocks/models/outcome';
import { Workflow } from '../../../blocks/models/workflow';
import {
  CompletedFailedWorkflowStep,
  CompletedWorkflowStep,
  InteractiveWorkflowStep,
  IWorkflowStep,
  StartWorkflowStep,
  WorkflowStep,
} from '../../../blocks/models/workflow-step';
import {
  WorkflowStepCostType,
  WorkflowStepPageLicenseCost,
} from '../../../blocks/models/workflow-step-page-license-cost';
import { ActionService } from '../../../blocks/services/action.service';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { ModalsUtilitiesService } from '../../../blocks/services/modals-utilities.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { Step } from '../step';

declare const $: any;

@Component({
  selector: 'app-workflow-editor',
  templateUrl: './workflow-editor.component.html',
  styleUrls: ['./workflow-editor.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class WorkflowEditorComponent implements OnInit, AfterViewInit, OnDestroy, OnChanges {
  @ViewChild('graphContainer', { static: true })
  graphContainer: ElementRef;
  @ViewChild('graphWrapper', { static: true })
  graphWrapper: ElementRef;
  @ViewChild('stepPropertiesModal', { static: true })
  stepPropertiesModalElement: ElementRef;
  @ViewChild('modalBody', { static: true })
  modalBody: ElementRef;

  @Input() workflow: Workflow;
  private _onSave = new Subject<void>();
  onSave = this._onSave.asObservable();

  subscriptions: Subscription;
  stepPropertiesModal: NgxSmartModalComponent;
  selectionHandler: mxSelectionCellsHandler;

  public graph: mxGraph;
  done: mxCell;

  portMap = {};
  vertexMap = {};

  zoomInTitle: string;
  zoomOutTitle: string;

  isLoading: boolean;

  readonly min_zoom = 0.2;
  readonly max_zoom = 5;
  readonly graphMinWidth = 500;
  readonly graphMinHeight = 500;
  readonly graphMaxWidth = 5000;
  readonly graphMaxHeight = 5000;
  private loadingGraph: boolean;
  cellHasProperties: boolean;
  cellCanBeDeleted: boolean;
  contextMenuActive = false;
  contextMenuPosition: {
    top: number;
    left: number;
  };

  showConnectionLabels = true;
  stepPropertiesModalTitle: string;
  stepPropertiesModalCustomClasses: string;

  baseCellsMoved: (
    cells: mxCell[],
    dx: number,
    dy: number,
    disconnect: boolean,
    constrain: boolean,
    extend: boolean
  ) => {};
  baseViewStateChanged: () => {};

  stepsCost: Array<WorkflowStepPageLicenseCost> = [];

  lastSelectedStep: Step;

  constructor(
    public ngxSmartModalService: NgxSmartModalService,
    public translate: TranslateService,
    public hydraApi: HydraApiService,
    private toastr: ToastrService,
    private cdr: ChangeDetectorRef,
    private actionService: ActionService,
    private errorsService: ErrorsService,
    private spinnerState: SpinnerStateService,
    private browserDetector: BrowserDetectorService,
    private modalsUtilitiesService: ModalsUtilitiesService
  ) {
    this.subscriptions = new Subscription();

    mxUtils.getStyleString = (style) => Object.keys(style).reduce((acc, key) => `${acc}${key}=${style[key]};`, '');
  }

  ngOnInit() {
    this.subscriptions.add(this.actionService.onActivateAction.subscribe(this.invokeAction));

    this.zoomInTitle = this.getActionTitle('Scanning.ZoomIn', Action.ZoomIn);
    this.zoomOutTitle = this.getActionTitle('Scanning.ZoomOut', Action.ZoomOut);

    this.hydraApi.getWorkflowStepsCosts(this.onStepsCostLoaded, this.onStepsCostLoadFailed);
  }

  onStepsCostLoaded = (workflowStepsCost: Array<WorkflowStepPageLicenseCost>): void => {
    this.stepsCost = workflowStepsCost;
  };

  onStepsCostLoadFailed = (err: any): void => {
    Log.error('WorkflowEditorComponent.onStepsCostLoadFailed', err);
  };

  get zoomInDisabled() {
    return this.graph.view.getScale() >= this.max_zoom;
  }

  get zoomOutDisabled() {
    return this.graph.view.getScale() <= this.min_zoom;
  }

  ngOnChanges(changes: SimpleChanges) {
    this.loadGraph();
  }

  ngAfterViewInit() {
    mxVertexHandler.prototype.createSelectionShape = function (bounds) {
      const shape = new mxRectangleShape(bounds, null, this.getSelectionColor());
      shape.isRounded = true;
      const { width, height } = bounds;
      shape.style = {
        // since 15% rounded edges works for square cells
        // multiply by width to height ratio for cells with height < width (start, done)
        arcSize: (width / height) * 15,
      };
      shape.strokewidth = 4;
      shape.isDashed = this.isSelectionDashed();

      return shape;
    };

    this.loadGraph();

    this.stepPropertiesModal = this.ngxSmartModalService.getModal('stepPropertiesModal');
    this.subscriptions.add(
      this.stepPropertiesModal.onClose.subscribe(() => {
        this.hideLoading();
      })
    );

    $('.context-menu').bind('contextmenu', function (e: Event) {
      e.preventDefault();
    });
  }

  invokeAction = (event: ActionEvent): void => {
    if (event.action === Action.DeleteSelectedItem || event.action === Action.CtrlDeleteSelectedItem) {
      this.invokeDeleteKeyAction();
    } else if (event.action == Action.ZoomIn) {
      this.zoomIn();
    } else if (event.action == Action.ZoomOut) {
      this.zoomOut();
    }
  };

  invokeDeleteKeyAction() {
    if (this.modalsUtilitiesService.isAnyModalOpened()) return;

    const selectedStepsCount = {};
    const errors = [];

    for (const cell of this.graph.getSelectionCells()) {
      if (!cell.vertex || !(cell.data instanceof Step)) continue;

      const step = <Step>cell.data;
      selectedStepsCount[step.stepType] = selectedStepsCount[step.stepType] + 1 || 1;

      if (step.workflowStep.requiredForWorkflow()) {
        if (this.getCellCount(step.stepType) === selectedStepsCount[step.stepType]) {
          errors.push(
            this.translate.instant('admin-workflow.step-cannot-deleted').replace('{stepname}', step.workflowStep.name)
          );
        }
      }
    }

    if (errors.length) {
      this.errorsService.showErrorsModal(errors, 'Common.ActionFailed');
      return;
    }

    this.showItemDeleteModal();
  }

  showItemDeleteModal() {
    this.ngxSmartModalService.setModalData(
      {
        title: 'MainHeader.DeleteConfirmation',
        text: this.translate.instant('admin-workflow.cell-delete-confirm-text'),
        buttonText: this.translate.instant('Common.Delete'),
        confirm: this.confirmedCellDelete,
        cancel: this.cancelCellDelete,
      },
      'actionConfirmModal',
      true
    );

    this.ngxSmartModalService.getModal('actionConfirmModal').open();
  }

  confirmedCellDelete = () => {
    this.ngxSmartModalService.getModal('actionConfirmModal').close();
    this.deleteSelectedCell();
  };

  cancelCellDelete = () => {
    this.ngxSmartModalService.getModal('actionConfirmModal').close();
  };

  deleteSelectedCell = (): void => {
    for (const cell of this.graph.getSelectionCells()) {
      if (cell.vertex) {
        if (!(cell.data instanceof Step)) {
          continue;
        }

        const step = <Step>cell.data;

        this.workflow.workflowSteps = this.workflow.workflowSteps.filter((ws) => ws.id !== step.workflowStep.id);

        for (const wf of this.workflow.workflowSteps) {
          if (!(wf instanceof InteractiveWorkflowStep)) continue;

          const iws = wf as InteractiveWorkflowStep;
          if (!iws.formTraining.enabled) continue;

          if (iws.formTraining.classifyStepId == step.workflowStep.id) {
            iws.formTraining.enabled = false;
          }
        }
      } else if (cell.edge) {
        if (cell.source == null || !(cell.source.value instanceof Outcome)) {
          continue;
        }

        const outcome = <Outcome>cell.source.value;
        outcome.nextId = '';
      }
    }

    this.graph.removeCells(this.graph.getSelectionCells(), true);
  };

  createGraphEditor = (): void => {
    if (this.graph) {
      return;
    }

    mxGraphView.prototype.optimizeVmlReflows = false;
    mxConnectionHandler.prototype.movePreviewAway = false;
    mxGraphHandler.prototype.htmlPreview = true;
    mxConnectionHandler.prototype.moveIconFront = true;

    mxEvent.disableContextMenu(this.graphContainer.nativeElement);

    const model = new mxGraphModel();
    this.graph = new mxGraph(this.graphContainer.nativeElement, model);
    const rubberband = new mxRubberband(this.graph);

    this.graph.setConnectable(true);
    this.graph.setMultigraph(true);
    this.graph.graphHandler.scrollOnMove = false;
    this.graph.maximumGraphBounds = new mxRectangle(0, 0, this.graphMaxWidth, this.graphMaxHeight);
    this.graph.minimumGraphSize = new mxRectangle(0, 0, this.graphMinWidth, this.graphMinHeight);
    this.graph.keepEdgesInBackground = true;

    this.graph.dblClick = this.onComponentProperties;
    this.graph.convertValueToString = this.getCellLabel;
    this.graph.click = this.onClick;
    this.baseCellsMoved = this.graph.cellsMoved.bind(this.graph);
    this.graph.cellsMoved = this.onCellsMoved;
    this.graph.addListener(mxEvent.ADD, (cell: mxCell) => {
      const highlight = new mxCellHighlight(this.graph, '#ff0000', 2, false);
      highlight.highlight(this.graph.view.getState(cell));
    });

    this.baseViewStateChanged = mxGraphView.prototype.viewStateChanged.bind(this.graph.view);
    this.graph.view.viewStateChanged = this.updateTranslate.bind(this);

    this.graph.setTooltips(false);
    this.handleCustomTooltip();

    this.graph.getTooltipForCell = this.getTooltipForCell;

    this.graph.allowLoops = false;
    this.graph.setCellsEditable(false);
    this.graph.setCellsDisconnectable(true);
    this.graph.setHtmlLabels(true);
    this.graph.setConnectable(true);
    this.graph.setAllowDanglingEdges(false);
    this.graph.centerZoom = false;
    this.graph.edgeLabelsMovable = false;
    this.graph.expandedImage = '';

    this.graph.addListener('cellConnected', this.onConnectCell);

    this.graph.popupMenuHandler.factoryMethod = (menu, cell, evt) => menu;
  };

  handleCustomTooltip = (): void => {
    const that = this;
    this.graph.addMouseListener({
      mouseDown: function (sender, event) {},
      mouseMove: function (sender, event) {
        if (!that.graph?.tooltipHandler) return;
        if (!event.sourceState?.cell || event.evt.srcElement.nodeName !== 'ellipse') {
          that.graph.tooltipHandler.hide();
          return;
        }
        const tooltipText = that.getTooltipForCell(event.sourceState.cell);
        if (!tooltipText) return;
        that.graph.tooltipHandler.show(tooltipText, event.evt.clientX, event.evt.clientY);
      },
      mouseUp: function (sender, event) {},
    });
  };

  getTooltipForCell = (cell: mxCell): string => {
    if (cell.value == null) {
      return '';
    }

    if (cell.edge) {
      if (cell.source.data instanceof Outcome && cell.target.data instanceof WorkflowStep) {
        const source = this.findWorkflowNameForOutcome(<Outcome>cell.source.data);
        const target =
          cell.target.data instanceof Interactive
            ? (<Interactive>cell.target.value).graphLabel
            : (<WorkflowStep>cell.target.value).name;

        return `${this.getOutcomeLabel(cell.data)}<br>${source} -> ${target}`;
      }

      return cell.value;
    } else {
      if (cell.data instanceof Step) {
        return (<Step>cell.data).getCellName(this.translate);
      } else if (typeof cell.data === 'string') {
        return cell.data;
      } else if (cell.data instanceof Outcome) {
        return this.getOutcomeLabel(<Outcome>cell.data);
      } else if (cell.data instanceof WorkflowStep) {
        return (<WorkflowStep>cell.data).name;
      } else if (cell.value instanceof Outcome) {
        return this.getOutcomeLabel(<Outcome>cell.value);
      } else if (cell.value instanceof WorkflowStep) {
        return (<WorkflowStep>cell.value).name;
      }
    }

    return '';
  };

  findWorkflowNameForOutcome = (o: Outcome): string => {
    for (const ws of this.workflow.workflowSteps) {
      for (const outcome of ws.outcomes) {
        if (outcome.id === o.id) {
          return ws.name;
        }
      }
    }

    return '';
  };

  getCellLabel = (cell: mxCell): string => {
    if (cell.value == null) {
      return '';
    }

    if (cell.value instanceof Step) {
      return (<Step>cell.value).getCellName(this.translate);
    } else if (typeof cell.value === 'string') {
      return cell.value;
    }

    return '';
  };

  zoomIn = (): void => {
    this.graph.zoomIn();
  };

  zoomOut = (): void => {
    this.graph.zoomOut();
  };

  onComponentProperties = (evt, cell: mxCell): void => {
    if (!cell || !cell.data || !(cell.data instanceof Step)) return;

    const step: Step = cell.data;
    if (!step.workflowStep.supportsUIProperties()) return;

    this.ngxSmartModalService.resetModalData('stepPropertiesModal');

    this.stepPropertiesModal.open();
    this.cdr.detectChanges();

    this.ngxSmartModalService.setModalData(
      {
        cell: cell,
        step: step.clone(),
        workflow: this.workflow.clone(),
        workflowId: this.workflow.id,
        cost: this.getStepCost(step.stepType),
      },
      'stepPropertiesModal',
      true
    );

    this.stepPropertiesModalTitle = `${(
      this.stepPropertiesModal.getData() as any
    ).step.getCellName()}  ${this.translate.instant('admin-workflow.properties-for')}`;

    this.stepPropertiesModalCustomClasses = `modal-content workflow-step-properties-modal ${step.stepType.toLocaleLowerCase()}-properties`;
  };

  private getStepCost = (stepType: string): string => {
    const cost = this.stepsCost.find((x) => x.workflowStepClassName.toLocaleLowerCase() == stepType);
    if (!cost) return;

    if (cost.baseCostValue === 0) return; 

    if (cost.baseCostType == WorkflowStepCostType.Constant) return cost.baseCostValue.toString();

    return ` ${this.translate.instant('admin-workflow-step-cost.additional')} ${
      cost.baseCostValue
    } ${this.translate.instant('admin-workflow-step-cost.page-count')}`;
  };

  onClick = (e: mxMouseEvent) => {
    if (document && document.activeElement && (document.activeElement as any).blur) {
      (document.activeElement as any).blur();
    }

    if (e.evt.which === MouseClick.Right) {
      this.onHandleRightClick(e);
    }
  };

  onCellsMoved = (cells: mxCell[], dx, dy, disconnect, constrain, extend) => {
    this.baseCellsMoved(cells, dx, dy, disconnect, constrain, extend);

    const cell = cells[0];
    const designerPropeties = cell.data.workflowStep.designerProperties;
    designerPropeties.x = cell.geometry.x;
    designerPropeties.y = cell.geometry.y;

    cell.children.forEach((child) => {
      if (!child.edges) return;

      child.edges.forEach((edge: mxCell) => {
        if (edge.source === child) {
          child.removeEdge(edge, false);
        } else {
          child.removeEdge(edge, true);
        }
      });
    });

    this.workflow.workflowSteps.forEach(this.connectVertexStep.bind(this));
  };

  onHandleRightClick = (e: mxMouseEvent) => {
    this.contextMenuActive = false;
    const cell = e.getCell();
    if (!cell || (cell.vertex && !cell.data)) {
      return;
    }

    this.cellCanBeDeleted = true;
    this.cellHasProperties = true;
    if (!(cell.data instanceof Step)) {
      this.cellHasProperties = false;
    } else {
      const step = <Step>cell.data;
      if (!step.workflowStep.supportsUIProperties()) {
        this.cellHasProperties = false;
      }

      if (step.workflowStep.requiredForWorkflow()) {
        if (this.getCellCount(step.stepType) < 2) {
          this.cellCanBeDeleted = false;
        }
      }
    }

    if (this.cellCanBeDeleted || this.cellHasProperties) {
      const graphWrapperElement = this.graphWrapper.nativeElement as HTMLElement;
      const elementRect = graphWrapperElement.getBoundingClientRect();
      this.contextMenuPosition = {
        top: e.evt.pageY + graphWrapperElement.scrollTop - elementRect.top,
        left: e.evt.pageX + graphWrapperElement.scrollLeft - elementRect.left,
      };
      this.contextMenuActive = true;
    }
  };

  @HostListener('document:click')
  disableContextMenu() {
    this.contextMenuActive = false;
  }

  getCellCount(type: string): number {
    const cells = this.graph.getChildCells(this.graph.getDefaultParent(), true, true);
    let stepCount = 0;

    for (let i = 0; i < cells.length; i++) {
      if (cells[i].data instanceof Step) {
        const step = <Step>cells[i].data;
        if (step.stepType === type) {
          stepCount++;
        }
      }
    }

    return stepCount;
  }

  openPropertiesModal() {
    this.onComponentProperties(null, this.graph.getSelectionCell());
  }

  cancelProperties = (): void => {
    this.stepPropertiesModal.close();
    this.hideLoading();
  };

  saveProperties = (): void => {
    this._onSave.next();
    this.stepPropertiesModal.close();

    const data = this.ngxSmartModalService.getModalData('stepPropertiesModal') as any;
    if (!data) {
      return;
    }

    const cell = <mxCell>data.cell;
    if (!cell) {
      return;
    }

    const step = <Step>data.step;
    if (!step) {
      return;
    }

    this.workflow.workflowSteps = this.workflow.workflowSteps.map((ws) =>
      ws.id === step.workflowStep.id ? step.workflowStep : ws
    );

    this.workflow.scanSettings = data.workflow.scanSettings;
    this.workflow.prompts = data.workflow.prompts;

    this.loadGraph();
  };

  onConnectCell = (graph, event): void => {
    if (this.loadingGraph) {
      return;
    }

    const edge: mxCell = event.properties.edge;
    if (!edge.edge) {
      return;
    }

    if (edge.source == null || edge.target == null) {
      return;
    }

    if (!(edge.source.value instanceof Outcome) || !(edge.target.value instanceof WorkflowStep)) {
      this.graph.removeCells([edge], true);
      this.toastr.error(this.translate.instant('admin-workflow.invalid-connection'));
      return;
    }

    const sourceOutcome = <Outcome>edge.source.value;
    const targetWorkflowStep = <WorkflowStep>edge.target.value;

    sourceOutcome.nextId = targetWorkflowStep.id;

    edge.value = this.getOutcomeHtmlLabel(sourceOutcome);
    edge.data = sourceOutcome;

    if (edge.source.edges.length === 1) {
      return;
    }

    const edgesToDeleted = [];
    for (let i = 0; i < edge.source.edges.length - 1; i++) {
      edgesToDeleted.push(edge.source.edges[i]);
    }
    this.graph.removeCells(edgesToDeleted, true);
  };

  loadGraph = (): void => {
    this.createGraphEditor();

    const graph = this.graph;

    graph.removeCells(graph.getChildVertices(graph.getDefaultParent()), true);

    const styleSheet = graph.getStylesheet();
    const style = styleSheet.getDefaultEdgeStyle();
    style.strokeWidth = 3;
    style.edgeStyle = 'elbowEdgeStyle';
    style.elbow = 'vertical';
    style.orthogonal = '1';

    graph.connectionHandler.createEdgeState = function (me) {
      // style needs to be a string...
      var edge = graph.createEdge(null, null, null, null, null, mxUtils.getStyleString(style));

      return new mxCellState(this.graph.view, edge, this.graph.getCellStyle(edge));
    };

    graph.getModel().beginUpdate();

    try {
      this.loadingGraph = true;

      this.vertexMap = {};
      this.portMap = {};

      this.buildGraphFromSteps(this.workflow.workflowSteps);

      const { translateX, translateY } = this.workflow.designerProperties;
      this.graph.view.setTranslate(translateX, translateY);
    } finally {
      graph.getModel().endUpdate();
      this.loadingGraph = false;
    }
  };

  buildGraphFromSteps = (steps: IWorkflowStep[]): void => {
    steps.forEach((step) => {
      const vertex = this.createVertexForStep(step);
      this.addWorkflowStepVertex(vertex, step);
    });
    steps.forEach(this.connectVertexStep.bind(this));
  };

  createVertexForStep = (ws: IWorkflowStep): mxCell => {
    const label = this.createCellLabel(ws);
    const { x, y } = ws.designerProperties;
    let cellGeometry = new mxGeometry(x, y, 100, 100);
    let cellStyle = 'resizable=0';
    if (
      ws instanceof StartWorkflowStep ||
      ws instanceof CompletedWorkflowStep ||
      ws instanceof CompletedFailedWorkflowStep
    ) {
      cellGeometry = new mxGeometry(x, y, 150, 40);
      cellStyle += ';strokeOpacity=0;fillOpacity=0';
    }
    return new mxCell(label, cellGeometry, cellStyle);
  };

  addWorkflowStepVertex = (vertex: mxCell, ws: IWorkflowStep): void => {
    const graph = this.graph;

    const step = Step.getStepFor(ws.stepType);
    if (step) step.workflowStep = ws; //todo: this indicates a real problem....

    vertex.setVertex(true);
    vertex.setConnectable(false);
    vertex.data = step;
    const designerProperties = step.workflowStep.designerProperties;
    designerProperties.x = vertex.geometry.x;
    designerProperties.y = vertex.geometry.y;

    graph.addCell(vertex);

    graph.setCellStyles('strokeColor', '#D3D3D3', [vertex]);
    graph.setCellStyles('fillColor', '#EAEAEA', [vertex]);
    graph.setCellStyles('strokeWidth', '3', [vertex]);
    graph.setCellStyles('fontColor', 'white', [vertex]);
    graph.setCellStyles('shape', 'rectangle', [vertex]);
    graph.setCellStyles('whiteSpace', 'wrap', [vertex]);
    graph.setCellStyles('rounded', '1', [vertex]);

    this.vertexMap[ws.id] = vertex;

    if (ws instanceof StartWorkflowStep) {
      return null;
    }

    const port = (this.portMap[ws.id] = graph.insertVertex(vertex, 'input', ws, 0.5, 0.0, 8, 8, 'port', true));
    port.data = ws;

    port.connectable = true;
    port.geometry.offset = new mxPoint(-4, -8);

    graph.setCellStyles('shape', 'ellipse', [port]);
    graph.setCellStyles('strokeColor', '#0099cc', [port]);
    graph.setCellStyles('fillColor', '#0099cc', [port]);

    if (ws instanceof CompletedWorkflowStep || ws instanceof CompletedFailedWorkflowStep) this.done = port;
  };

  getOutcomeHtmlLabel = (outcome: Outcome): string => {
    if (!this.showConnectionLabels) return '';

    return `<div class="edge-label">${this.getOutcomeLabel(outcome)}</div>`;
  };

  getOutcomeLabel = (outcome: Outcome): string => {
    const localization = `outcome-labels.${outcome.graphLabel().toLowerCase()}`;

    const localized = this.translate.instant(localization);
    if (localized !== localization) {
      return localized;
    }

    return outcome.graphLabel();
  };

  connectVertexStep = (step: IWorkflowStep): void => {
    const graph = this.graph;
    const start = this.vertexMap[step.id];

    const offset = 1.0 / (step.outcomes.length + 1);
    let index = 1;

    for (const outcome of step.outcomes) {
      if (!outcome.enabled) {
        continue;
      }

      const x = offset * index;
      const port = graph.insertVertex(start, this.getOutcomeLabel(outcome), outcome, x, 1.0, 8, 8, 'port;', true);
      port.data = outcome;

      if (step instanceof StartWorkflowStep) {
        port.geometry.offset = new mxPoint(-4, -2);
      } else {
        port.geometry.offset = new mxPoint(-4, 0);
      }

      graph.setCellStyles('shape', 'ellipse', [port]);

      let edge = null;
      if (this.portMap[outcome.nextId]) {
        edge = graph.insertEdge(
          graph.getDefaultParent(),
          '',
          this.getOutcomeHtmlLabel(outcome),
          port,
          this.portMap[outcome.nextId]
        );
        edge.data = outcome;
      }

      let color = '#003399';
      if (outcome instanceof Failed) {
        color = '#ff8080';
      } else if (outcome instanceof Succeeded) {
        color = '#339933';
      } else {
        const colors = ['#003cb3', '#004de6', '#1a66ff', '#4d88ff', '#80aaff', '#ccddff'];
        color = colors[index - 1];
      }

      if (color) {
        if (edge) {
          graph.setCellStyles('strokeColor', '#8e8c8d', [edge]);
          graph.setCellStyles('bendable', '0', [edge]);
        }

        graph.setCellStyles('strokeColor', color, [port]);
        graph.setCellStyles('fillColor', color, [port]);
      }

      index++;
    }
  };

  createCellLabel = (ws: IWorkflowStep): string => {
    const isStart = ws instanceof StartWorkflowStep;
    const isComplete = ws instanceof CompletedWorkflowStep;
    const isFailed = ws instanceof CompletedFailedWorkflowStep;

    let bgClass = '';
    let containerClass = isStart || isComplete || isFailed ? 'terminal-cell-label' : 'cell-label';

    let isIE = this.browserDetector.isIE();
    if (isIE && isIE <= 11) {
      containerClass += isStart || isComplete || isFailed ? ' label-ie' : '';
    }

    if (isStart) {
      bgClass = 'start-step';
    } else if (isComplete) {
      bgClass = 'completed-success-step';
    } else if (isFailed) {
      bgClass = 'completed-failed-step';
    }

    return (
      `<div class="${containerClass}" id="workflow-step-${ws.name}">` +
      '<div class="icon-container">' +
      `<img src="assets/img/workflow-editor/${ws.stepType}.svg" />` +
      '</div>' +
      `<div class="title ${bgClass}">${ws.name}</div>` +
      '</div>'
    );
  };

  updateTranslate() {
    this.baseViewStateChanged();

    if (!this.workflow) {
      return;
    }

    const { x, y } = this.graph.view.getTranslate();
    this.workflow.designerProperties.translateX = x;
    this.workflow.designerProperties.translateY = y;
  }

  showLoading(key: string) {
    this.isLoading = true;
    this.spinnerState.show(this.translate.instant(key), this.modalBody.nativeElement);
  }

  hideLoading() {
    this.spinnerState.hide();
    this.isLoading = false;
  }

  ngOnDestroy() {
    this.subscriptions.unsubscribe();
    this.hideLoading();
  }

  getActionTitle(translateResource: string, action: Action): string {
    const title = this.translate.instant(translateResource);
    const keyboardTooltip = KeyboardActionMapping.getTooltipFor(action, this.translate);

    return `${title}${keyboardTooltip}`;
  }

  toggleConnectionLabels() {
    this.showConnectionLabels = !this.showConnectionLabels;
    this.loadGraph();
  }
}
