import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { TranslateService } from '@ngx-translate/core';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import {
  DelayService,
  DeliveryFormatsDataSource,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataType,
  SplittableDeliveryFormatsDataSource,
} from 'shared-code-lib';
import { Log } from '../../../../blocks/core/log';
import { DeliverToNetDocsWorkflowStep, DeliveryTypes } from '../../../../blocks/models/workflow-step';
import { ProxyApiService } from '../../../../blocks/services/proxy-api.service';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../../blocks/services/user-state.service';

interface IVault extends IFieldOptions {
  clientId: string;
  server: string;
}

@Component({
  selector: 'app-step-delivery-to-netdocs-general',
  templateUrl: './step-delivery-to-netdocs-general.component.html',
  styleUrls: ['./step-delivery-to-netdocs-general.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliveryToNetdocsGeneralComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() deliverToNetDocs: DeliverToNetDocsWorkflowStep;

  authorizing = false;
  lastAuthorizationUrl: SafeResourceUrl;
  authorizationUrl: SafeResourceUrl;

  fieldVault: FieldDefinition;
  fieldDeliveryFormat: FieldDefinition;
  fieldDeliveryFormatSplittable: FieldDefinition;
  fieldRefreshToken: FieldDefinition;
  fieldNetDocsObjectId: FieldDefinition;

  deliveryFormats?: Record<DeliveryTypes, IFieldOptions[]>;

  authorizationButtonLabel: string;

  vaults: IVault[] = [
    { name: 'US', value: '0', clientId: 'AP-JAOJ6H5S', server: 'vault.netvoyage.com' },
    { name: 'EU', value: '1', clientId: 'AP-2Z9HYIS9', server: 'eu.netdocuments.com' },
    { name: 'AU', value: '2', clientId: 'AP-GHC3ZQT2', server: 'au.netdocuments.com' },
  ];

  private propertiesModal: HTMLElement;
  private propertiesModalBody: HTMLElement;
  private authorizationModal: NgxSmartModalComponent;

  DeliveryTypes = DeliveryTypes;

  constructor(
    private translateService: TranslateService,
    private userStateService: UserStateService,
    private delayService: DelayService,
    private sanitizer: DomSanitizer,
    private proxyApiService: ProxyApiService,
    private spinnerService: SpinnerStateService,
    private ngxSmartModal: NgxSmartModalService
  ) {}

  ngOnInit(): void {
    this.createFields();
    this.onVaultSelected(this.deliverToNetDocs.vault);

    this.updateAuthorizationButtonLabel();
  }

  ngAfterViewInit(): void {
    this.authorizationModal = this.ngxSmartModal.get('authorizationModal');
    this.authorizationModal.onClose.subscribe(() => {
      this.cancelAuthorizing();
    });
  }

  ngOnDestroy(): void {
    this.authorizationModal.onClose.unsubscribe();
  }

  onDeliveryTypeChanged = (type: DeliveryTypes): void => {
    this.deliverToNetDocs.deliveryType = type;

    this.resetSelectedFormatIfRequired(type);
  };

  resetSelectedFormatIfRequired = (newType: DeliveryTypes) => {
    const formatExistsInNewType = this.deliveryFormats[newType].some(
      (format) => format.value === this.deliverToNetDocs.finalFormCode
    );
    if (formatExistsInNewType) return;

    this.deliverToNetDocs.finalFormCode = this.deliveryFormats[newType][0].value;
  };

  createFields = (): void => {
    this.fieldRefreshToken = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('step-delivery-to-netdocs.refresh-token'),
      placeholder: this.translateService.instant('step-delivery-to-netdocs.place-holder-refresh-token'),
      required: true,
    });

    this.fieldVault = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('step-delivery-to-netdocs.vault'),
      placeholder: '',
      datasource: new FixedDataSource(this.vaults),

      required: true,
    });

    let deliveryFormatsDataSource = new DeliveryFormatsDataSource(this.translateService, this.userStateService);
    let splittableFormatsSource = new SplittableDeliveryFormatsDataSource(this.translateService, this.userStateService);

    this.deliveryFormats = {
      [DeliveryTypes.ViaRoutingSheet]: splittableFormatsSource.getItemsEx(),
      [DeliveryTypes.ToObjectID]: deliveryFormatsDataSource.getItemsEx(),
    };

    this.fieldDeliveryFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('workflow-step.delivery-format'),
      placeholder: '',
      datasource: deliveryFormatsDataSource,
      required: true,
    });

    this.fieldDeliveryFormatSplittable = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('workflow-step.delivery-format'),
      placeholder: '',
      datasource: splittableFormatsSource,
      required: true,
    });

    this.fieldNetDocsObjectId = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: this.translateService.instant('step-delivery-to-netdocs.netdocs-objectid-placeholder'),
      required: false,
    });
  };

  onVaultSelected = (value: string): void => {
    Log.enter('StepDeliveryToNetdocsComponent.onVaultSelected', value);

    const vault = this.vaults.find((v) => v.value == value);

    this.authorizationUrl = this.sanitizer.bypassSecurityTrustResourceUrl(
      `https://${vault.server}/neWeb2/OAuth.aspx?client_id=${vault.clientId}&scope=full&response_type=code&redirect_uri=${window.location.origin}/netdocumentscode`
    );

    Log.var(this.authorizationUrl);
  };

  onAuthorize = (): void => {
    this.authorizing = true;
    this.propertiesModal = document.querySelector('.workflow-step-properties-modal');
    this.propertiesModalBody = document.querySelector('.properties-modal-body');
    this.propertiesModal.classList.add('has-iframe');
    this.authorizationModal.open();
    this.delayService.execute(() => {
      const iframe = document.getElementById('netdocs-iframe') as any;
      iframe.style.height = this.propertiesModalBody.offsetHeight - 15 + 'px';
    }, 10);
  };

  dispatchChange = (): void => {
    Log.enter('dispatchChange');

    try {
      const iframe = document.getElementById('netdocs-iframe') as any;
      const newHref = iframe.contentWindow.location.href;

      Log.var(newHref);

      const url = new URL(newHref);
      const code = url.searchParams.get('code');
      const error = url.searchParams.get('error');

      if (code) {
        this.cancelAuthorizing();

        this.spinnerService.show(
          this.translateService.instant('step-delivery-to-netdocs.exchanging-code'),
          document.getElementsByClassName('nsm-content')[0] as HTMLElement
        );

        this.proxyApiService.netDocsExchangeCodeForRefreshToken(
          this.deliverToNetDocs.vault,
          code,
          this.exchangeSucceeded,
          this.exchangeFailed
        );
      } else if (error) {
        this.cancelAuthorizing();

        this.exchangeFailed(error);
      }
    } catch (e) {}
  };

  exchangeSucceeded = (token: string): void => {
    Log.enter('exchangeSucceeded', token);

    this.deliverToNetDocs.refreshToken = token;

    this.spinnerService.hide();

    this.updateAuthorizationButtonLabel();
  };

  exchangeFailed = (e): void => {
    Log.error('exchangeFailed', e);

    this.deliverToNetDocs.refreshToken = '';

    this.spinnerService.hide();

    this.updateAuthorizationButtonLabel();
  };

  unloadHandler = function () {
    setTimeout(this.dispatchChange, 0);
  };

  attachUnload() {
    try {
      const iframe = document.getElementById('netdocs-iframe') as any;

      iframe.contentWindow.removeEventListener('unload', this.unloadHandler);
      iframe.contentWindow.addEventListener('unload', this.unloadHandler);
    } catch (e) {}
  }

  onIframeLoad = (evt): void => {
    this.attachUnload();
    this.dispatchChange();
  };

  cancelAuthorizing = (): void => {
    this.propertiesModal.classList.remove('has-iframe');
    this.authorizationModal.close();
    this.authorizing = false;
  };

  updateAuthorizationButtonLabel = (): void => {
    this.authorizationButtonLabel = this.translateService.instant(
      !this.deliverToNetDocs.refreshToken
        ? 'step-delivery-to-netdocs.authorize'
        : 'step-delivery-to-netdocs.re-authorize'
    );
  };
}
