import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ImageProcessingStep } from '../../../blocks/models/workflow-step';
import { Step } from '../step';

@Component({
  selector: 'app-step-imageprocessing',
  templateUrl: './step-imageprocessing.component.html',
  styleUrls: ['./step-imageprocessing.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepImageprocessingComponent implements OnInit {
  @Input() step: Step;

  imageProcessingStep: ImageProcessingStep;

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.imageProcessingStep = <ImageProcessingStep>this.step.workflowStep;
  }
}
