import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { DelayService } from 'shared-code-lib';
import { IOutcome, Interactive, Outcome } from '../../../../blocks/models/outcome';

@Component({
  selector: 'app-step-interactive-outcomes',
  templateUrl: './step-interactive-outcomes.component.html',
  styleUrls: ['./step-interactive-outcomes.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepInteractiveOutcomesComponent implements OnInit {
  @Input() outcomes: Array<Outcome> = [];
  @Output() outcomesChange = new EventEmitter<Array<Outcome>>();

  sortable = false;
  constructor(private delay: DelayService) {}

  ngOnInit() {}

  onDeleteOutcome = (outcome: IOutcome): void => {
    this.outcomes = this.outcomes.filter((o) => o.id !== outcome.id);
    this.outcomesChange.emit(this.outcomes);
  };

  onOutcomeOrderChanged = (): void => {
    this.outcomesChange.emit(this.outcomes);
  };

  onAddOutcome = (): void => {
    this.outcomes.push(Interactive.create());
    this.outcomesChange.emit(this.outcomes);

    this.delay.execute(this.setFocusToAddedOutcome, 1);
  };

  setFocusToAddedOutcome = (): void => {
    const id = `workflow-outcome-${this.outcomes.length - 1}`;
    const lastOutcome = document.getElementById(id);
    if (lastOutcome) {
      lastOutcome.focus();
    }
  };

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.outcomes, event.previousIndex, event.currentIndex);
  }
}
