import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Tab } from 'shared-code-lib';
import { DeliverToFtpWorkflowStep } from '../../../../app/blocks/models/workflow-step';
import { ExportMetaData } from '../models/export-metadata';

@Component({
  selector: 'app-step-deliver-to-ftp',
  templateUrl: './step-deliver-to-ftp.component.html',
  styleUrls: ['./step-deliver-to-ftp.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliverToFtpComponent implements OnInit {
  @Input() deliverToFtpStep: DeliverToFtpWorkflowStep;
  @Input() workflowId: string;

  activeTab = 'general';
  tabs = new Array<Tab>();

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    this.tabs.push(new Tab('general', this.translate.instant('step-deliver.general')));
    this.tabs.push(new Tab('export-metadata', this.translate.instant('step-deliver.export-metadata')));
  }

  activateTab = (view: string): void => {
    this.activeTab = view;
  };

  onMetaDataTypeChanged(exportMetaData: ExportMetaData): void {
    this.deliverToFtpStep.exportMetaData = exportMetaData;
  }
}
