import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType, Tab } from 'shared-code-lib';
import { BarcodeDataMappingWorkflowStep } from '../../../blocks/models/workflow-step';
import { Step } from '../step';

@Component({
  selector: 'app-step-barcode-data-mapping',
  templateUrl: './step-barcode-data-mapping.component.html',
  styleUrls: ['./step-barcode-data-mapping.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepBarcodeDataMappingComponent implements OnInit {
  @Input() step: Step;

  fieldDisplayName: FieldDefinition;
  barcodeDataMappingStep: BarcodeDataMappingWorkflowStep;

  activeTab = 'barcode-mappings';
  tabs = new Array<Tab>();

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.barcodeDataMappingStep = <BarcodeDataMappingWorkflowStep>this.step.workflowStep;
    this.createFields();
    this.createTabs();
  }

  createFields() {
    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translate.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });
  }

  createTabs() {
    this.tabs.push(new Tab('barcode-mappings', this.translate.instant('step-interactive.barcode-mappings')));
  }

  activateTab(view: string) {
    this.activeTab = view;
  }
}
