import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition } from '../../../../../../../../../Bamba.Shared/src/models/field-definition';
import { EnumDataSource } from '../../../../../../../../../Bamba.Shared/src/models/fixed-datasource';
import { MetaDataType } from '../../../../../../../../../Bamba.Shared/src/models/metadata-definition';
import { ContentCriteria, ContentCriteriaType, TextContentCriteria } from '../../../../../blocks/models/workflow-step';

@Component({
  selector: 'app-content-criteria',
  templateUrl: './content-criteria.component.html',
  styleUrls: ['./content-criteria.component.scss'],
})
export class ContentCriteriaComponent implements OnInit {
  @Input() contentCriteria: ContentCriteria;
  @Input() classificationIndex: number;
  @Input() index: number;
  @Input() disableDeletion: boolean;
  @Output() onDeleteContentCriteria = new EventEmitter();
  @Output() onUpdateType = new EventEmitter();

  contentCriteriaTypeField: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.createFields();
  }

  createFields() {
    this.contentCriteriaTypeField = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      datasource: new EnumDataSource(ContentCriteriaType, 'ContentCriteriaTypeEnum', this.translateService),
      required: true,
    });
  }

  deleteContentCriteria() {
    if (this.disableDeletion) return;
    this.onDeleteContentCriteria.emit(this.index);
  }

  updateSelectedFieldType(value: string) {
    this.onUpdateType.emit(value);
  }

  isTextContentCriteria(contentCriteria: ContentCriteria) {
    return (
      contentCriteria instanceof TextContentCriteria ||
      contentCriteria.contentCriteriaType === 'RegexContentCriteria' ||
      contentCriteria.contentCriteriaType === 'LiteralContentCriteria' ||
      contentCriteria.contentCriteriaType === 'WildcardContentCriteria'
    );
  }

  getTextContentCriteria(contentCriteria: ContentCriteria) {
    return contentCriteria as TextContentCriteria;
  }
}
