import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataDefinition, MetaDataType } from 'shared-code-lib';
import { FormsAIWorkflowStep, InteractiveWorkflowStep, IWorkflowStep } from '../../../../blocks/models/workflow-step';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-step-interactive-training',
  templateUrl: './step-interactive-training.component.html',
  styleUrls: ['./step-interactive-training.component.scss'],
})
export class StepInteractiveTrainingComponent implements OnInit {
  @Input() classifySteps: Array<IWorkflowStep> = [];
  @Input() workflowStep: InteractiveWorkflowStep;

  stepsDataSource = new FixedDataSource(new Array<IFieldOptions>());
  fieldClassifyStep: FieldDefinition;

  availableDataFields: Array<MetaDataDefinition> = [];
  currentDataFields: Array<MetaDataDefinition> = [];

  get isTrainingLocked(): boolean {
    return this.formsAiWorkflowStep?.lockTraining ?? false;
  }

  get stepLockedText(): string {
    return this.translate
      .instant('step-interactive.step-locked')
      .replace('{stepname}', this.formsAiWorkflowStep?.name ?? '');
  }

  get formsAiWorkflowStep(): FormsAIWorkflowStep {
    let id = this.workflowStep?.formTraining?.classifyStepId;
    return (this.classifySteps?.find((s) => s.id === id) as FormsAIWorkflowStep) ?? null;
  }

  constructor(private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    this.setupMetaData();
    this.createFields();

    if (
      this.classifySteps.length &&
      this.classifySteps.findIndex((s) => s.id === this.workflowStep.formTraining.classifyStepId) < 0
    ) {
      this.workflowStep.formTraining.classifyStepId = this.classifySteps[0].id;
    }
  }

  setupMetaData(): void {
    this.availableDataFields = this.userState.user.metaData.filter((m) => this.workflowStep.metaDataIds.includes(m.id));
    this.currentDataFields = this.availableDataFields.filter((f) =>
      this.workflowStep.formTraining.metaDataIds.includes(f.id)
    );
  }

  createFields(): void {
    this.classifySteps.forEach((s) => {
      this.stepsDataSource.options.push({
        name: s.name,
        value: s.id,
      });
    });

    this.fieldClassifyStep = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-interactive.training-classify-step'),
      placeholder: this.translate.instant('step-interactive.training-classify-step'),
      datasource: this.stepsDataSource,
      required: true,
    });
  }

  currentDataFieldChanged = (items: Array<MetaDataDefinition>) => {
    this.workflowStep.formTraining.metaDataIds = [];
    for (let item of items) {
      this.workflowStep.formTraining.metaDataIds.push(item.id);
    }
  };
}
