import { Component, OnInit, Input, Output, EventEmitter, ViewEncapsulation } from '@angular/core';
import { Capabilities } from '../../../document-capture/capture-models';
import { TranslateService } from '@ngx-translate/core';

class Capability {
  constructor(id: string, name: string) {
    this.id = id;
    this.name = name;
  }

  public id: string;
  public name: string;
}

@Component({
  selector: 'app-step-interactive-capabilities',
  templateUrl: './step-interactive-capabilities.component.html',
  styleUrls: ['./step-interactive-capabilities.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepInteractiveCapabilitiesComponent implements OnInit {
  @Input() capabilities: Array<string>;
  @Output() capabilitiesChange = new EventEmitter<Array<string>>();

  available: Array<Capability> = [];
  current: Array<Capability> = [];

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.loadAvailable();
    this.loadCurrent();
  }

  loadCurrent = (): void => {
    for (let capability of this.capabilities) {
      const c = this.available.find((a) => a.id == capability);
      if (c == null) continue;

      this.current.push(c);
    }
  };

  loadAvailable = (): void => {
    this.addAvailable(Capabilities.allowCaptureImport);
    this.addAvailable(Capabilities.allowCaptureScan);
    this.addAvailable(Capabilities.deletePage);
    this.addAvailable(Capabilities.deleteDocument);
    this.addAvailable(Capabilities.allowDropDocument);
    this.addAvailable(Capabilities.allowDropDocumentPage);
    this.addAvailable(Capabilities.allowDropExternalDocument);
    this.addAvailable(Capabilities.allowCaptureRescanPage);
    this.addAvailable(Capabilities.allowCaptureScanMorePages);
    this.addAvailable(Capabilities.allowCaptureScanToFolder);
    this.addAvailable(Capabilities.allowCaptureReimportPage);
    this.addAvailable(Capabilities.allowCaptureImportMorePages);
    this.addAvailable(Capabilities.allowCaptureImportToFolder);
    this.addAvailable(Capabilities.allowAnnotations);
    this.available = this.available.sort((x, y) => (x.name === y.name ? 0 : x.name < y.name ? -1 : 1));
  };

  addAvailable = (capability: string): void => {
    this.available.push(new Capability(capability, this.translate.instant(`step-capabilities.${capability}`)));
  };

  currentChanged = (items: Array<Capability>) => {
    this.capabilities = [];

    for (let item of items) {
      this.capabilities.push(item.id);
    }

    this.capabilitiesChange.emit(this.capabilities);
  };
}
