import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { OCRWorkflowStep } from '../../../blocks/models/workflow-step';
import { Step } from '../step';

class OCRLanguage {
  public code: string;
  public displayName: string;

  constructor(code: string, displayName: string) {
    this.code = code;
    this.displayName = displayName;
  }
}

@Component({
  selector: 'app-step-ocr',
  templateUrl: './step-ocr.component.html',
  styleUrls: ['./step-ocr.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepOcrComponent implements OnInit {
  @Input() step: Step;

  ocrWorkflowStep: OCRWorkflowStep;

  available: Array<OCRLanguage> = [];
  current: Array<OCRLanguage> = [];

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.ocrWorkflowStep = <OCRWorkflowStep>this.step.workflowStep;

    this.addLanguages();

    console.log(this.ocrWorkflowStep.languageCodes);

    this.current = this.ocrWorkflowStep.languageCodes.map((lc) => this.available.find((a) => a.code == lc));
  }

  addLanguages = (): void => {
    this.addLanguage('zh-CN');
    this.addLanguage('en-US');
    this.addLanguage('fr-FR');
    this.addLanguage('de-DE');
    this.addLanguage('it-IT');
    this.addLanguage('pt-BR');
    this.addLanguage('es-ES');

    this.addLanguage('ar-SA');
    this.addLanguage('bg-BG');
    this.addLanguage('zh-TW');
    this.addLanguage('cs-CZ');
    this.addLanguage('da-DK');
    this.addLanguage('nl-NL');
    this.addLanguage('fi-FI');
    this.addLanguage('el-GR');
    this.addLanguage('he-IL');
    this.addLanguage('hu-HU');
    this.addLanguage('id-ID');
    this.addLanguage('ja-JP');
    this.addLanguage('no');
    this.addLanguage('pl-PL');
    this.addLanguage('pt-PT');
    this.addLanguage('ro-RO');
    this.addLanguage('ru-RU');
    this.addLanguage('sk-SK');
    this.addLanguage('sv-SE');
    this.addLanguage('th-TH');
    this.addLanguage('tr-TR');
    this.addLanguage('ko-KR');

    this.available = this.available.sort((x, y) => x.displayName.localeCompare(y.displayName));
  };

  addLanguage = (code: string): void => {
    this.available.push(new OCRLanguage(code, this.translateService.instant(`step-ocr.language-${code}`)));
  };

  currentChanged = (items: Array<OCRLanguage>): void => {
    this.ocrWorkflowStep.languageCodes = items.map((item) => item.code);

    console.log(this.ocrWorkflowStep.languageCodes);
  };
}
