import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  FieldDefinition,
  FixedDataSource,
  IFieldDataSource,
  IFieldOptions,
  MetadataIntellisenseDatasource,
  MetaDataType,
} from 'shared-code-lib';
import { SummarizationWorkflowStep } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-summarization',
  templateUrl: './step-summarization.component.html',
  styleUrls: ['./step-summarization.component.scss'],
})
export class StepSummarizationComponent implements OnInit {
  @Input() step: Step;

  summarizationStep: SummarizationWorkflowStep;
  maxWordsField: FieldDefinition;
  metaDataOptions: IFieldOptions[] = [];
  fieldDataField: FieldDefinition;

  get maxWords() {
    return `${this.summarizationStep.maxWords}`;
  }
  set maxWords(val: string) {
    this.summarizationStep.maxWords = Number(val);
  }

  constructor(private translator: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    this.summarizationStep = this.step.workflowStep as SummarizationWorkflowStep;

    this.setupMetaData();
    this.createFields();
  }

  setupMetaData(): void {
    this.metaDataOptions = this.userState.user.metaData
      .filter((metaData) => metaData.type !== MetaDataType.LineItems)
      .map((metaData) => ({ name: metaData.label, value: metaData.id }));

    this.metaDataOptions.unshift({
      name: this.translator.instant('step-summarization.summary'),
      value: '',
    });
  }

  createFields(): void {
    this.maxWordsField = this.createField('maxWords', 'maxWords', MetaDataType.Integer, true);
    this.fieldDataField = this.createField(
      'data-field-label',
      '',
      MetaDataType.List,
      true,
      false,
      new FixedDataSource(this.metaDataOptions)
    );
  }

  createField(
    label: string = null,
    placeholder: string = '',
    type: MetaDataType = MetaDataType.Text,
    required: boolean = false,
    substitutable: boolean = true,
    datasource: IFieldDataSource = null
  ): FieldDefinition {
    return new FieldDefinition({
      type: type,
      label: label ? this.translator.instant(`step-summarization.${label}`) : '',
      placeholder: placeholder ? this.translator.instant(`step-summarization.${placeholder}`) : '',
      required: required,
      datasource: substitutable ? new MetadataIntellisenseDatasource(this.userState) : datasource,
      substitutable: substitutable,
    });
  }
}
