import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { MetaDataDefinition } from 'shared-code-lib';
import { Workflow } from '../../../../blocks/models/workflow';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-step-interactive-indexing',
  templateUrl: './step-interactive-indexing.component.html',
  styleUrls: ['./step-interactive-indexing.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepInteractiveIndexingComponent implements OnInit {
  @Input() metadataIds: Array<string> = [];
  @Input() workflow: Workflow;
  @Output() metadataIdsChange = new EventEmitter<Array<string>>();

  available: Array<MetaDataDefinition> = [];
  current: Array<MetaDataDefinition> = [];

  constructor(private userState: UserStateService) {}

  ngOnInit(): void {
    this.setupMetaData();
  }

  setupMetaData(): void {
    const mdds = this.userState.user.metaData;
    this.current = [];

    this.available = mdds.filter((i) => i.allowedForWorkflow(this.workflow.id));

    for (let id of this.metadataIds) {
      const item = mdds.find((m) => m.id === id);
      if (!item.allowedForWorkflow(this.workflow.id)) continue;

      if (item) this.current.push(item);
    }
  }

  currentChanged(items: Array<MetaDataDefinition>): void {
    this.metadataIds = items.map((item) => item.id);

    this.metadataIdsChange.emit(this.metadataIds);
  }
}
