import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { cloneDeep } from 'lodash';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType, ObjectIdGenerator } from 'shared-code-lib';
import { FormsAIWorkflowStep } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-forms-ai',
  templateUrl: './step-forms-ai.component.html',
  styleUrls: ['./step-forms-ai.component.scss'],
})
export class StepFormsAiComponent implements OnInit {
  @Input() step: Step;

  formsWorkflowStep: FormsAIWorkflowStep;

  fieldFormType: FieldDefinition;
  fieldFormTypeVariant: FieldDefinition;
  fieldsDataSource = new FixedDataSource(new Array<IFieldOptions>());
  fieldsVariantsDataSource = new FixedDataSource(new Array<IFieldOptions>());

  fieldDisplayName: FieldDefinition;
  fieldMaxDocsClassification: FieldDefinition;
  fieldMaxDocsFieldTraining: FieldDefinition;
  dataFieldsLoaded = false;

  get isVarianceSupported(): boolean {
    return this.formsWorkflowStep.projectVersion >= 3;
  }
  get maxDocsClassification() {
    return `${this.formsWorkflowStep.maxDocumentsUsedForClassification}`;
  }
  set maxDocsClassification(val: string) {
    this.formsWorkflowStep.maxDocumentsUsedForClassification = Number(val);
  }
  get maxDocsFieldTraining() {
    return `${this.formsWorkflowStep.maxDocumentsUsedForFieldTraining}`;
  }
  set maxDocsFieldTraining(val: string) {
    this.formsWorkflowStep.maxDocumentsUsedForFieldTraining = Number(val);
  }

  constructor(private translateService: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    this.formsWorkflowStep = <FormsAIWorkflowStep>this.step.workflowStep;

    this.setupMetaData();
    this.createFields();
  }

  setupMetaData(): void {
    const mdds = cloneDeep(this.userState.user.metaData);
    mdds.sort((x, y) => x.name.localeCompare(y.name));

    this.fieldsVariantsDataSource.options.push({
      name: this.translateService.instant('Metadata-Form.none'),
      value: ObjectIdGenerator.Empty,
    });
    this.fieldsDataSource.options.push({
      name: this.translateService.instant('Metadata-Form.none'),
      value: ObjectIdGenerator.Empty,
    });

    mdds.forEach((f) => {
      const mdd = { name: f.name, value: f.id };
      this.fieldsDataSource.options.push(mdd);
      this.fieldsVariantsDataSource.options.push(mdd);
    });
  }

  createFields(): void {
    this.fieldFormType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('step-interactive.training-form-type-field'),
      placeholder: this.translateService.instant('step-interactive.training-form-type-field'),
      datasource: this.fieldsDataSource,
      required: true,
    });

    this.fieldFormTypeVariant = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('step-interactive.training-form-type-variant-field'),
      placeholder: this.translateService.instant('step-interactive.training-form-type-variant-field'),
      datasource: this.fieldsVariantsDataSource,
      required: true,
    });

    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translateService.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });

    this.fieldMaxDocsClassification = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('step-interactive.training-max-docs-classification'),
      placeholder: this.translateService.instant('step-interactive.training-max-docs-classification-placeholder'),
      allowNegativeNumbers: false,
      required: true,
    });

    this.fieldMaxDocsFieldTraining = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('step-interactive.training-max-docs-field-training'),
      placeholder: this.translateService.instant('step-interactive.training-max-docs-field-training-placeholder'),
      allowNegativeNumbers: false,
      required: true,
    });

    this.dataFieldsLoaded = true;
  }
}
