import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EnumDataSource, FieldDefinition, MetaDataType } from '../../../../../../../Bamba.Shared';
import { FileDropEvent } from '../../../blocks/directives/drop-zone.directive';
import { AddTemplateInsertPosition, AddTemplateWorkflowStep } from '../../../blocks/models/workflow-step';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-add-template',
  templateUrl: './step-add-template.component.html',
  styleUrls: ['./step-add-template.component.scss'],
})
export class StepAddTemplateComponent implements OnInit {
  @ViewChild('file', { static: true })
  fileInputRef: ElementRef;
  @Input() step: Step;

  fileInput: HTMLInputElement;
  addTemplateWorkflowStep: AddTemplateWorkflowStep;
  fieldPagePosition: FieldDefinition;
  fieldPageNumber: FieldDefinition;

  constructor(private translate: TranslateService, private hydraApi: HydraApiService) {}

  ngOnInit() {
    this.addTemplateWorkflowStep = this.step.workflowStep as AddTemplateWorkflowStep;
    this.fileInput = this.fileInputRef.nativeElement;
    this.createFields();
  }

  createFields = (): void => {
    this.fieldPagePosition = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-add-template.add-to-document'),
      required: true,
      datasource: new EnumDataSource(AddTemplateInsertPosition, 'step-add-template.insert-position', this.translate),
    });

    this.fieldPageNumber = new FieldDefinition({
      type: MetaDataType.Integer,
      allowNegativeNumbers: false,
      label: this.translate.instant('step-add-template.insert-at-page'),
      placeholder: this.translate.instant('step-add-template.insert-at-page'),
      required: true,
    });
  };

  onFileSelected = (evt: any): void => {
    if (this.fileInput.files.length == 0) return;
    this.readFile(this.fileInput.files[0]);
    this.addTemplateWorkflowStep.pathname = this.fileInput.files[0].name;
  };

  public onFilesDropped = (event: FileDropEvent) => {
    if (event.files.length == 0) return;
    this.readFile(event.files[0]);
    this.addTemplateWorkflowStep.pathname = event.files[0].name;
  };

  getTemplateFile = (): string => {
    if (this.addTemplateWorkflowStep.pathname) return this.addTemplateWorkflowStep.pathname;

    return this.translate.instant('step-add-template.no-template-selected');
  };

  readFile = (file: File): void => {
    this.addTemplateWorkflowStep.file = null;
    const reader = new FileReader();
    reader.onload = (event: any) => {
      this.addTemplateWorkflowStep.file = event.target.result.split(',')[1];
    };

    reader.readAsDataURL(file);
  };

  updatePagePosition(value: string) {
    this.addTemplateWorkflowStep.insertPosition = +value;
  }

  updatePageNumber(value: string) {
    this.addTemplateWorkflowStep.insertPageNumber = +value;
  }
}
