import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { Observable } from 'rxjs';
import { FieldDefinition, MetaDataType, MetadataWithDocumentNameDatasource } from 'shared-code-lib';
import {
  IRadioButtonOption,
  RadioButtonsLayout,
} from '../../../../../../../Bamba.Shared/src/models/radio-buttons-group';
import { RenameDocumentMode, RenameDocumentWorkflowStep } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-rename-document',
  templateUrl: './step-rename-document.component.html',
  styleUrls: ['./step-rename-document.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepRenameDocumentComponent implements OnInit {
  @ViewChild(PerfectScrollbarDirective, { static: true }) directiveRef?: PerfectScrollbarDirective;
  @Input() step: Step;
  @Input() onSave: Observable<void>;

  renameDocumentStep: RenameDocumentWorkflowStep;
  modeField: FieldDefinition;
  newNameField: FieldDefinition;
  modeOptions: IRadioButtonOption<string>[] = [];
  RenameDocumentMode = RenameDocumentMode;
  RadioButtonsLayout = RadioButtonsLayout;

  constructor(private translate: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    this.renameDocumentStep = <RenameDocumentWorkflowStep>this.step.workflowStep;

    this.createFields();

    for (let key in RenameDocumentMode) {
      if (!isNaN(Number(key))) continue;

      this.modeOptions.push({
        label: this.translate.instant(`step-rename-document.${key.toLocaleLowerCase()}`),
        value: RenameDocumentMode[key],
      });
    }
  }

  createFields = (): void => {
    this.newNameField = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-rename-document.new-name'),
      placeholder: '',
      required: true,
      substitutable: true,
      datasource: new MetadataWithDocumentNameDatasource(this.userState, this.translate),
    });
  };

  onModeChanged = (mode: RenameDocumentMode): RenameDocumentMode => (this.renameDocumentStep.mode = mode);
}
