import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetadataIntellisenseDatasource,
  MetaDataType,
} from 'shared-code-lib';
import { ClassifierWorkflowStep, FormsAIWorkflowStep } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-classifier',
  templateUrl: './step-classifier.component.html',
  styleUrls: ['./step-classifier.component.scss'],
})
export class StepClassifierComponent implements OnInit {
  @Input() step: Step;

  classifierStep: ClassifierWorkflowStep;
  formsWorkflowStep: FormsAIWorkflowStep;
  metaDataOptions: IFieldOptions[] = [];
  formTypeDataField: FieldDefinition;
  confidenceThresholdField: FieldDefinition;
  fieldDisplayName: FieldDefinition;
  splitDocumentNameField: FieldDefinition;

  get confidenceThreshold() {
    return `${this.classifierStep.confidenceThreshold}`;
  }
  set confidenceThreshold(val: string) {
    this.classifierStep.confidenceThreshold = Number(val);
  }

  constructor(private translateService: TranslateService, private userState: UserStateService) {}

  ngOnInit(): void {
    this.classifierStep = this.step.workflowStep as ClassifierWorkflowStep;

    this.setupMetaData();
    this.createFields();
  }

  setupMetaData(): void {
    this.metaDataOptions = this.userState.user.metaData
      .filter((metaData) => metaData.type !== MetaDataType.LineItems)
      .map((metaData) => ({ name: metaData.name, value: metaData.id }));

    this.metaDataOptions.unshift({
      name: this.translateService.instant('simple-classifier-step-properties.form-type'),
      value: '',
    });
  }

  createFields(): void {
    this.confidenceThresholdField = this.createField(
      MetaDataType.Integer,
      'confidence-threshold-label',
      'confidence-threshold-label',
      null,
      true,
      false
    );

    this.splitDocumentNameField = this.createField(
      MetaDataType.Text,
      'split-document-name',
      'split-document-name',
      new MetadataIntellisenseDatasource(this.userState),
      true,
      true
    );

    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translateService.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });

    this.formTypeDataField = this.createField(
      MetaDataType.List,
      'data-field-label',
      '',
      new FixedDataSource(this.metaDataOptions),
      true,
      false
    );
  }

  createField(
    type: MetaDataType = MetaDataType.Text,
    label: string = null,
    placeholder: string = '',
    datasource: FixedDataSource = null,
    required: boolean = false,
    substitutable: boolean = false
  ): FieldDefinition {
    return new FieldDefinition({
      type: type,
      label: label ? this.translateService.instant(`simple-classifier-step-properties.${label}`) : '',
      placeholder: placeholder ? this.translateService.instant(`simple-classifier-step-properties.${placeholder}`) : '',
      required: required,
      substitutable: substitutable,
      datasource: datasource,
    });
  }
}
