import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FileBoundFields, FileBoundProjectField } from '../../../../blocks/models/filebound';
import { UserStateService } from '../../../../blocks/services/user-state.service';
import { DocumentFieldMapping } from '../../models/document-field-mapping';

import {
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  IMetaData,
  MetaDataCollection,
  MetadataIntellisenseDatasource,
  MetaDataType,
  MetaDataValidity,
  MetaDataValueType,
  Tab,
} from 'shared-code-lib';

@Component({
  selector: 'app-step-metadata-mapping',
  templateUrl: './step-metadata-mapping.component.html',
  styleUrls: ['./step-metadata-mapping.component.scss'],
})
export class StepMetaDataMappingComponent implements OnInit, OnChanges {
  @Input() documentFields: Array<DocumentFieldMapping> = [];
  @Input() metaDataFields: Array<FileBoundProjectField> = [];
  @Input() lineitemFields: Array<FileBoundProjectField> = [];
  @Input() lineItemMappings: MetaDataCollection;
  @Input() lineItemMetaDataId: string;
  @Output() lineItemMetaDataIdChange = new EventEmitter<string>();
  @Input() mappings: MetaDataCollection;
  @Input() metaDataHeaderText: string;

  fieldLineItems: FieldDefinition;
  documentFieldDefinitions = new Array<FieldDefinition>();

  activeTab = 'metadata';
  tabs = new Array<Tab>();

  constructor(private userService: UserStateService, private translateService: TranslateService) {}

  ngOnInit() {
    this.createFields();
    this.createTabs();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.lineitemFields) {
      this.createTabs();
    }
  }

  createTabs = (): void => {
    this.tabs = [];
    this.tabs.push(new Tab('metadata', this.translateService.instant('step-delivery-filebound.metadata-mappings')));

    if (this.lineitemFields && this.lineitemFields.length > 0) {
      this.tabs.push(new Tab('lineitems', this.translateService.instant('step-delivery-filebound.lineitem-mappings')));
    }
  };

  createFields() {
    for (const field of this.documentFields) {
      const fieldDefinition = new FieldDefinition({
        type: MetaDataType.Text,
        label: '',
        placeholder: '',
        required: false,
        substitutable: true,
        datasource: new MetadataIntellisenseDatasource(this.userService),
      });

      this.documentFieldDefinitions.push(fieldDefinition);
    }

    if (this.lineitemFields) {
      const lineItemMetaData = new Array<IFieldOptions>();
      lineItemMetaData.push({ name: this.translateService.instant('admin-workflow.not-mapped'), value: '' });

      for (const mdd of this.userService.user.metaData) {
        if (mdd.type != MetaDataType.LineItems) continue;

        lineItemMetaData.push({ name: mdd.name, value: mdd.id });
      }

      this.fieldLineItems = new FieldDefinition({
        type: MetaDataType.List,
        label: '',
        placeholder: '',
        required: false,
        datasource: new FixedDataSource(lineItemMetaData),
      });
    }
  }

  getDocumentFieldValue = (field: DocumentFieldMapping): string => {
    return this.getMetaData(field.fieldName);
  };

  onDocumentFieldChanged = (field: DocumentFieldMapping, value: string): void => {
    this.mappings.set(field.fieldName, value, MetaDataValidity.valid);
  };

  getMetaData = (name: string, mappings: MetaDataCollection = null): string => {
    if (!mappings) mappings = this.mappings;

    const md: IMetaData = mappings.get(name);
    if (!md) return '';

    if (md.valueType == MetaDataValueType.String) return <string>md.value;

    return '';
  };

  autoMapFields = (): void => {
    if (this.metaDataFields) {
      for (const field of this.metaDataFields) {
        if (this.getMetaData(field.name)) continue;

        const mapping = this.autoMapField(field.name);
        if (mapping) this.mappings.set(field.name, mapping, MetaDataValidity.valid);
      }
    }

    if (this.lineitemFields) {
      for (const field of this.lineitemFields) {
        if (this.getMetaData(field.name, this.lineItemMappings)) continue;

        const mapping = this.autoMapField(field.name);
        if (mapping) this.lineItemMappings.set(field.name, mapping, MetaDataValidity.valid);
      }
    }
  };

  autoMapField = (name: string): string => {
    if (this.userService.findMetaDataByName(name)) return `%${name}%`;

    const nameNoSpaces = name.replace(' ', '');
    if (this.userService.findMetaDataByName(nameNoSpaces)) return `%${nameNoSpaces}%`;

    if (name == FileBoundFields.FileBoundDivider && this.userService.findMetaDataByName('divider')) return '%divider%';
    if (name == FileBoundFields.FileBoundSeparator && this.userService.findMetaDataByName('separator'))
      return '%separator%';

    return '';
  };

  getLineItemsFieldValue = (field: FileBoundProjectField): string => {
    return this.getMetaData(field.name, this.lineItemMappings);
  };

  onLineItemsFieldChanged = (value: string, field: FileBoundProjectField): void => {
    this.lineItemMappings.set(field.name, value, MetaDataValidity.valid);
  };

  onLineItemsChanged = (value: string): void => {
    this.lineItemMetaDataId = value;
    this.lineItemMetaDataIdChange.emit(this.lineItemMetaDataId);
  };

  getFieldValue = (field: FileBoundProjectField): string => {
    return this.getMetaData(field.name);
  };

  onFieldChanged = (value: string, field: FileBoundProjectField): void => {
    this.mappings.set(field.name, value, MetaDataValidity.valid);
  };

  getFieldDefinition(): FieldDefinition {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: '',
      required: false,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userService),
    });
  }

  activateTab = (tab: string): void => {
    this.activeTab = tab;
  };
}
