import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

import { EnumDataSource, FieldDefinition, MetadataIntellisenseDatasource, MetaDataType } from 'shared-code-lib';
import { DeliverToInterFAXWorkflowStep, Rendering, Resolution, Scaling } from '../../../blocks/models/workflow-step';
import { ProxyApiService } from '../../../blocks/services/proxy-api.service';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-deliver-to-interfax',
  templateUrl: './step-deliver-to-interfax.component.html',
  styleUrls: ['./step-deliver-to-interfax.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliverToInterFAXComponent implements OnInit {
  @Input() step: Step;
  @Input() modalElement;

  fieldUsername: FieldDefinition;
  fieldPassword: FieldDefinition;
  fieldFaxNumber: FieldDefinition;
  fieldCSID: FieldDefinition;
  fieldTopLine: FieldDefinition;
  fieldRetryCount: FieldDefinition;
  fieldScaling: FieldDefinition;
  fieldResolution: FieldDefinition;
  fieldRendering: FieldDefinition;

  deliverToInterFAX: DeliverToInterFAXWorkflowStep;

  get deliverToInterFAXRetryCount() {
    return `${this.deliverToInterFAX.retryCount}`;
  }
  set deliverToInterFAXRetryCount(val: string) {
    this.deliverToInterFAX.retryCount = Number(val);
  }
  get deliverToInterFAXScaling() {
    return `${this.deliverToInterFAX.scaling}`;
  }
  set deliverToInterFAXScaling(val: string) {
    this.deliverToInterFAX.scaling = Number(val);
  }
  get deliverToInterFAXResolution() {
    return `${this.deliverToInterFAX.resolution}`;
  }
  set deliverToInterFAXResolution(val: string) {
    this.deliverToInterFAX.resolution = Number(val);
  }
  get deliverToInterFAXRendering() {
    return `${this.deliverToInterFAX.rendering}`;
  }
  set deliverToInterFAXRendering(val: string) {
    this.deliverToInterFAX.rendering = Number(val);
  }

  constructor(
    private translate: TranslateService,
    private proxy: ProxyApiService,
    private spinner: SpinnerStateService,
    private toastr: ToastrService,
    private userStateService: UserStateService
  ) {}

  ngOnInit() {
    this.deliverToInterFAX = <DeliverToInterFAXWorkflowStep>this.step.workflowStep;

    this.createFields();
  }

  createFields = () => {
    this.fieldUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-deliver-to-interfax.username'),
      placeholder: this.translate.instant('step-deliver-to-interfax.username-placeholder'),
      required: true,
    });

    this.fieldFaxNumber = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-deliver-to-interfax.fax-number'),
      placeholder: this.translate.instant('step-deliver-to-interfax.fax-number-placeholder'),
      required: true,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userStateService),
    });

    this.fieldPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translate.instant('step-deliver-to-interfax.password'),
      placeholder: this.translate.instant('step-deliver-to-interfax.password-placeholder'),
    });

    this.fieldCSID = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-deliver-to-interfax.csid'),
      placeholder: this.translate.instant('step-deliver-to-interfax.csid-placeholder'),
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userStateService),
    });

    this.fieldTopLine = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-deliver-to-interfax.top-line'),
      placeholder: this.translate.instant('step-deliver-to-interfax.top-line-placeholder'),
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userStateService),
    });

    this.fieldRetryCount = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translate.instant('step-deliver-to-interfax.retry-count'),
      placeholder: this.translate.instant('step-deliver-to-interfax.retry-count-placeholder'),
      required: true,
    });

    this.fieldScaling = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-deliver-to-interfax.scaling'),
      datasource: new EnumDataSource(Scaling, 'ScalingEnum', this.translate),
      required: true,
    });

    this.fieldResolution = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-deliver-to-interfax.resolution'),
      datasource: new EnumDataSource(Resolution, 'ResolutionEnum', this.translate),
      required: true,
    });

    this.fieldRendering = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-deliver-to-interfax.rendering'),
      datasource: new EnumDataSource(Rendering, 'RenderingEnum', this.translate),
      required: true,
    });
  };

  checkInterFAXConnectivity() {
    this.spinner.show(
      this.translate.instant('step-deliver-to-interfax.checking-connectivity'),
      this.modalElement.nsmContent.nativeElement
    );

    this.proxy.checkInterFAXConnectivity(
      this.deliverToInterFAX.username,
      this.deliverToInterFAX.password,
      () => {
        this.spinner.hide();
        this.toastr.success(this.translate.instant('step-deliver-to-interfax.connectivity-succeeded'));
      },
      (err) => {
        this.spinner.hide();
        this.toastr.error(this.translate.instant('step-deliver-to-interfax.connectivity-failed'));
      }
    );
  }
}
