import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { BurstingType, BurstingWorkflowStep } from '../../../blocks/models/workflow-step';
import { Step } from '../step';

@Component({
  selector: 'app-step-bursting',
  templateUrl: './step-bursting.component.html',
  styleUrls: ['./step-bursting.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepBurstingComponent implements OnInit {
  @Input() step: Step;

  fieldBarcodeValue: FieldDefinition;
  fieldPageCount: FieldDefinition;

  burstingWorkflowStep: BurstingWorkflowStep;

  get burstingWorkflowStepPageCount() {
    return `${this.burstingWorkflowStep.pageCount}`;
  }
  set burstingWorkflowStepPageCount(val: string) {
    this.burstingWorkflowStep.pageCount = Number(val);
  }

  constructor(private translate: TranslateService) {}

  ngOnInit() {
    this.burstingWorkflowStep = <BurstingWorkflowStep>this.step.workflowStep;
    this.createFields();
  }

  onTypeChanged = (value: BurstingType): void => {
    this.burstingWorkflowStep.burstingType = value;
  };

  createFields = (): void => {
    this.fieldBarcodeValue = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-workflow.barcode-value'),
      placeholder: this.translate.instant('admin-workflow.barcode-value-placeholder'),
      required: true,
    });

    this.fieldPageCount = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translate.instant('admin-workflow.pagecount-value'),
      placeholder: this.translate.instant('admin-workflow.pagecount-value-placeholder'),
      required: true,
    });
  };
}
