import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Input, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { cloneDeep } from 'lodash';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { Observable, Subscription } from 'rxjs';
import {
  DelayService,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetadataIntellisenseDatasource,
  MetaDataType,
} from 'shared-code-lib';
import { ComputedDataFieldValue, SetDataFieldsWorkflowStep } from '../../../blocks/models/workflow-step';
import { UserStateService } from '../../../blocks/services/user-state.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-set-data-fields',
  templateUrl: './step-set-data-fields.component.html',
  styleUrls: ['./step-set-data-fields.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepSetDataFieldsComponent implements OnInit, OnDestroy {
  @ViewChild(PerfectScrollbarDirective, { static: true }) directiveRef?: PerfectScrollbarDirective;
  @Input() step: Step;
  @Input() onSave: Observable<void>;

  subscriptions: Subscription = new Subscription();
  workflowStep: SetDataFieldsWorkflowStep;
  dataFieldsDataSource: FixedDataSource;
  dataFieldsLoaded: boolean = false;
  items: ComputedDataFieldValue[] = [];
  constructor(private translate: TranslateService, private delay: DelayService, private userState: UserStateService) {}

  ngOnInit() {
    this.workflowStep = <SetDataFieldsWorkflowStep>this.step.workflowStep;
    this.dataFieldsDataSource = new FixedDataSource(new Array<IFieldOptions>());
    this.loadMetaData();

    this.items = cloneDeep(this.workflowStep.items);
    this.subscriptions.add(this.onSave.subscribe(() => (this.workflowStep.items = this.items)));
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadMetaData = (): void => {
    const metadataFields: Array<IFieldOptions> = [];

    for (let i = 0; i < this.userState.user.metaData.length; i++) {
      const md = this.userState.user.metaData[i];
      if (md.type == MetaDataType.CascadingOptions || md.type == MetaDataType.LineItems) continue;
      metadataFields.push({
        name: md.name,
        value: md.id,
      });
    }

    this.dataFieldsDataSource.updateOptions(metadataFields);
    this.dataFieldsLoaded = true;
  };

  createListField = (label: string, placeholder: string, datasource: FixedDataSource) => {
    return new FieldDefinition({
      type: MetaDataType.List,
      label: label ? this.translate.instant(label) : '',
      placeholder: this.translate.instant(placeholder),
      required: true,
      datasource: datasource,
    });
  };

  createTextField = (label: string, placeholder: string) => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: label ? this.translate.instant(label) : '',
      placeholder: this.translate.instant(placeholder),
      required: true,
      substitutable: true,
      datasource: new MetadataIntellisenseDatasource(this.userState),
    });
  };

  onAddValue = () => {
    this.items.push({ metaDataId: '', value: '' });

    this.delay.execute(() => {
      this.directiveRef.scrollToBottom(0, 300);
    }, 100);
  };

  deleteAllItems = (): void => {
    this.items = [];

    this.delay.execute(() => {
      this.directiveRef.scrollToTop(0, 300);
    }, 100);
  };

  deleteItem = (item) => {
    const rowIndexToDelete = this.items.indexOf(item);

    if (rowIndexToDelete > -1) {
      this.items.splice(rowIndexToDelete, 1);
    }
  };

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.items, event.previousIndex, event.currentIndex);
  }
}
