import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { SharePointFieldsResult } from '../../views/admin-workflow/step-deliver-to-sharepoint365/models/sharepoint-fields-result';
import { SharePointFolder } from '../../views/admin-workflow/step-deliver-to-sharepoint365/models/sharepoint-folder';
import { SharePointFolderResult } from '../../views/admin-workflow/step-deliver-to-sharepoint365/models/sharepoint-folders-result';
import { DeliverToSharePoint365WorkflowStep } from '../models/workflow-step';

@Injectable({
  providedIn: 'root',
})
export class SharePointService {
  constructor(private http: HttpClient) {}

  private _sharePointFolderSelected = new Subject<SharePointFolder>();
  onSelectFolder = this._sharePointFolderSelected.asObservable();

  private _expandAndSelect = new Subject<any>();
  onExpandAndSelect = this._expandAndSelect.asObservable();

  private _showWarningMessage = new Subject<string>();
  onShowWarningMessage = this._showWarningMessage.asObservable();

  private _showSpinner = new Subject<boolean>();
  onShowSpinner = this._showSpinner.asObservable();

  selectedFolder(folder: SharePointFolder) {
    this._sharePointFolderSelected.next(folder);
  }

  expandAndSelect(selectionInfo: any) {
    this._expandAndSelect.next(selectionInfo);
  }

  showWarningMessage(message: string) {
    this._showWarningMessage.next(message);
  }

  showSpinner(show: boolean) {
    this._showSpinner.next(show);
  }

  public getDocumentLibraryFields(
    sharePoint365Step: DeliverToSharePoint365WorkflowStep,
    documentLibraryId: string,
    successCallBack: (result: SharePointFieldsResult) => void,
    failureCallBack: (err: any) => void
  ): void {
    let params = this.createSharePointParms(sharePoint365Step);
    this.http
      .get(`api/sharepoint/documentlibraries/${documentLibraryId}/fields`, { params: params })
      .subscribe((result: SharePointFieldsResult) => {
        successCallBack(result);
      }, failureCallBack);
  }

  public getSiteDocumentLibraries(
    sharePoint365Step: DeliverToSharePoint365WorkflowStep,
    successCallBack: (result: SharePointFolderResult) => void,
    failureCallBack: (err: any) => void
  ): void {
    let params = this.createSharePointParms(sharePoint365Step);
    this.http
      .get('api/sharepoint/documentlibraries', { params: params })
      .subscribe((result: SharePointFolderResult) => {
        successCallBack(result);
      }, failureCallBack);
  }

  public getFolderChildren(
    sharePoint365Step: DeliverToSharePoint365WorkflowStep,
    folderId: string,
    successCallBack: (result: SharePointFolderResult) => void,
    failureCallBack: (err: any) => void
  ): void {
    let params = this.createSharePointParms(sharePoint365Step);
    this.http
      .get(`api/sharepoint/folders/${folderId}/folders`, { params: params })
      .subscribe((result: SharePointFolderResult) => {
        successCallBack(result);
      }, failureCallBack);
  }

  createSharePointParms(sharePoint365Step: DeliverToSharePoint365WorkflowStep): HttpParams {
    let params = new HttpParams();
    params = params.append('siteUrl', sharePoint365Step.siteUrl);
    params = params.append('username', sharePoint365Step.sharePointUsername);
    params = params.append('password', sharePoint365Step.sharePointPassword);

    return params;
  }
}
