import { HttpClient } from '@angular/common/http';
import {
  AfterViewInit,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import {
  DelayService,
  DeliveryFormatsDataSource,
  FieldDefinition,
  IMetaData,
  MetaDataCollection,
  MetaDataType,
  MetaDataValidity,
  MetaDataValueType,
} from 'shared-code-lib';
import { FileBoundFields, FileBoundProjectField } from '../../../../blocks/models/filebound';
import { DeliverToSharePoint365WorkflowStep } from '../../../../blocks/models/workflow-step';
import { SharePointService } from '../../../../blocks/services/sharepoint.service';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../../blocks/services/user-state.service';
import { StepMetaDataMappingComponent } from '../../components/step-metadata-mapping/step-metadata-mapping.component';
import { DocumentFieldMapping } from '../../models/document-field-mapping';
import { SharePointFieldsResult } from './../models/sharepoint-fields-result';
import { SharePointFolder } from './../models/sharepoint-folder';
import { SharePointFolderResult } from './../models/sharepoint-folders-result';

@Component({
  selector: 'app-step-deliver-to-sharepoint365-general',
  templateUrl: './step-deliver-to-sharepoint365-general.component.html',
  styleUrls: ['./step-deliver-to-sharepoint365-general.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliverToSharepoint365GeneralComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() deliverToSharePoint365: DeliverToSharePoint365WorkflowStep;
  @Output() folderSelected: EventEmitter<SharePointFolder> = new EventEmitter<SharePointFolder>();
  @Output() startLoading: EventEmitter<string> = new EventEmitter<string>();
  @Output() finishedLoading: EventEmitter<any> = new EventEmitter();
  @ViewChild(StepMetaDataMappingComponent, { static: true })
  stepMetaDataMapping: StepMetaDataMappingComponent;

  fieldSharePointSiteUrl: FieldDefinition;
  fieldSharePointUsername: FieldDefinition;
  fieldSharePointPassword: FieldDefinition;
  fieldDeliveryFormat: FieldDefinition;
  fieldDocumentName: FieldDefinition;

  siteFolders: Array<SharePointFolder>;
  hasFolderSelected: boolean;

  subscriptions: Subscription;

  selectedFolderId: string;

  sharePointFields = new Array<FileBoundProjectField>();
  documentFields = new Array<DocumentFieldMapping>();

  constructor(
    public translate: TranslateService,
    private http: HttpClient,
    private spinnerState: SpinnerStateService,
    private sharePointService: SharePointService,
    private userState: UserStateService,
    private delay: DelayService,
    private toastr: ToastrService,
    private elementRef: ElementRef
  ) {
    this.subscriptions = new Subscription();
  }

  ngOnInit(): void {
    this.createFields();

    this.subscriptions.add(
      this.sharePointService.onSelectFolder.subscribe((folder: SharePointFolder) => {
        this.onSelectFolder(folder);
      })
    );

    this.subscriptions.add(
      this.sharePointService.onShowWarningMessage.subscribe((message: string) => {
        this.showWarningMessage(message);
      })
    );

    this.subscriptions.add(
      this.sharePointService.onShowSpinner.subscribe((show: boolean) => {
        this.showSpinner(show);
      })
    );
  }

  ngAfterViewInit(): void {
    if (
      this.deliverToSharePoint365.siteUrl &&
      this.deliverToSharePoint365.sharePointUsername &&
      this.deliverToSharePoint365.sharePointPassword
    ) {
      this.loadChildFolders()
        .then(() => {
          this.delay.execute(() => {
            this.onLoadedFolders();
          }, 100);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }

  ngOnDestroy(): void {
    this.showSpinner(false);
    this.subscriptions.unsubscribe();
  }

  createFields = (): void => {
    this.fieldSharePointSiteUrl = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-deliver-to-sharepoint.sharepoint-site-label'),
      placeholder: this.translate.instant('step-deliver-to-sharepoint.sharepoint-site-placeholder'),
      required: true,
    });

    this.fieldSharePointUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-delivery-filebound.username'),
      placeholder: this.translate.instant('step-delivery-filebound.username-placeholder'),
      required: true,
    });

    this.fieldSharePointPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translate.instant('step-delivery-filebound.password'),
      placeholder: this.translate.instant('step-delivery-filebound.password-placeholder'),
      required: true,
    });

    this.fieldDeliveryFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('workflow-step.delivery-format'),
      placeholder: this.translate.instant('workflow-step.delivery-format-placeholder'),
      datasource: new DeliveryFormatsDataSource(this.translate, this.userState),
      required: true,
    });

    this.fieldDocumentName = new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: '',
      required: false,
    });

    this.documentFields.push(
      new DocumentFieldMapping('SharePointDocumentName', 'step-delivery-filebound.document-name', true)
    );
  };

  onSiteUrlChanged = (siteUrl: string): void => {
    this.deliverToSharePoint365.siteUrl = siteUrl;
    this.startFolderLoad();
  };

  onUsernameChanged = (username: string): void => {
    this.deliverToSharePoint365.sharePointUsername = username;
    this.startFolderLoad();
  };

  onPasswordChanged = (password: string): void => {
    this.deliverToSharePoint365.sharePointPassword = password;
    this.startFolderLoad();
  };

  getSharePointDocumentName = (): string => {
    return this.getMetaData(FileBoundFields.FileBoundDocumentName);
  };

  onSharePointDocumentChanged = (value: string): void => {
    this.deliverToSharePoint365.mappings.set(FileBoundFields.FileBoundDocumentName, value, MetaDataValidity.valid);
  };

  getFolderPath = (): string => {
    if (this.deliverToSharePoint365.folderUrl) return this.deliverToSharePoint365.folderUrl;

    return this.translate.instant('step-deliver-to-sharepoint.no-deliver-to-folder-selected');
  };

  getMetaData = (name: string, mappings: MetaDataCollection = null): string => {
    if (!mappings) mappings = this.deliverToSharePoint365.mappings;

    const md: IMetaData = mappings.get(name);
    if (!md) return '';

    if (md.valueType == MetaDataValueType.String) return <string>md.value;

    return '';
  };

  startFolderLoad(): void {
    this.clearSelectedFolder();
    this.loadChildFolders().then(() => {
      this.onLoadedFolders();
    });
  }

  clearSelectedFolder(): void {
    this.deliverToSharePoint365.folderUrl = '';
  }

  onSelectFolder(folder: SharePointFolder): void {
    this.deliverToSharePoint365.folderUrl = folder.url;
    this.deliverToSharePoint365.pathFolderIds = this.getPathFolderIds(folder.id);
    this.loadFields(folder);
  }

  getPathFolderIds(id: string): Array<string> {
    for (let f of this.siteFolders) {
      const parentIds = f.findParentIds(id);
      if (parentIds) return parentIds;
    }

    return new Array<string>();
  }

  onLoadedFolders(): void {
    if (!this.deliverToSharePoint365.pathFolderIds || this.deliverToSharePoint365.pathFolderIds.length === 0) return;

    this.delay.execute(() => {
      this.sharePointService.expandAndSelect({ folderIds: this.deliverToSharePoint365.pathFolderIds, levelIndex: 0 });
    }, 100);
  }

  loadChildFolders(): Promise<void> {
    return new Promise<void>((resolve, reject) => {
      this.siteFolders = [];

      if (
        !this.deliverToSharePoint365.siteUrl ||
        !this.deliverToSharePoint365.sharePointUsername ||
        !this.deliverToSharePoint365.sharePointPassword
      ) {
        this.clearSelectedFolder();
        reject();
        return;
      }

      this.showSpinner(true);

      this.sharePointService.getSiteDocumentLibraries(
        this.deliverToSharePoint365,
        (result: SharePointFolderResult) => {
          this.showSpinner(false);
          if (result.succeeded) {
            const documentLibraryFolders = result.folders.map(
              (f) => new SharePointFolder(f.id, f.url, f.documentLibraryId, f.name, f.hasChildFolders)
            );

            this.siteFolders = documentLibraryFolders;
            resolve();
          } else {
            this.showWarningMessage(result.error);
            this.clearSelectedFolder();
            reject();
          }
        },
        (err) => {
          this.showSpinner(false);
          this.showWarningMessage(err.error);
          this.clearSelectedFolder();
          reject();
        }
      );
    });
  }

  loadFields(folder: SharePointFolder): void {
    this.sharePointFields = [];
    this.showSpinner(true);
    this.sharePointService.getDocumentLibraryFields(
      this.deliverToSharePoint365,
      folder.documentLibraryId,
      (result: SharePointFieldsResult) => {
        this.showSpinner(false);
        if (result.succeeded) {
          this.sharePointFields = result.fields.map((f) => new FileBoundProjectField(f, '0'));
          this.deliverToSharePoint365.fieldNames = result.fields;
          this.delay.execute(() => {
            this.stepMetaDataMapping.autoMapFields();
          }, 100);
        } else {
          this.showWarningMessage(result.error);
        }
      },
      (err) => {
        this.showSpinner(false);
        this.showWarningMessage(err.error);
      }
    );
  }

  showWarningMessage(message: string): void {
    const sharePointBaseError = this.translate.instant('step-deliver-to-sharepoint.sharepoint-request-failed');
    const fullMessage = `${sharePointBaseError} ${message}`;
    this.toastr.warning(fullMessage);
  }

  showSpinner(show: boolean): void {
    if (show) {
      this.startLoading.emit('folder.loading');
      return;
    }

    this.finishedLoading.emit();
  }
}
