import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';

import {
  DelayService,
  DeliveryFormatsDataSource,
  FieldDefinition,
  IFieldDataSource,
  IFieldOptions,
  MetaDataType,
} from 'shared-code-lib';
import {
  FileBoundFields,
  FileBoundProject,
  FileBoundProjectField,
  FileBoundProjectFieldResult,
  FileBoundProjectRoute,
  IFileBoundRelatedProject,
} from '../../../../blocks/models/filebound';
import { DeliverToFileBoundWorkflowStep } from '../../../../blocks/models/workflow-step';
import { ProxyApiService } from '../../../../blocks/services/proxy-api.service';
import { UserStateService } from '../../../../blocks/services/user-state.service';
import { StepMetaDataMappingComponent } from '../../components/step-metadata-mapping/step-metadata-mapping.component';
import { DocumentFieldMapping } from '../../models/document-field-mapping';

@Component({
  selector: 'app-step-delivery-filebound-general',
  templateUrl: './step-delivery-filebound-general.component.html',
  styleUrls: ['./step-delivery-filebound-general.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepDeliveryFileboundGeneralComponent implements OnInit {
  @Input() deliverToFileBound: DeliverToFileBoundWorkflowStep;

  @ViewChild(StepMetaDataMappingComponent, { static: true })
  stepMetaDataMapping: StepMetaDataMappingComponent;

  fieldFileboundUrl: FieldDefinition;
  fieldFileboundUsername: FieldDefinition;
  fieldFileboundPassword: FieldDefinition;
  fieldFileboundProject: FieldDefinition;
  fieldFileboundRoute: FieldDefinition;
  fieldFileboundFile: FieldDefinition;
  fieldDeliveryFormat: FieldDefinition;

  fileBoundProjectDataSource: FileBoundProjectDataSource;
  fileBoundProjectRoutesDataSource: FileBoundProjectRoutesDataSource;

  projectFields = new Array<FileBoundProjectField>();
  lineitemFields = new Array<FileBoundProjectField>();
  relatedProject: IFileBoundRelatedProject;
  documentFields = new Array<DocumentFieldMapping>();

  readonly allowedFormats = ['pdf', 'ocr.pdf', 'txt.pdf', 's.tif'];

  constructor(
    private translate: TranslateService,
    private proxy: ProxyApiService,
    private userService: UserStateService,
    private delay: DelayService
  ) {}

  ngOnInit(): void {
    this.fileBoundProjectDataSource = new FileBoundProjectDataSource(this.deliverToFileBound, this.proxy);
    this.fileBoundProjectRoutesDataSource = new FileBoundProjectRoutesDataSource(
      this.deliverToFileBound,
      this.proxy,
      this.translate
    );

    this.createFields();

    if (this.deliverToFileBound.projectId) {
      this.onProjectSelected(this.deliverToFileBound.projectId);
    }
  }

  onLoadProjectsURLChanged = (url: string): void => {
    this.deliverToFileBound.fileBoundURL = url;
    this.fileBoundProjectDataSource.changed.next('');
  };

  onLoadProjectsUsernameChanged = (username: string): void => {
    this.deliverToFileBound.username = username;
    this.fileBoundProjectDataSource.changed.next('');
  };

  onLoadProjectsPasswordChanged = (password: string): void => {
    this.deliverToFileBound.password = password;
    this.fileBoundProjectDataSource.changed.next('');
  };

  onProjectSelected = (value: string): void => {
    this.projectFields = [];

    if (!value) return;
    if (!this.deliverToFileBound.fileBoundURL) return;
    if (!this.deliverToFileBound.username) return;

    this.proxy.getFileBoundProjectFields(
      this.deliverToFileBound.fileBoundURL,
      this.deliverToFileBound.username,
      this.deliverToFileBound.password,
      value,
      this.projectFieldsLoaded,
      this.projectFieldsLoadFailed
    );
  };

  onProjectChanged(): void {
    this.fileBoundProjectRoutesDataSource.changed.next('');
  }

  projectFieldsLoaded = (results: FileBoundProjectFieldResult): void => {
    if (this.documentFields && this.documentFields.length > 0) {
      this.documentFields[0].enabled = results.project.enableSeparators;
    }

    this.deliverToFileBound.projectName = results.project.name;
    this.projectFields = results.metaDataFields;
    this.lineitemFields = results.lineItemFields;
    this.relatedProject = results.relatedProject;

    if (this.relatedProject) {
      this.deliverToFileBound.lineItemProjectName = this.relatedProject.relatedProjectName;
    } else {
      this.deliverToFileBound.lineItemProjectName = '';
      this.deliverToFileBound.lineItemMetaDataId = '';
    }

    this.delay.execute(this.stepMetaDataMapping.autoMapFields);
  };

  projectFieldsLoadFailed = (): void => {};

  createFields = (): void => {
    this.fieldFileboundUrl = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-delivery-filebound.url'),
      placeholder: this.translate.instant('step-delivery-filebound.url-placeholder'),
      required: true,
    });

    this.fieldFileboundUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('step-delivery-filebound.username'),
      placeholder: this.translate.instant('step-delivery-filebound.username-placeholder'),
      required: true,
    });

    this.fieldFileboundPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translate.instant('step-delivery-filebound.password'),
      placeholder: this.translate.instant('step-delivery-filebound.password-placeholder'),
      required: true,
    });

    this.fieldFileboundProject = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-delivery-filebound.project'),
      placeholder: this.translate.instant('step-delivery-filebound.project-placeholder'),
      datasource: this.fileBoundProjectDataSource,
      required: true,
    });

    this.fieldFileboundRoute = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-delivery-filebound.route'),
      placeholder: this.translate.instant('step-delivery-filebound.route-placeholder'),
      datasource: this.fileBoundProjectRoutesDataSource,
      required: false,
    });

    this.fieldFileboundFile = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('step-delivery-filebound.file'),
      placeholder: this.translate.instant('step-delivery-filebound.file-placeholder'),
      datasource: new FileBoundProjectFilesDataSource(this.deliverToFileBound, this.proxy, this.translate),
      required: false,
    });

    this.fieldDeliveryFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('workflow-step.delivery-format'),
      placeholder: this.translate.instant('workflow-step.delivery-format-placeholder'),
      datasource: new DeliveryFormatsDataSource(this.translate, this.userService, this.allowedFormats),
      required: true,
    });

    this.documentFields.push(
      new DocumentFieldMapping(FileBoundFields.FileBoundSeparator, 'step-delivery-filebound.separator', false)
    );
    this.documentFields.push(
      new DocumentFieldMapping(FileBoundFields.FileBoundDivider, 'step-delivery-filebound.divider', true)
    );
    this.documentFields.push(
      new DocumentFieldMapping(FileBoundFields.FileBoundDocumentName, 'step-delivery-filebound.document-name', true)
    );
  };
}

class FileBoundProjectDataSource implements IFieldDataSource {
  changed = new Subject<any>();

  constructor(public deliveryStep: DeliverToFileBoundWorkflowStep, private proxy: ProxyApiService) {}

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    if (!this.deliveryStep.fileBoundURL || !this.deliveryStep.username || !this.deliveryStep.password) {
      failureCallBack(null);
      return;
    }

    this.proxy.getFileBoundProjects(
      this.deliveryStep.fileBoundURL,
      this.deliveryStep.username,
      this.deliveryStep.password,
      (projects: Array<FileBoundProject>) => {
        const items = new Array<IFieldOptions>();

        for (const project of projects) {
          const item = { name: project.name, value: project.id };
          items.push(item);
        }

        successCallBack(items);
      },
      failureCallBack
    );
  }
}

class FileBoundProjectRoutesDataSource implements IFieldDataSource {
  changed = new Subject<any>();

  constructor(
    public deliveryStep: DeliverToFileBoundWorkflowStep,
    private proxy: ProxyApiService,
    private translate: TranslateService
  ) {}

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    if (
      !this.deliveryStep.fileBoundURL ||
      !this.deliveryStep.username ||
      !this.deliveryStep.password ||
      !this.deliveryStep.projectId
    ) {
      failureCallBack(null);
      return;
    }

    this.proxy.getFileBoundProjectRoutes(
      this.deliveryStep.fileBoundURL,
      this.deliveryStep.username,
      this.deliveryStep.password,
      this.deliveryStep.projectId,
      (routes: Array<FileBoundProjectRoute>) => {
        const items = new Array<IFieldOptions>();

        items.push({ name: this.translate.instant('step-delivery-filebound.no-route-selected'), value: '' });

        for (const route of routes) {
          const item = { name: route.name, value: route.id };
          items.push(item);
        }

        successCallBack(items);
      },
      failureCallBack
    );
  }
}

class FileBoundProjectFilesDataSource implements IFieldDataSource {
  changed = new Subject<any>();

  constructor(
    public deliveryStep: DeliverToFileBoundWorkflowStep,
    private proxy: ProxyApiService,
    private translate: TranslateService
  ) {}

  public projectId: string = '';

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    const items = new Array<IFieldOptions>();

    items.push({ name: this.translate.instant('step-delivery-filebound.new-file'), value: '' });

    successCallBack(items);
  }
}
