import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { Tab } from 'shared-code-lib';
import { Workflow } from '../../../blocks/models/workflow';

@Component({
  selector: 'app-step-start',
  templateUrl: './step-start.component.html',
  styleUrls: ['./step-start.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class StepStartComponent implements OnInit {
  @Input() workflow: Workflow;

  activeTab = 'scan-settings';
  tabs = new Array<Tab>();

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.createTabs();
  }

  activateTab = (view) => {
    this.activeTab = view;
  };

  private createTabs = () => {
    this.tabs.push(new Tab('scan-settings', this.translateService.instant('step-interactive.scan-settings')));
    this.tabs.push(new Tab('prompts', this.translateService.instant('admin-workflow.prompts')));
  };
}
