import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild,
  ViewEncapsulation,
} from '@angular/core';
import { ToastrService } from 'ngx-toastr';

import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { WorkflowsDataSource, WorkflowStepsDataSource } from '../../../blocks/models/workflow-datasource';
import { MoveToWorkflow } from '../../../blocks/models/workflow-step';
import { HydraApiService } from '../../../blocks/services/hydra-api.service';
import { Step } from '../step';

@Component({
  selector: 'app-step-move-to-workflow',
  templateUrl: './step-move-to-workflow.component.html',
  encapsulation: ViewEncapsulation.None,
})
export class StepMoveToWorkflowComponent implements OnInit {
  @ViewChild('modalBody', { static: true }) modalBody: ElementRef;
  @Input() step: Step;
  @Input() workflowId: string;
  @Output() elementLoading: EventEmitter<string> = new EventEmitter<string>();
  @Output() finishedLoading: EventEmitter<any> = new EventEmitter();

  initialSelectedWorkflowId: string;
  initialSelectedWorkflowStepId: string;

  moveToWorkflow: MoveToWorkflow;

  fieldDisplayName: FieldDefinition;
  fieldWorkflows: FieldDefinition;
  fieldWorkflowSteps: FieldDefinition;
  workflowsLoaded = false;

  workflowsDataSource: WorkflowsDataSource;
  workflowStepsDataSource: WorkflowStepsDataSource;

  constructor(private translate: TranslateService, private hydraApi: HydraApiService, private toastr: ToastrService) {}

  ngOnInit() {
    this.moveToWorkflow = <MoveToWorkflow>this.step.workflowStep;
    this.initialSelectedWorkflowId = this.moveToWorkflow.selectedWorkflowId;
    this.initialSelectedWorkflowStepId = this.moveToWorkflow.selectedWorkflowStepId;

    this.elementLoading.emit('admin-workflow.loading-workflows');
    this.hydraApi.getWorkflows(
      (workflows) => {
        workflows = workflows.filter((w) => w.id !== this.workflowId);
        this.workflowsDataSource = new WorkflowsDataSource(workflows);
        this.workflowStepsDataSource = new WorkflowStepsDataSource(this.moveToWorkflow, workflows);

        this.createFields();
        this.finishedLoading.emit();
      },
      (err: any) => {
        this.toastr.error(this.translate.instant('errors.failed-load-workflows'));
        this.finishedLoading.emit();
      }
    );
  }

  createFields = () => {
    this.fieldDisplayName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-workflow.interactive-step-displayname'),
      placeholder: this.translate.instant('admin-workflow.interactive-step-displayname-placeholder'),
      required: true,
    });

    this.fieldWorkflows = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('workitem-properties.workflow'),
      placeholder: this.translate.instant('workitem-properties.workflow'),
      datasource: this.workflowsDataSource,
      required: true,
    });

    this.fieldWorkflowSteps = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('workitem-properties.workflow-step'),
      placeholder: this.translate.instant('workitem-properties.workflow-step'),
      datasource: this.workflowStepsDataSource,
      required: true,
    });

    this.workflowsLoaded = true;
  };

  onSelectedWorkflowChanged(selectedWorkflowId: string) {
    if (!selectedWorkflowId) return;

    this.moveToWorkflow.selectedWorkflowId = selectedWorkflowId;
    this.workflowStepsDataSource.changed.next();

    if (selectedWorkflowId === this.initialSelectedWorkflowId) {
      this.moveToWorkflow.selectedWorkflowStepId = this.initialSelectedWorkflowStepId;
      return;
    }

    const startStep = this.workflowStepsDataSource.steps.find((s) => s.stepType === 'start');
    if (startStep) {
      this.moveToWorkflow.selectedWorkflowStepId = startStep.id;
    }
  }
}
